@import "variables.10ft";
@import './style';
@import '../../../styles/mixins.scss';

$spinner: (
    single: '.network-ribbon-spinner__single-ribbon > .core-spinner',
    multi: '.network-ribbon-spinner > .core-spinner'
);

$skeleton: (
    single: '.network-ribbon-skeleton__single-ribbon > .skeleton-screen',
    multi: '.network-ribbon-skeleton > .skeleton-screen'
);

core-channel-guide {
    .scrolling-container {
        height: 66.9vh;
        padding-bottom: 5vh;
    }

    #{map-get($spinner, multi)} {
        margin-top: -135px;
    }
    #{map-get($spinner, single)} {
        margin-top: -50vh;
    }

    #{map-get($skeleton, multi)} {
        margin-top: -22vh;
    }
    #{map-get($skeleton, single)} {
        margin-top: -59vh;
        .skeleton-tile {
            margin-bottom: 14.33vh;
        }
    }
}

@include scale-for-720p {
    core-channel-guide {
        .scrolling-container {
            height: 62vh;
            padding-bottom: 0;
        }

        #{map-get($spinner, multi)} {
            margin-top: -90px;
        }

        #{map-get($skeleton, multi)} {
            margin-top: -16.7vh;
        }
        #{map-get($skeleton, single)} {
            margin-top: -56vh;
            .skeleton-tile {
                margin-bottom: 16.22vh;
            }
        }
    }
}
