@import "variables.10ft";
@import './style';
@import '../../../styles/mixins.scss';
@import '../guide-container.variables.10ft.scss';

// Toggle button for LG/10ft
core-guide-toggle {
    section {
        left: calc(50% - 170px); // centers the toggle button
        position: absolute;
    }

    button.btn {
        padding: 15px 0; // regular button top/bottom padding minus 3px border and 0 left/right padding
        width: 170px; // 1/2 the section width
        min-width: 0; // reset any pre-existing button min-widths

        &:hover {
            background-color: inherit;
            color: $color-white;
        }

        &.disabled {
            background-color: $color-near-gray;
            color: $color-white;
        }

        &.has-hover {
            color: $color-cool-white;
            background-color: $color-shadow-gray;
        }

        &.core-focused-item {
            @include focused-box();
            min-width: 170px;
            padding: 15px 0;
        }

        + .btn {
            margin-left: 0;
        }
    }

    button.btn {
        font-size: $guide-header-component-font-size;

        &:focus {
            font-size: $guide-header-component-font-size;
        }
    }
}

@include scale-for-720p {
    core-guide-toggle {
        section {
            left: calc(50% - 113px); // centers the toggle button
        }

        button.btn {
            padding: 10px 0;
            width: 113px;
            height: 50px;
            border-width: 2px 0 2px 2px;

            &.core-focused-item {
                min-width: 113px;
                border-width: 2px 0 2px 2px;
                padding: 10px 0;
            }

            & + .btn {
                border-width: 2px 2px 2px 0;
            }
        }

        button.btn.black {
            padding: 10px 0;
            width: 113px;
            height: 50px;
            border-width: 2px 0 2px 2px;

            & + .btn.black {
                border-width: 2px 2px 2px 0;
            }
        }

        button.btn {
            font-size: $guide-header-component-font-size-720p;

            &:focus {
                font-size: $guide-header-component-font-size-720p;
            }
        }
    }
}
