@import "variables.10ft";
@import "./style";
@import '../../../../styles/mixins';

@include scale-for-720p {
    core-cart-summary {
        .iap-cart-summary {
            padding: 40px 27px 0;

            .cart-title {
                line-height: 27px;
                margin-bottom: 14px;
                padding-bottom: 14px;
                font-size: 20px;
            }

            .cart-items {
                .cart-item {
                    line-height: 26px;
                    padding-bottom: 14px;
                    margin-bottom: 14px;
                    margin-left: 34px;
                    font-size: 17px;

                    &:before {
                        left: -34px;
                        margin-top: -17px;
                        font-size: 17px;
                    }
                }
            }
        }
    }
}
