@import "variables.10ft";
@import '../ribbon-system/ribbon-system.variables.10ft';
@import './style';

// Turns DVR screens into modals, 10ft style
core-dvr-manager {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 10;
    background: $color-dark-gray;

    // Header above the view recordings/manage recordings screens
    header.dvr-header {
        background: $color-black;
        font-size: 30px;
        line-height: 40px;
        padding: 40px $ribbon-left-gutter;

        // The individual headings
        span {
            font-size: 30px;
            text-transform: uppercase;

            // The host page or pages
            &.core-breadcrumb-primary {
                color: $color-off-white;
            }

            // The current page
            &.core-breadcrumb-current {
                color: $color-yellow;
                font-weight: 500;
            }

            // Breadcrumb chevrons with a bit of padding
            i {
                font-size: 19px;
                padding: 0 10px;
                position: relative;
                top: -2px;
            }
        }

        // Enable and position logo for specific platforms as needed
        span.dvr-logo.browse-logo {
            display: none;
        }
    }
}
