@import "../../styles/mixins";

core-splash {

    .splash-page {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;

            .header-row {
                width: 100%;
                background-color: $color-black;
                display: flex;
                align-items: center;
                flex-direction: row;
                justify-content: space-between;

                .sling-logo {
                    background-size: contain;
                    background-position: center;
                    background-repeat: no-repeat;

                    &.fallback-logo {
                        content: url("../../../assets/images/sling_logo.svg");
                    }
                }

                .iap-elements {
                    .dropdown-container {
                        .dropdown-toggle {
                            text-align: left;
                            .arrow {
                                position: absolute;
                                right: 15px;
                                top: 10px;

                            }
                        }

                        .dropdown-list {
                            @include fade-in();
                            position: absolute;
                            top: 100px;
                            overflow-x: hidden;
                            border-radius: 0;
                            margin: 0;

                            .list-item {
                                display: block;
                                text-decoration: none;
                                color: $color-off-white;
                                overflow-x: hidden;
                                text-overflow: ellipsis;
                                width: 100%;
                                text-align: left;
                                cursor: pointer;

                                &:hover {
                                    color: $color-white;
                                    background-color: $color-near-gray;
                                }
                            }
                        }
                    }
                }
            }

            .splash-background {
                width: 100%;
                flex: 1;
                background-repeat: no-repeat;
                background-size: contain;
                background-position: 50%;

                &:not(.loading) {
                    @include fade-in();
                }
            }

            .splash-footer {
                width: 100%;
                background-image: linear-gradient(to right, #f9a501, #fecb00);
                display: flex;
                flex-direction: row;
                justify-content: space-between;

                .verbage {
                    overflow: hidden;

                .tagline, .subtagline {
                    color: $color-black;
                    text-transform: uppercase;
                    overflow: hidden;
                }
            }

            .button-container {
                padding: 0 30px;
                white-space: nowrap;
                margin-top: 39px;
            }
        }
    }
}
