@import "variables.10ft";
@import "../../../styles/base";

channel-recall {
    div.channel-recall-action {
        width: 200px;
        display: inline-flex;
        flex-direction: column;
    }
    button.channel-recall-button {
        background: transparent;
        border: none;
        outline: none;
        padding: 0;
        margin: 0;
        display: flex;
        flex-direction: column;
        align-items: center;

        span.button-icon {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100px;
            height: 103px;
            background: transparent;
            margin: 0 auto;
            border: solid 2px transparent;
            color: $color-light-gray;
            fill: $color-off-white;
        }
        span.button-text {
            font-size: 25px;
            margin-top: 7px;
            color: $color-white;
            visibility: hidden;
        }
        &:hover {
            span.button-icon {
                fill: $color-white;
            }
            span.button-text {
                visibility: visible;
            }
        }
        &.core-focused-item {
            span.button-icon {
                background-color: $color-dark-blue;
                fill: $color-white;
            }
            span.button-text {
                visibility: visible;
            }
        }
        &.core-focused-item:enabled {
            span.button-icon {
                color: $color-white;
            }
            span.button-text {
                visibility: visible;
            }
        }
        &:disabled {
            span.button-icon {
                fill: $color-off-white;
                border: 0 !important;
                cursor: not-allowed;
            }
            span.button-text {
                visibility: hidden;
            }
        }
    }
}

// TODO this is a bit of a hack to make sure webpack includes these files, these styles are not actually used.
.channel-recall-button {
    background: url('../../../assets/images/channel-recall.svg');
}

@include scale-for-hd-tv {
    channel-recall {
        div.channel-recall-action {
            width: 134px;
        }

        button.channel-recall-button {
            span.button-icon {
                width: 67px;
                height: 69px;
                border: solid 1px transparent;

                svg {
                    width: 35px;
                    height: 39px;
                }
            }

            span.button-text {
                font-size: 17px;
                margin-top: 4px;
            }
        }
    }
}
