@import "../../styles/variables.scss";

core-search-container {
    div.left {
        width: 31vw;
        height: 86.9vh;
        // border: 1px solid pink;
        float: left;
        padding: 3vh 3vh 0 8vh;
    }

    div.right {
        width: 69vw;
        height: 86.9vh;
        // border: 1px solid lightblue;
        float: left;
        padding: 3vh 3vh 0 0;
    }

    section.keyboard {
        width: 100%;
        height: 45.9vh;
        // border: 1px solid yellow;
        float: left;

        display: flex;
        flex-direction: row;
        flex-wrap: wrap;

        .key {
            // border: 1px solid lightseagreen;
            text-align: center;
            font-size: 50px;
            font-weight: 450;
        }

        .character {
            width: calc(100% / 6)
        }

        .space {
            font-size: 50px;
            width: 33%;
        }

        .backspace {
            font-size: 50px;
            width: 33%;
        }

        .clear {
            font-size: 50px;
            width: 34%;
        }
    }

    section.search-terms {
        width: 100%;
        height: 8vh;
        float: right;

        input.search-terms-input {
            background-color: $color-medium-cool-gray;
            height: 100%;
            width: 100%;

            outline: none;
            border-radius: 0;
            border-color: transparent;

            color: $color-white;
            font-weight: 500;
            font-size: 56px;
            padding: 0 .5em 0 .5em;
        }
    }

    section.suggestions {
        width: 100%;
        height: 38vh;
        // border: 1px solid purple;
        float: left;
        padding: 3vh 0 0 1vh;

        .suggestions-title {
            // border: 1px solid rgb(100, 11, 100);
            color: $color-white;
            font-size: 26px;
            font-weight: 500;
        }

        .suggestion-list {
            height: 31vh;
            overflow-y: scroll;
        }

        .suggestion {
            // border: 1px solid rgb(194, 34, 194);
            color: $color-warm-gray;
            font-size: 38px;
            font-weight: 400;
        }
    }

    section.search-results-container {
        width: 67.3vw;
        height: 75.9vh;
        // border: 1px solid rgb(93, 145, 93);
        float: right;
        overflow-y: scroll;

        .search-results {
            // border: 1px solid yellow;
            height: 6vh;

            .search-results-tabs {
                // border: 1px solid darkgreen;
                color: $color-warm-gray;
                padding: 1vh 0 0 1vh;
                height: 6vh;

                .active {
                    color: $color-white;
                }

                .search-results-tab {
                    // border: 1px solid lightgreen;
                    padding-right: 2em;
                }
            }

            .search-results-grid {
                // border: 1px solid green;
                height: 70vh;
                overflow-y: scroll;
            }
        }

    }
}
