@import "variables.10ft";
@import 'style';

core-device-options {
    .device-name-section  {
        margin-bottom: 30px;
        .device-name-label {
            margin-right: 26px;
            font-size: $font-size-biggest2;
            font-weight: 500;
        }
        .device-name {
            font-size: $font-size-bigger2;
            font-weight: normal;
        }
    }
    button.btn.device-rename-btn,
    button.btn.device-rename-btn:active,
    button.btn.device-rename-btn.core-focused-item {
        width: 212.9px;
        height: 75px;
        background-color: $color-medium-light-gray;
        color: $color-off-white;
        padding: 9px 18px 20px;
        font-size: $font-size-bigger2;
        font-weight: 500;
        &:active,
        &.core-focused-item {
            color: $color-white;
        }
    }
    .zoom-to-fill-screen {
        margin-top: 50px;
        width: 649.4px;
        .toggle-label {
            color: $color-white;
            height: 48px;
            font-size: $font-size-biggest2;
            font-weight: 500;
            text-align: left;
            padding-left: 0px;
        }
        core-toggle {
            margin-top: 15px;
        }
    }
}

@include scale-for-720p {
    core-device-options {
        .device-name-section {
            margin-bottom: 20px;
            .device-name-label {
                margin-right: 17px;
                font-size: $font-size-big;
            }
            .device-name {
                font-size: $font-size-medium;
            }
        }
        button.btn.device-rename-btn,
        button.btn.device-rename-btn:active,
        button.btn.device-rename-btn.core-focused-item {
            min-width: 143px;
            width: 143px;
            height: 50px;
            padding: 6px 12px 13px;
            font-size: $font-size-medium;
            &:active,
            &.core-focused-item {
                color: $color-white;
            }
        }
        .zoom-to-fill-screen {
            margin-top: 33px;
            width: 435px;
            span.toggle-label {
                padding: 0;
                height: 32px;
                font-size: $font-size-big;
            }
            core-toggle {
                margin-top: 10px;
            }
        }
    }
}
