@import "../../styles/mixins";

$margin-top: 15px;
$pinpad-input-font-size: 40px;
$pinpad-input-gutter: 3px;
$pinpad-input-width: 164px;
$pinpad-width: ($pinpad-input-width + $pinpad-input-gutter) * 3;
$pinpad-row-width: $pinpad-width - $pinpad-input-gutter;
$pinpad-button-font-size: 28px;
$pinpad-button-height: 75px;
$pinpad-splitter-height: 20px;

core-modal-pinpad {

    .pinpad-modal {
        width: $pinpad-width;

        .modal-header .pinpad-title {
            color: $color-yellow;
            @include font-size(bigger);
            margin: $margin-top 0 0 0;
            font-weight: bold;
            text-align: center;
            text-transform: uppercase;
        }

        .row {
            margin: 0;
            width: $pinpad-row-width;
        }

        .pin-input {
            margin: 0 0 $pinpad-splitter-height 0;
            font-size: $pinpad-input-font-size;
            height: 58px;
            width: 100%;
            padding: 0 20px 6px;
            color: $color-white;
            background-color: $color-medium-gray;
            border: none;
        }

        .pin-button {
            margin: 0;
        }

        .pin-button + .pin-button {
            margin-left: 0;
            margin-right: $pinpad-input-gutter;
        }

        .pin-button + .pin-button + .pin-button {
            margin-right: 0;
        }

        .pin-button {
            width: $pinpad-input-width;
            float: left;
            font-size: $pinpad-button-font-size;
            height: $pinpad-button-height;
            line-height: $pinpad-button-height;
            margin: 0 $pinpad-input-gutter $pinpad-input-gutter 0;
            padding: 0 12px;
            text-align: center;
            color: $color-white;
            background-color: $color-medium-gray;
            cursor: pointer;
            @include border-radius(0);

            .icon-backspace {
                font-size: 22px;
            }

            &:hover {
                background-color: $color-dark-blue;
            }

            &.core-focused-item {
                background-color: $color-dark-blue;
            }
        }

        .pin-button-full {
            margin-top: $pinpad-splitter-height;
            height: $pinpad-button-height;
            width: $pinpad-row-width;
            font-size: $pinpad-button-font-size;
        }
    }

    .modal-spinner-container {
        @include position();
    }
}
