@import "variables.10ft";
@import '../../../styles/mixins.scss';
@import '../../../styles/variables.10ft';

.skeleton-screen {
    height: $screen-container-height;
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    overflow: hidden;
    padding-top: 7.88056vh;
    padding-left: 5.560709vw;
    position: relative;

    .skeleton-tile {
        height: 21.667vh;
        width: 21.67vw;
        margin-right: 1vw;
        @include shine-animation();
    }

    &.channel-guide-skeleton {
        height: 100%;
        padding-top: 0;
        padding-left: 5.2vw;
    }

    &.grid-skeleton {
        .skeleton-tile {
            margin-bottom: 12.22222vh;
        }
    }

    &.ribbon-skeleton {
        .skeleton-tile {
            margin-bottom: 15.76112vh;
        }
    }
}
