@import '../../../styles/mixins';

core-series-details {
    .season-menu {
        font-weight: 600;
        // margin-bottom is set in platform specific sheets
        padding: 0 $details-side-gutter;
    }

    .season-menu-content {
        display: flex;
    }

    .season-menu-label {
        padding-bottom: 10px;
        border-bottom: 2px solid transparent;
    }

    .season-number-wrapper {
        cursor: pointer;
        display: inline-block;
        border-bottom: 2px solid transparent;

        &:hover {
            color: $color-white;
        }
    }

    .season-number {
        cursor: pointer;
        // margin-left is set in platform specific sheets
    }

    .episode {
        cursor: pointer;
        padding: 9px $details-side-gutter;
        &:hover {
            .episode-title {
                color: $color-white;
            }
        }

        .episode-title {
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
        }

        .episode-time {
            margin-left: 10px;
            text-overflow: ellipsis;
            white-space: nowrap;
        }

        .episode-header {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }

        &.selected {
            background-color: $color-black;
            cursor: default;
            .episode-title {
                color: $color-white;
            }

            .episode-content {
                display: block;
            }
        }

        &.unselected {
            .episode-progress {
                display: none;
            }

            .episode-content {

                .episode-description{
                    display: none;
                }

                .episode-button-row {
                    height: 0;
                    margin: 0;

                    .btn {
                        padding: 0;
                        min-width: 0;
                        font-size: 0;
                    }
                }
            }
        }
    }

    .episode-description {
        padding: 10px 20px;
    }

    .episode-button-row {
        margin-top: 10px;
        margin-left: 20px;

        .btn {
            background-color: $color-medium-gray;
        }

        // Because the core-protect-button wraps another
        // .btn - we need to do some magic to ensure that the
        // margins are correct
        button, core-protect-button, div {
            &.btn {
                margin: 5px 10px 0 0;
            }
        }
    }

    .season-number-wrapper.selected {
        color: $color-white;
    }

    .offscreen-text {
        border: 0;
        clip: rect(0, 0, 0, 0);
        height: 1px;
        margin: -1px;
        overflow: hidden;
        padding: 0;
        width: 1px;
        position: absolute;
    }
}
