@import './variables';
@import './mixins';

// SLING ACTIONS
@font-face {
    font-family: 'Sling-Actions';
    src: url('../../assets/fonts/sling-icons/Sling-Actions.eot') format('embedded-opentype'),
    url('../../assets/fonts/sling-icons/Sling-Actions.ttf') format('truetype'),
    url('../../assets/fonts/sling-icons/Sling-Actions.woff') format('woff'),
    url('../../assets/fonts/sling-icons/Sling-Actions.svg#Sling-Actions') format('svg');
    font-weight: normal;
    font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'Sling-Actions' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
.icon-manage:before,
.icon-add:before {
    content: "\e91e";
}
