@import '../../../../styles/mixins';

// See style.win10.scss, style.10ft.scss, style.3ft.scss for platform specific styling.

core-grid-item {
    position: absolute;
    //CSS trick to improve rendering time of translated elements
    backface-visibility: hidden;

    .schedule-item {
        width: auto;
        background-color: $color-light-black;
        box-shadow: -1px -1px 0 1px $color-medium-gray;
        color: $color-off-white;
        // value for $grid-guide-row-height is imported in platform specific sheets
        height: $grid-guide-row-height;

        .schedule-item-shell {
            // value for $grid-guide-item-padding is imported in platform specific sheets
            padding: $grid-guide-item-padding;
            @include ellipsis-able();
        }

        &:hover {
            cursor: pointer;
            color: $color-white;
            background: $color-light-medium-gray;

            i:before {
                color: $color-white;
            }
        }

        &.on-now {
            color: $color-white;
        }

        .recording-icon {
            color: $color-red;
        }

        .description {
            display: none;
        }

        &:hover, &.focus-grid-focused {
            .description {
                display: inline;
            }
        }
    }

    .last & .schedule-item {
        border-bottom: 2px solid $color-medium-gray;
    }
}
