@import '../../../styles/mixins.scss';
@import '../../../styles/variables.scss';

core-screen {
    flex: 1;

    .screen-ribbons-container {
        width: 100%;
        height: 100%;
    }

    .vertical-scroll-container {
        padding-bottom: 5vh;

        @media screen and (max-height: 700px) {
            padding-bottom: 8vh;
        }
    }

    .screen-error {
        @include flex-center();
        color: $color-yellow;
        font-weight: 600;
        height: 100%;
        width: 100%;
    }

    core-spinner {
        display: flex;
        position: absolute;
        height: calc(100vh - #{$nav-dimensions-desktop});
        width: 100%;
    }
}
