@import '../../styles/variables';

core-settings-container {
    flex: 1;
    display: block;
    position: relative;

    .back-to-menu {
        display: none;
    }

    & > section {
        height: 100%;
    }

    .settings-container {
        display: flex;
        height: 100%;
    }

    .settings-content {
        background-color: $color-dark-gray;
        color: $color-off-white;
        height: 100%;
        width: calc(100% - #{$settings-left-menu-width});
        overflow: auto;
    }

    .toggle-label {
        letter-spacing: 1px;
        font-weight: 400;
        color: #999;
        padding: 5px 0 0 15px;
    }
}
