@import "variables.10ft";
@import './style';
@import '../micro-guide-variables.10ft';
@import '../../../styles/mixins';

// Individual channels in the micro guide for 10ft
core-micro-channel {
    display: block;
    .micro-channel {
        width: $micro-guide-item-width;
        height: $micro-guide-height;
        padding: 17px 36px;

        // vertical separator at left and after the last channel created by border
        &::before,
        &:last-child::after {
            height: $micro-guide-height;
            border: 2px solid $color-shadow-gray;
            top: 0;
        }

        // last channel left vertical separator position override
        &:last-child::after {
            left: $micro-guide-item-width;
        }

        // Program title
        .program-title {

            i {
                display: inherit;
                align-items: center;
                padding-right: 8px;

                &:before {
                    font-size: $micro-guide-item-icon-font-size;
                }
                // padding and font-size set in platform stylesheets
            }

            h3 {
                font-size: $micro-guide-item-title-font-size;
                line-height: 47px;
                text-overflow: ellipsis;
                // width, font-size and padding set in platform stylesheets
            }
        }

        // 10:00pm TV-MA 60min
        h4 {
            font-size: $micro-guide-item-text-font-size;
            line-height: 34px;
            width: $micro-guide-item-width * 0.666;
            padding-bottom: 11px;
            border-bottom: 2px solid $color-shadow-gray;
        }

        // In 9m: Teenage Mutant Ninja Turtles
        p {
            width: $micro-guide-item-width * 0.666;
            font-size: $micro-guide-item-text-font-size;
            line-height: 34px;
            padding-top: 7px;
        }


        // Parental lock overlay and lock icon
        .lock-overlay .icon-locked {
            font-size: 36px;
            top: 67px;
        }

        .channel-logo {
            width: $micro-guide-item-width * 0.333;
            right: 0;
            bottom: 22px;

            img {
                height: $micro-guide-item-logo-height;
                max-width: $micro-guide-item-width * 0.25;
            }
        }

        // When the channel lacks all data needed, display only the logo
        &.no-data .channel-logo {
            width: $micro-guide-item-width;
            top: 50px;
            img {
                height: $micro-guide-item-logo-height-no-data;
                // When all you have is alt text
                &[src=''],
                &:not([src]) {
                    top: 14px;
                }
            }
        }

        // active state for focus, hover for development
        &.core-focused-item {
            background-color: $color-dark-blue;

            h4, p {
                color: $color-white;
            }

            h4 {
                border-bottom: 2px solid rgba(255,255,255,0.3);
            }
        }

        &.active,
        &:hover {
            background-color: $color-gray;
            color: $color-white;
        }

        .is-locked, &.core-focused-item {
            .program-title {
                h3 {
                    color: $color-white;
                }
            }

            h4, p {
                color: $color-white;
            }
        }
    }
}

@include scale-for-720p {
    core-micro-channel {
        .micro-channel {
            width: $micro-guide-item-width-720p;
            height: $micro-guide-height-720p;
            padding: 11px 23px;

            // vertical separator at left and after the last channel created by border
            &::before,
            &:last-child::after {
                height: $micro-guide-height-720p;
            }

            // last channel left vertical separator position override
            &:last-child::after {
                left: $micro-guide-item-width-720p;
            }
            
            .program-title {
                i {
                    padding-right: 5px;

                    &:before {
                        font-size: $micro-guide-item-icon-font-size-720p;
                    }
                }

                h3 {
                    font-size: $micro-guide-item-title-font-size-720p;
                    line-height: 31px;
                    text-overflow: ellipsis;
                }
            }

            // 10:00pm TV-MA 60min
            h4 {
                font-size: $micro-guide-item-text-font-size-720p;
                line-height: 22px;
                width: $micro-guide-item-width-720p * 0.666;
                padding-bottom: 7px;
                border-bottom: 1px solid $color-shadow-gray;
            }

            // In 9m: Teenage Mutant Ninja Turtles
            p {
                width: $micro-guide-item-width-720p * 0.666;
                font-size: $micro-guide-item-text-font-size-720p;
                line-height: 22px;
                padding-top: 4px;
            }


            // Parental lock overlay and lock icon
            .lock-overlay .icon-locked {
                font-size: 24px;
                top: 44px;
            }

            .channel-logo {
                width: $micro-guide-item-width-720p * 0.333;
                bottom: 14px;

                img {
                    height: $micro-guide-item-logo-height-720p;
                    max-width: $micro-guide-item-width-720p * 0.25;
                }
            }

            // When the channel lacks all data needed, display only the logo
            &.no-data .channel-logo {
                width: $micro-guide-item-width-720p;
                top: 33px;
                img {
                    height: $micro-guide-item-logo-height-no-data-720p;
                    // When all you have is alt text
                    &[src=''],
                    &:not([src]) {
                        top: 9px;
                    }
                }
            }
        }
    }
}
