@import '../../styles/mixins';
@import '../../styles/variables';

core-modal-forgot-password {
    .core-modal {
        @include fade-in();
        backface-visibility: hidden;

        .modal-header {
            color: #fff;

            .forgot-password-title {
                &.error-message {
                    color: $color-orange;
                }
            }
        }

        .modal-body {
            .forgot-password-input {
                font-weight: 500;
                color: #fff;
                margin: 0;
                border-radius: 0;
                outline: none;
                box-shadow: none!important;
            }
        }

        .modal-footer {
            text-align: center;
            margin-top: 0px;
        }
    }

    .core-spinner {
        z-index: 9999;
    }
}
