@import '../../../styles/mixins';

core-iap-billing {
    .account-billing-page {
        display: flex;
        flex-direction: row;
        height: 100%;

        core-billing-form {
            width: 60%;
            margin: 5.55vh 5.2vw;
        }

        input[type=number]::-webkit-inner-spin-button,
        input[type=number]::-webkit-outer-spin-button {
            -webkit-appearance: none;
            -moz-appearance: none;
            appearance: none;
            margin: 0;
        }
    }
}
