@import "variables.10ft";
@import './style';
@import '../ribbon-system.variables.10ft.scss';

/* 10ft specific styles */
core-ribbon-item-favorite-channels {
    li {
        padding: 19px 28px;
        height: $my-channels-asset-tile-height;
        background-color: $color-light-cool-gray;
        border-bottom: 3px solid $color-light-cool-gray;

        // Large title, spans entire width
        h3 {
            font-size: 28px;
            line-height: 35px;
        }

        // Absolutely-positioned metadata
        h4 {
            font-size: 22px;
            bottom: 55px;
            width: $my-channels-asset-tile-width * 0.6;
            color: $color-off-white;

            // spaces between items in 10ft sketches
            span::after {
                content: "";
                padding-left: 0.3em;
            }

            span:last-child::after {
                content: "";
                padding-left: 0;
            }
        }

        // Absolutely-positioned upcoming show
        p {
            font-size: 22px;
            bottom: 19px;
            width: $my-channels-asset-tile-width * 0.6;
            color: $color-off-white;
        }

        // Channel logo
        span.channel-logo {
            width: $my-channels-asset-tile-width * 0.3;
            right: 28px;
            bottom: 19px;
            img {
                max-height: 60px;
                max-width: $my-channels-asset-tile-width * 0.3;
            }
        }

        .asset-locked-icon {
            i {
                font-size: 1.6em;
                margin-top: 4px;
            }
        }

        .offscreen-text {
            @include offscreen-accessible-element();
        }
    }
}

@include scale-for-720p {
    core-ribbon-item-favorite-channels {
        li.favorite-channel-tile {
            padding: 13px 19px;
            height: $my-channels-asset-tile-height-720p;
            border-bottom-width: 2px;

            // Large title, spans entire width
            h3 {
                font-size: 19px;
                line-height: 23px;
            }

            // Absolutely-positioned metadata
            h4 {
                font-size: 15px;
                bottom: 37px;
                width: $my-channels-asset-tile-width-720p * 0.6;

                // spaces between items in 10ft sketches
                span::after {
                    padding-left: 0.2em;
                }
            }

            // Absolutely-positioned upcoming show
            p {
                font-size: 15px;
                bottom: 13px;
                width: $my-channels-asset-tile-width-720p * 0.6;
            }

            // Channel logo
            span.channel-logo {
                width: $my-channels-asset-tile-width-720p * 0.3;
                right: 19px;
                bottom: 13px;
                img {
                    max-height: 40px;
                    max-width: $my-channels-asset-tile-width-720p * 0.3;
                }
            }

            .asset-locked-icon {
                i {
                    font-size: 1.2em;
                    margin-top: 0px;
                }
            }
        }
    }
}
