@import "variables.10ft";
@import 'style';
@import '../../styles/mixins';

@include scale-for-720p {
    $arrow-size-720p: 7px;
    $outer-arrow-size-720p: 8px;

    .coreValidationToolTip {
        border-width: 1px;
        padding: 10px 13px;
        margin-top: -3px;
        font-size: 14px;
        // Larger sized triangle that acts as the border
        &:after {
           border-width: 0 $outer-arrow-size-720p $outer-arrow-size-720p;
            left: 13px;
            top: -8px;
        }
        // inner triangle that acts as the background
        &:before {
            border-width: 0 $arrow-size-720p $arrow-size-720p;
            left: 15px;
            top: -5px;
        }
    }
}
