@import '../../../styles/variables';
@import '../../../styles/mixins';

core-device-options {
    .device-name-section {
        .device-name-label {
            height: 48px;
            font-size: $font-size-bigger2;
            text-align: left;
            color: $color-white;
        }
        .device-name {
            height: 56px;
            font-size: $font-size-biggest2;
            font-weight: 500;
            text-align: left;
            color: $color-white;
        }
    }
    .hidden-accessible-element {
        @include offscreen-accessible-element();
    }
}
