@import "variables.10ft";
@import './style';

// 10ft platform overrides for My TV screen
core-my-tv {
    height: 87vh;
    flex: 1;
    display: block;
    position: relative;
    & section {
        height: 100%;

        .scrolling-container {
            padding-bottom: 5vh;
        }
    }
    .vertical-scroll-container {
        height: 87vh;

        core-screen-ribbon {
            display: block;
        }
    }
}
