@import '../../../styles/mixins.scss';

core-ribbon-utility-tile {
    .utility-tile {
        @include black-box($asset-border-size);
        @include flex-center();
        height: $asset-item-height;
        width: 100%;
        // font-size is set in platform specific style sheets
        font-weight: bold;
        fill: $color-light-gray;
        margin-right: $asset-item-spacer;
        text-align: center;
        line-height: $asset-item-line-height;
        // width is set in platform specific style sheets

        &:hover {
            fill: $color-white;
            @include hovered-box();
        }

        &:focus,
        &.focus-grid-focused {
            fill: $color-white;
            @include focused-box();
        }

        .utility-icon {
            // font-size is set in platform specific style sheets
            margin-bottom: 10px;
        }

    }

    &.favorite-channels__utility-tile .utility-tile {
        // height is set in platform stylesheets
    }
}
