@import "variables.10ft";
@import "../base";

.init-loader {
    background-color: $color-black;
    height: 100%;
    width: 100%;
    text-align: center;
    @include full-absolute();

    .loader-inner {
        position: absolute;
        vertical-align: middle;
        margin-top: 10%;
        width: 100%;
    }

    .dish-logo {
        background: url("../../assets/images/sling_logo_splash.png") no-repeat;
        background-size: cover;
        background-position: center;
        margin: auto;
        width: 100vw;
        height: 100vh;
        top: 0;
        position: fixed;
    }

    .fa {
        color: $color-yellow;
        width: 120px;
    }
}

@include scale-for-hd-tv {
    .init-loader {
        .dish-logo {
            background-size: 1280px 720px;
        }

        .fa {
            width: 80px;
        }
    }
}
