@import "variables.10ft";
@import './style';
@import '../../guide-container.variables.10ft';

core-channel-ribbon {
    height: $channel-ribbon-height;
    padding: 0;
    .core-ribbon-container {
        height: inherit;
        display: block;
        overflow-x: auto;
        width: 100%;
        line-height: 0;

        .core-channel-ribbon__item {
            &--selected {
                @include hovered-box();
            }
            &.core-focused-item {
                @include focused-box();
            }
            img {
                @include ellipsis-able();

                &::before {
                    line-height: 70px;
                }
            }
        }
    }
}


@include scale-for-720p {
    core-channel-ribbon {
        padding: 7px 0;

        .core-ribbon-container {
            .core-channel-ribbon__item {
                img {

                    &::before {
                        font-size: 11px;
                        width: 67px;
                        line-height: 47px;
                    }
                }
            }
        }
    }
}
