@import "variables.10ft";
@import './style';

core-asset-progress-bar {
    &.episode-progress {
        .progress-line {
            width: 300px;
            height: 12px;
            border: 2px solid $color-warm-gray;
        }
    }
}

@include scale-for-720p {
  core-asset-progress-bar {
        .progress-line {
            height: $asset-progress-bar-height-720p;
        }

        &.episode-progress {
            .progress-line {
                width: 200px;
                height: 8px;
                border-width: 1px;
                border-radius: 4px;
            }
        }
    }
}
