@import "variables.10ft";
@import "../../../styles/base";
@import "../../../styles/mixins";

.paused-indicator {
  @include flex-box;
  @include align-items(center);
  @include make-transform(-50%, -50%);
  @include vertical-horizontal-center;
  color: $color-white;
  @include font-size(biggest4);
  text-shadow: $color-overlay-gray 0 0 5px;
  fill: $color-white;

  svg {
    @include include-icon($playback-controls-icon-width, $playback-controls-icon-height);
  }
}

@include scale-for-hd-tv {
    .paused-indicator {
        text-shadow: $color-overlay-gray 0 0 4px;

        svg {
            @include include-icon($playback-controls-icon-width-hd-tv, $playback-controls-icon-height-hd-tv);
        }
    }
}
