@import '../../../../styles/mixins.scss';

core-screen-ribbon-row {
    h2.ribbon-title {
        color: $color-white;
        letter-spacing: 0.4px;
    }

    .ribbon-section {
        position: relative;

        .message {
            color: $color-yellow;
            text-transform: none;
            margin-left: 20px;
            font-weight: 400;
        }
    }

    .ribbon-container {
        line-height: 0;
    }

    .ribbon-content {
        display: inline-flex;
        flex: 1;
    }

    .scrolling-ribbon__empty .ribbon-content::after {
        background: repeating-linear-gradient(to right, rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0.25) 315px, transparent 315px, transparent 330px);
        content: '';
        flex: 1;
    }
}