@import '../../../styles/variables';
@import '../../../styles/mixins';

core-iap-wizard-view {
    .wizard-view {
        height: 100%;
    }

    .top-nav {
        position: relative;
        float: left;
        width: 100%;
        height: 160px;
        background: $color-black;
        white-space: nowrap;

        .sling-logo {
            height: 140px;
            width: 192px;
            margin: 0 250px 0 100px;
            float: left;

            &.fallback-logo {
                content: url("../../../../assets/images/sling_logo.svg");
            }
        }

        .top-nav-progress {
            height: 32px;
            margin-top: 45px;
            width: 50%;
            float: left;
            position: relative;

            .progress-bar {
                height: 100%;
                background-position: bottom;
                background-size: contain;
                background-repeat: no-repeat;
            }

            &.step-one-of-three .progress-bar {
                background-image: url("../../../../assets/images/signup_progress_1_of_3.png");
            }

            &.step-two-of-three .progress-bar {
                background-image: url("../../../../assets/images/signup_progress_2_of_3.png");
            }

            &.step-three-of-three .progress-bar {
                background-image: url("../../../../assets/images/signup_progress_3_of_3.png");
            }

            &.step-one-of-four .progress-bar {
                background-image: url("../../../../assets/images/signup_progress_1_of_4.png");
            }

            &.step-two-of-four .progress-bar {
                background-image: url("../../../../assets/images/signup_progress_2_of_4.png");
            }

            &.step-three-of-four .progress-bar {
                background-image: url("../../../../assets/images/signup_progress_3_of_4.png");
            }

            &.step-four-of-four .progress-bar {
                background-image: url("../../../../assets/images/signup_progress_4_of_4.png");
            }

            .progress-text {
                &.set-3 {
                    margin: 0 12%;
                }
                &.set-4 {
                    margin: 0;
                }

                .progress-step {
                    font-size: 21px;
                    margin-top: 15px;
                    color: $color-gray;
                    display: inline-block;
                    text-align: center;
                    vertical-align: top;
                    line-height: 18px;
                    padding: 0 5px;

                    &.active {
                        color: $color-off-white;
                    }
                }

                .progress-3-step {
                    width: (100% / 3);
                }

                .progress-4-step {
                    width: (100% / 4);
                }
            }
        }
    }

    .main-content {
        position: relative;
        float: left;
        height: calc(100% - 320px);
        width: 100%;
        background: $color-dark-gray;

        .container {
            height: 100%;
        }
    }

    .bottom-nav {
        height: 160px;
        background: $color-black;
        position: absolute;
        bottom: 0;
        width: 100%;

        .footer-group {
            width: 50%;
            height: 100%;
            position: absolute;
            right: 0;
            top: 0;
            padding-right: 100px;

            .btn {
                float: right;
                margin: 45px 0 0 20px;

                &.disabled {
                    background-color: $color-cool-gray;
                    color: $color-silver;
                }
            }
        }

        .digicert-image {
            @include position($top: 30px, $left: 30px);
            width: 122px;
            height: 88px;
            background: url("../../../../assets/images/digi-seal.png");
            outline: none;
        }

        .billing-footnote {
            height: 150px;
            width: 550px;
            position: absolute;
            right: 0;
            bottom: 0;
            border-radius: 33px 0 0 0;
            background-color: $color-light-black;
        }

        .terms-agreement-footnote {
            height: 150px;
            width: 1150px;
            position: absolute;
            right: 0;
            bottom: 0;
            border-radius: 33px 0 0 0;
            background-color: $color-light-black;

            .terms-text {
                @include position($top: 30px, $left: 211px);
                width: 367px;
                font-size: 21px;
                line-height: 32px;
                font-weight: 600;
                color: $color-off-white;
            }

            button {
                text-decoration: none;
                color: #ebebeb;
                margin: 0;
                padding: 0;
                border: 0;
                background: transparent;
                font-size: inherit;

                &.core-focused-item {
                    color: $color-dark-blue;
                    text-decoration: underline;
                }
            }
        }
    }

    .core-spinner {
        position: fixed;
        top: 0;
    }
}

.modal-iap .core-modal {
    width: 900px;
}

.modal-kount-error .core-modal {
    .modal-body p {
        color: $color-white;
        line-height: normal
    }

    .modal-footer {
        .btn {
            text-transform: uppercase;
        }
    }
}
