@import "variables.10ft";
@import './style';
@import '../../styles/mixins.scss';
@import '../../styles/variables.10ft';

// 10ft platform overrides for Settings screen
core-settings-container {
    height: 87vh;

    .settings-content {
        padding: 50px 70px;
        width: calc(100% - #{$settings-left-menu-width});

        h3 {
            margin-bottom: 30px;
        }

        button {
            margin: 20px 0;
        }
    }
}

@include scale-for-720p {
    core-settings-container {
        .settings-content {
            padding: 25px 35px;
            width: calc(100% - #{$settings-left-menu-width-720p});
        }
    }
}
