@import '../../../styles/mixins.scss';
@import '../../../styles/variables.scss';

.tile-container {
    cursor: pointer;
    position: relative;
    overflow: hidden;
    display: inline-block;

    .thumbnail {
        display: flex;
        justify-content: center;
        background-color: $color-gray;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
    }

    .icon-no-tv {
        height: 100%;
        width: 100%;
        display: flex;
        font-size: 92px;
        color: $color-off-white;
        background: $color-gray;
        align-items: center;
        justify-content: center;
    }

    .standard-tile {
        height: auto;
        position: relative;
        outline: none;
        background-color: $color-black;

        .attributes {
            color: $color-off-white;
        }

        .icon-locked {
            @include full-absolute();
            @include full-center();
        }

        .icon-quick-play {
            display: none;
            position: absolute;
            transition: opacity 200ms ease-in-out;
            border-radius: 50%;
            box-shadow: 0 0 6px 2px rgba(0, 0, 0, 0.6);
            font-size: $play-icon-size;
            opacity: 0.7;
        }

        &:hover {
            .metadata {
                background-color: $color-gray;
            }

            .attributes {
                color: $color-white;
            }

            .icon-quick-play {
                display: initial;
                background-color: $color-light-text-gray;

                &:hover {
                    font-size: $play-icon-size-hover;
                    opacity: 1;
                }
            }
        }

        &:focus {
            background-color: $color-dark-blue
        }

        .unentitled-icon-wrapper {
            position: absolute;
            display: flex;
            top: 0;
            right: 0;
            font-weight: 700;
            background-color: $color-blue;
            align-items: center;
        }

    }

    .metadata {
        display: flex;
        flex-direction: column;
        justify-content: center;

        .meta-title {
            width: 100%;
            line-height: normal;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            font-weight: 500;
        }

        .attributes {
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
            line-height: normal;
            width: 100%;

            .featured {
                color: $color-yellow;
            }

            span {
                &::before {
                    content: "|";
                }

                &:first-child {
                    &::before {
                        content: "";
                    }
                }
            }
        }

        &.MICRO_GUIDE {
            background-color: initial;
            padding: 0;
            display: block;

            .meta-title {
                padding: 0;
            }

            .attributes {
                color: $color-off-white;
                font-weight: normal;
                margin: 1.5vh 0 0.5vh;
            }

            .upcoming-content {
                color: $color-off-white;
                white-space: nowrap;
                text-overflow: ellipsis;
                overflow: hidden;
                line-height: normal;
                display: inline-block;
            }
        }
    }

    .channel-tile {
        background-color: $color-light-cool-gray;
        position: relative;
        outline: none;

        &:hover {
            background-color: $color-gray;
            outline: none;
            text-decoration: none;
            cursor: pointer;
            .metadata .attributes, .metadata .upcoming-content{
                color: $color-white;
            }
        }

        img {
            width: auto;
            position: absolute;
            display: inline-block;
            text-align: right;
        }

        .metadata {
            background-color: transparent !important;
        }
    }

    .progress-bar {
        position: absolute;
        width: 100%;
        left: 0;
        background-color: $color-dark-gray;

        .progress-indicator {
            height: inherit;
            background-color: $color-yellow;
            position: absolute;
        }
    }

    .utility-tile {
        @include flex-center();
        outline: none;
        color: $color-off-white;
        background-color: $color-black;

        .utility-tile-text {
            text-transform: uppercase;
            text-align: center;
        }
    }
}

// UI4.0
.metadata-ui4 {
    display: flex;
    flex-direction: column;
    justify-content: center;

    &.STANDARD, &.STANDARD_V4 {
        .meta-title-badge-container {
            display: flex;
            flex: 1;
            color: $color-off-white;

            .meta-title {
                flex: 4;
                justify-content: center;
                text-align: center;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }

            .badges {
                flex: 1;
                height: 1.48vh;
                display: flex;
                justify-content: flex-end;
            }
        }
        .meta-attributes {
            text-align: center;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            line-height: 1.3;
        }
    }
}

.standard-tile-ui4 {
    cursor: pointer;
    position: relative;
    overflow: hidden;
    display: inline-block;
    height: auto;
    outline: none;
    background-color: transparent;

    .thumbnail {
        display: flex;
        justify-content: center;
        background-color: $color-gray;
        background-repeat: no-repeat;
        background-position: center;

        .icon-no-tv {
            @include full-center();
            height: 100%;
            width: 100%;
            font-size: 92px;
            color: $color-off-white;
            background: $color-gray;
        }

        &.icon-locked::before {
            @include full-absolute();
            @include full-center();
            background-color: $color-faux-black;
            color: $color-tint-gray;
            z-index: $index-front;
        }
    }

    .progress-bar {
        position: absolute;
        width: 100%;
        left: 0;
        background-color: $color-dark-gray;

        .progress-indicator {
            height: inherit;
            background-color: $color-yellow;
            position: absolute;
        }
    }

    .unentitled-icon-wrapper {
        position: absolute;
        display: flex;
        top: 0;
        right: 0;
        font-weight: 700;
        background-color: $color-blue;
        align-items: center;
    }
}

.utility-tile-ui4 {
    cursor: pointer;
    position: relative;
    overflow: hidden;
    display: inline-block;
    @include flex-center();
    padding: 0 1vw;
    outline: none;
    color: $color-white;
    background-color: $color-tawny;

    .utility-tile-text {
        text-transform: uppercase;
        text-align: center;
    }
}

