@import '../../../styles/mixins';

core-rental-no-payment {

    .nopayment-container {
        width: 700px;
    }

    .modal-body {

        .header {
            font-weight: 200;
            padding-bottom: 35px;
            color: $color-white;
            text-align: center;
        }
        .message {
            line-height: 1.75em;
            font-size: 17px;
            font-weight: 300;
            text-align: center;

            a {
                color: $color-light-blue;
                text-decoration: none;
            }

            .phone {
                color: $color-white;
                font-weight: 400;
                white-space: nowrap;
            }
        }
    }

    .modal-footer {
        text-align: center;
    }
}
