@import "variables.10ft";
@import '../../ribbon-system/ribbon-system.variables.10ft';
@import './style';
@import '../../../styles/mixins.scss';

core-dvr-manage-recordings {
    @import '../dvr-recordings.10ft';

    .dvr-content-container {
        padding-bottom: 25vh; // add extra room at the bottom
    }

    .btn.manage-select {
        width: 216px;
    }
}

@include scale-for-720p {
    core-dvr-manage-recordings {
        .btn.manage-select {
            width: 144px;
        }

        .auto-delete-text {
            font-size: 17px;
            line-height: 24px;
            margin: 20px 0 0 67px;
        }
    }
}
