// Micro guide styles shared by all platforms
core-micro-guide {
    position: fixed;
    left: 0;
    right: 0;
    transition: all 0.5s ease;

    .empty-page-container {
        width: 100%;
        background-color: $color-overlay-dark-gray;
        height: 200px;
        padding-top: 12px;
    }
    // Visibility for this component set in platform stylesheets

    // Filter links above the micro guide channels ('All Channels' etc)
    nav.channel-filter-container {
        border-bottom: 2px solid $color-black;
        // height and background color set in platform stylesheets

        ul {
            display: flex;
            justify-content: left;
            align-items: center;
            list-style-type: none;
            // margin set in platform stylesheets

            li {
                text-transform: uppercase;
                white-space: nowrap;
                overflow: hidden;
                flex: 0 0 auto; // don't let items shrink
                color: $color-text-grey;
                cursor: pointer;
                margin: 0;
                // padding, height and font-size set in platform stylesheets

                // hover, focus and selected text in the filter is white
                // see platform stylesheets for other values
                &.core-focused-item,
                &:hover,
                &.selected {
                    color: $color-white;
                }

                // text container for spacing and underlines
                span {
                    display: inline-block;
                }
            }
        }
    }

    // Container for the channels within the paging ribbon
    div.channel-list-container {
        background-color: $color-overlay-dark-gray;

        ul {
            display: inline-flex;
            justify-content: flex-start;
            align-items: center;
            list-style-type: none;

            .core-focused-item {
                background-color: $color-dark-blue;
            }
        }
    }
}
