@import "variables.10ft";
@import './style';

@include scale-for-720p {
    core-iap-wizard-view {
        .top-nav {
            height: 107px;

            .sling-logo {
                height: 94px;
                width: 100px;
                margin: 0 167px 0 70px;
            }

            .top-nav-progress {
                height: 22px;
                margin-top: 30px;

                .progress-text {
                    .progress-step {
                        font-size: 14px;
                        margin-top: 10px;
                        line-height: 12px;
                        padding: 0 4px;
                    }
                }
            }
        }

        .main-content {
            height: calc(100% - 214px);
        }

        .bottom-nav {
            height: 107px;

            .digicert-image {
                left: 20px;
                top: 16px;
                width: 75px;
                height: 57px;
                background-size: 75px 57px;
            }

            .footer-group {
                padding-right: 67px;

                .billing-footnote {
                    height: 97px;
                    width: 467px;
                }

                .terms-agreement-footnote {
                    height: 97px;
                    width: 767px;
                    border-radius: 22px 0 0 0;


                    .terms-text {
                        top: 16px;
                        width: 245px;
                        font-size: 14px;
                        line-height: 21px;
                        left: 141px;
                    }
                }

                .btn {
                    margin: 30px 0 0 14px;
                }
            }

        }
    }
}
