// The dvr-recordings.UI_TYPE.scss sheets are designed to be imported inside the dvr-manage-recordings and
// dvr-view-recordings sheets as they both utilize the tool bar, recordings container, and auto delete text styles

// The platform specific style sheets should be imported directly after the component-level selector.
@import '../../styles/variables.scss';

.core-tool-bar {
    display: flex;
    position: relative;
    background-color: $color-medium-cool-gray;
    display: flex;
    padding: 15px $ribbon-left-gutter;
    min-width: 300px;
    border-bottom: none;

    &::after {
        $height: 20px;
        @include vertical-gradient(rgba(0, 0, 0, .25), transparent);
        content: ' ';
        position: absolute;
        display: block;
        width: 100%;
        height: $height;
        top: auto;
        left: 0;
        right: 0;
        bottom: -$height;
        z-index: 1;
    }
    // font-size is set in platform specific style sheets
    // padding is set in platform specific style sheets

    // Permit the flex-children to grow but not to shrink below what is needed to
    // display their text without wrapping
    button {
        flex-grow: 1;
        flex-shrink: 0;
        white-space: nowrap;
    }
}

.recordings-container {
    // border-top is set in platform specific style sheets
}

.auto-delete-text {
    color: $color-cool-white;
    // margin is set in platform specific style sheets
    text-align: center;
}
