@import "variables.10ft";
// Import local variables shared by guide/grid-guide components
@import '../grid-guide.variables.10ft';
@import './style';
@import '../../../../styles/mixins';

core-grid-schedule-row {
  .grid_guide__schedule-row {
    .error {
      padding: $grid-guide-item-padding;
    }
  }
}

@include scale-for-720p {
  core-grid-schedule-row {
    left: $grid-guide-channel-width-720p;

    .grid_guide__schedule-row {
      height: $grid-guide-row-height-720p;

      .no-linear-schedule {
        padding: $grid-guide-item-padding-720p;
      }

      .loading-text {
        padding: $grid-guide-item-padding-720p;
      }

      .shared-gradient {
        width: 80px;
      }

      .error {
        padding: $grid-guide-item-padding-720p;
      }
    }
  }
}
