@import "variables.10ft";
@import '../../../../styles/mixins';

.modal-with-checkbox {
    width: 33.5vw;

    .modal-close-btn {
        display: none;
    }

    .modal-header {
        .modal-header-text {
            color: $color-yellow;
            font-size: 2.8vh;
            font-weight: 500;
            text-transform: uppercase;
            text-align: center;
        }
    }

    .modal-body {
        .modal-checkbox {
            display: none;
        }
    }

    .modal-footer {
        display: flex;
        justify-content: center;
        .focused-button{
            background-color: $color-dark-blue;
            color: $color-white;
            outline: none;
            text-decoration: none;
            cursor: pointer;
        }
    }
}
