@import "variables.10ft";
@import '../../../styles/mixins';

.banner-container {
    height: 242px;
    background-color: $color-black;
    width: 100%;
    background-size: auto;
    background-repeat: no-repeat;
    background-position: top left 553px; /* image starts 200px before content ends because of transparent wedge */

    .btn {
        height: 48px;
        width: 148px;
    }

    @media screen and (min-width: 2027px) {
        /* Once browser window goes wider than needs of full content & image sections,
           then the image must float right. At 2027px and less the image will start
           cutting off, until we get to tablet width where image is removed. */
        background-position-x: right;
    }

    .banner-content {
        text-align: center;
        height: 100%;
        width: calc(100% - 1214px); /* image is 1464px wide minus 250px of black gradient area */
        min-width: 753px; /* width of text section per the mock */
    }

    .banner-title {
        font-weight: bold;
        color: $color-yellow;
        font-size: 58px;
        line-height: 78px;
    }

    .banner-subtitle {
        font-size: 28px;
        font-weight: 500;
        line-height: 39px;
    }

    .banner-buttons {
        margin-top: 24px;
    }

    @include scale-for-responsive {
        height: 205px;
        background-image: radial-gradient(circle 200px at top right, #2f2f2f 0%, $color-black 100%) !important;
        background-position: left;

        .banner-title {
            font-size: 56px;
        }

        .banner-content {
            width: 100%;
            min-width: initial;
            padding-top: 10px;
        }

        .banner-buttons {
            margin-top: 10px;
        }
    }

    @include scale-for-mobile {
        height: 164px;

        .banner-title {
            font-size: 30px;
            line-height: 42px;
        }

        .banner-subtitle {
            font-size: 18px;
            line-height: 21px;
        }

        .banner-buttons {
            margin-top: 18px;
        }
    }
    @include scale-for-small-mobile {
        .btn {
            height: 40px;
            width: 114px;
        }

        .banner-title {
            font-size: 8vw;
        }

        .banner-subtitle {
            font-size: 4.8vw;
        }
    }
}
