@import '../../../styles/mixins.scss';

core-record-button-player-control {
    // @TODO WEB-5167 - Styles below are tailored for the 10ft UI
    // Need adjusted for other platforms
    div.player-record-action {
        width: 200px;
        display: inline-flex;
        flex-direction: column;
    }
    button.record-button {
        background: transparent;
        border: none;
        outline: none;
        padding: 0;
        margin: 0;
        span.button-icon {
            width: 99px;
            height: 104px;
            padding-top: 20px;
            display: block;
            text-align: center;
            background: transparent;
            margin: 0 auto 7px;
            border: solid 2px transparent;

            .icon-rec-single {
                font-size: 60px;
                color: $color-light-gray;
            }
            &.recording {
                .icon-rec-single {
                    color: $color-red;
                }
            }
        }

        span.button-icon, &.recording {
            .icon-rec-single {
                &:hover {
                    color: $color-white;
                }
            }
        }
        span.button-text {
            font-size: 25px;
            margin: auto;
            display: none;
        }
        &.core-focused-item {
            span.button-icon {
                background-color: $color-dark-blue;
            }
            span.button-text {
                display: block;
            }
        }
        &.core-focused-item:enabled {
            span.button-icon:not(.recording) {
                .icon-rec-single {
                    color: $color-white;
                }
            }
        }
        &:disabled {
            span.button-icon {
                .icon-rec-single {
                    color: $color-off-white;
                }
            }
        }
    }

    .offscreen-text {
        @include offscreen-accessible-element();
    }
}
