@import './buttons';
@import './mixins';

// 10ft specific button styles
.btn {
    font-size: 28px;
    font-weight: 500;
    min-width: 180px;
    padding: 18px 30px;

    .icon-minimize {
        font-size: 21px;
    }


     + .btn {
        margin-left: 20px;
    }

    &:focus {
        outline: 0;
    }

    &.primary,
    &.active,
    &:active,
    &.core-focused-item {
        color: $color-white;
        background-color: $color-dark-blue !important;
        outline: none;

        // icon hover state color
        i:before {
            color: $color-white;
        }
    }
}

.block-buttons {
    .btn + .btn {
        margin-top: 12px;
    }
}

@include scale-for-720p {
    .btn {
        font-size: 19px;
        min-width: 120px;
        height: 50px;
        padding: 12px 20px;

        .icon-minimize {
            font-size: 14px;
        }

        + .btn {
            margin-left: 14px;
        }
    }

    .block-buttons {
        .btn + .btn {
            margin-top: 8px;
        }
    }
}
