@import "variables.10ft";
@import '../../../styles/variables';

.empty-screen-message-container {
    position: relative;
    margin: 10vh auto 0 auto;
    text-align: center;
    width: 24vw;

    .icon-ghost {
        display: inline-block;
        font-size: 10vh;
        color: $color-light-gray;
    }

    .empty-screen-message-title {
        color: $color-yellow;
        margin-top: 1vh;
        font-size: 2.5vh;
        font-weight: bold;
        text-transform: uppercase;
    }

    .empty-screen-message-description {
        color: $color-off-white;
        font-size: 2.3vh;
        margin-top: 1.11vh;
    }
}
