@import "variables.10ft";
@import "../../styles/base";

#screensaver-page {
    .screensaver-container {
        z-index: $index-front;
        background-color: $color-black;
        height: 100%;
        width: 100%;
        top: 0;
        left: 0;
        position: fixed;
        display: block;
    }

    .screensaver-logo {
        display: block;
        width: 240px;
        height: 240px;
        position: absolute;
        background: url("../../assets/images/logo_400x400.png") no-repeat;
        background-size: 100%;
        opacity: 1;
        transition: $animation-speed opacity ease-in;

        &.fade {
            opacity: 0;
            transition: $animation-speed opacity ease-in-out;
        }
    }

}