@import "variables.10ft";
.search-container {
    height: 83.9vh;
    overflow: hidden;
    line-height: 2.5vh;
    display: flex;
    flex: auto;
    outline: none;
}

.left-div {
    flex: 1;
    padding: 1.6vw 3vh 0 2.6vw;
}

.right-div {
    flex: 4;
    height: 100%;
    padding: 1.6vw 2.6vw 0 0;
    position: relative;
    display: flex;

    .skeleton-screen {
        padding-left: 1vw;
        padding-top: 6.6vh;
    }
}

.search-results-container {
    height: 83.9vh;
    width: 100%;
    overflow: hidden;
    display: flex;
    flex-direction: column;

    .grid-container {
        margin-left: 1vw;
    }
}

.popular-searches-header {
    padding: 1.3vh .55vw;
    height: 5.3vh;
    color: $color-white;
    font-size: 2.2vh;
    background-color: $color-dark-gray;
    text-transform: uppercase;
    position: absolute;
    z-index: 1;
    width: 100%;
    line-height: 4.5vh;
    margin-top: -0.2vh;
    margin-left: 0.6vw;
}

.empty-search-results {
    text-transform: uppercase;
    padding-top: 15vh;
    text-align: center;
    width: 100%;
    font-size: 4.2vh;
    color: $color-warm-gray;
}
