// See style.win10.scss, style.10ft.scss, style.3ft.scss for platform specific styling.

core-grid-guide {
    height: 100%;
    position: relative;
    display: block;

    .grid_guide__container {
        height: 100%;
        position: relative;
        background: $color-light-black;
        touch-action: none;
        overflow: hidden;
        -webkit-clip-path: inset(1px 0px 0px);
        clip-path: inset(1px 0px 0px);

        .sizer {
            position: absolute;
        }

        .grid_guide__wrapper {
            position: fixed;
        }

        .ruler-container {
            top: 0;
            left: 0;
            position: absolute;
            z-index: $index-front;
            border-bottom: 2px solid $color-black-shadow;

            // Space to left of ruler times, above channels
            .empty-box {
                // platform specific properties: width
                border-right: 2px solid $color-medium-gray;
                background-color: $color-dark-gray;
                z-index: $index-front + 1;
            }

            .grid-guide-ruler {
                //CSS trick to improve rendering time of translated elements
                backface-visibility: hidden;
                position: absolute;
            }
        }

        .schedule-container {
            // max-height is set by the CoreGridScroll directive. See: ./grid-scroll/grid-scroll.directive.js - setGuideContentHeight() method.

            .grid_guide__schedule-container {
                position: absolute;
                border-bottom: 2px solid $color-gray;

                //CSS trick to improve rendering time of translated elements
                backface-visibility: hidden;

                &.is-loading {
                    opacity: .5;
                }
            }
        }
    }
}
