@import "variables.10ft";
@import '../../styles/variables';
@import '../../styles/mixins';

$focus: "&:focus, &:hover, &.focus-grid-focused";
$row-padding: 0 24%;
core-activation-code {
    .activation {
        @include flex-center();
        height: 100vh;
        background-color: $color-dark-gray;

        &__sling-logo {
            @include position($top: 1vh, $left: 4vw);
            height: 10vh;
            width: 150px;
            margin-right: auto;

            &.fallback-logo {
                content: url("../../../assets/images/sling_logo.svg");
            }
        }

        &__header,
        &__instructions,
        &__message,
        &__return-button,
        &__return-button-arrow,
        &__code {
            margin-top: 0;
            margin-bottom: 50px;
            font-weight: 600;
            color: $color-text-grey;
        }

        &__header {
            margin-top: 16vh;
            font-weight: 550;
            color: $color-white;
            font-size: 2.5em;
        }

        &__instructions {
            line-height: initial;
            padding: $row-padding;
            margin-top: 0;
            font-size: 1.9em;
            text-align: center;
            font-weight: 450;
            > a {
                color: $color-white;
                text-decoration: underline;
            }
        }

        &__code {
            @include box-shadow(3px, 4px, 10px, $color-overlay-black);
            line-height: 27px; // this is a hack because in garuda they have this on the body element.
            color: $color-white;
            padding: 0.7em 0.9em;
            background: $color-medium-gray;
            letter-spacing: 10px;
            border-radius: 3px;
            font-size: 3em;
            font-weight: 700;
        }

        core-spinner {
            margin-top: -20px;
            height: 136px; // prevent flicker when the spinner goes away
        }

        &__message {
            color: $color-off-white;
            font-size: 1.3em;
            font-weight: 400;
            white-space: pre-line;
            text-align: center;
            margin-bottom: 30px;
        }

        &__return-buttons {
            @include box-align($flex-direction: row);
            margin-left: auto;
            margin-right: 5vw;
            font-size: 3.7vh;
            font-weight: bold;
            margin-top: auto;
            margin-bottom: 5vh;
            button.btn {
                height: 84px;
                border-radius: 6px;
                background-color: transparent;
                color: $color-off-white;
                #{$focus}, &.active {
                    background-color: $color-dark-blue;
                    color: $color-white;
                }

                &.activation__return-button {
                    #{$focus} {
                        & ~ &.activation__return-button-arrow {
                            background-color: $color-dark-blue;
                            color: $color-white;
                        }
                    }
                }

                &.activation__return-button-arrow {
                    min-width: 65px;
                    width: 84px;
                    margin-left: -10px;
                    padding-left: 31px;
                    #{$focus} {
                        & ~ &.activation__return-button {
                            background-color: transparent;
                        }
                    }
                    > .fa-angle-right {
                        line-height: 0.6;
                        font-size: 75px;
                    }
                }
            }
        }
    }
}

