@import '../../styles/mixins';

// Radio button component with platform-specific overrides.
// You may wish to set a min-width for this component in your templates.
core-radio {
    display: flex;

    // platforms are using bootstrap - which is greedily attempting to set a
    // negative margin top to the radio - should we use a different style?
    .radio, .radio + .radio {
        margin-top: 0;
    }

    // Don't display the base input element
    input[type='radio'] {
        display: none;
    }

    // The radio button's label container, holding icon and text
    label {
        cursor: pointer;
        display: flex;
        position: relative;
        vertical-align: middle;
        white-space: nowrap;
        width: 100%;
        // font-size in UI_TYPE overrides

        // to override Bootstrap's default label styles
        margin: 0;
        font-weight: inherit;
    }

    // Radio button icon (center of button)
    i.radio-circle.inner {
        position: relative;
        border-radius: 50%;
        // height, width, border, background-color in UI_TYPE overrides
    }

    // Radio button icon (outside of circle, some platforms only)
    i.radio-circle.outer {
        display: none;
    }

    // Radio button text
    span.radio-text {
        @include ellipsis-able();
        // margin-left, color in UI_TYPE overrides
    }

    // Icon when the radio button is checked
    input[type='radio']:checked + label .radio-circle.inner {
        // color in UI_TYPE overrides
        &:before {
            content: "";
            display: block;
            border-radius: 50%;
            // width, height, margin, color in UI_TYPE overrides
        }
    }

    // Text when the radio button is checked
    input[type='radio']:checked + label span.radio-text {
        color: $color-white;
    }

    // Label hover state for icon and text in UI_TYPE overrides
    // Disabled states in the UI_TYPE overrides
}
