@import "./base.hd";

// ==== APP-WIDE VARIABLES ======================================

// for 1920x1080 screen
$font-size-map: (
    smallest:   15px,
    smaller:    16px,
    small:      17px,
    medium:     18px,
    big:        20px,
    big2:       22px,
    big3:       24px,
    bigger:     26px,
    bigger2:    28px,
    biggest:    30px,
    biggest2:   32px,
    biggest3:   40px,
    biggest4:   50px,
    biggest5:   56px,
    biggest6:   60px,
    biggest8:   80px,
    biggest12:  120px,
    biggest13:  130px
);

// TV variables
$tv-font-size-medium: 24px;
$tv-font-size-big: 28px;
$tv-font-size-bigger: 32px;
$tv-font-size-biggest: 40px;

//layout defaults
$font-size-default: 18px;
$line-height-default: $font-size-default * 1.5;

// z-indexing
$index-front:   1;
$index-middle:  0;
$index-back:   -1;

// Animation Timing
$animation-speed: 500ms;

// ==== COLOR PALETTE VARIABLES ==================================

