$color-red:                 #c90018;
$color-orange:              #ff5a2e;
$color-light-orange:        #fa970b;
$color-yellow:              #ffab15;
$color-green:               #78bc43;
$color-light-blue:          #24c1ff;
$color-blue:                #0992cc;
$color-dark-blue:           #006699;

// gray scale
$color-white:               #ffffff;
$color-near-white:          #cccccc;
$color-near-off-white:      #cecece;
$color-off-white:           #999999;
$color-warm-gray:           #979797;
$color-silver:              #878787;
$color-light-gray:          #666666;
$color-mid-gray:            #505050;
$color-gray:                #444444;
$color-cool-gray:           #2A2A2A;
$color-dark-gray:           #222222;
$color-darkest-gray:        #191919;
$color-near-black:          #101010;
$color-black:               #000000;
$color-disabled:            #323232;
$color-hover-gray:          #383838;

// overlay colors
$color-overlay-dark-gray:       rgba(28, 28, 28, 0.90);     // this is '$color-dark-gray' $ 90% opacity
$color-overlay-darkest-gray:    rgba(25, 25, 25, 0.90);     // this is '$color-darkest-gray' $ 90% opacity
$color-overlay-cool-gray:       rgba(42, 42, 42, 0.90);     // this is '$color-cool-gray' $ 90% opacity
$color-overlay-gray:            $color-overlay-dark-gray;
$color-overlay-black:           rgba(0, 0, 0, 0.90);        // this is '$color-black' $ 90% opacity
$color-overlay-invisible:       rgba(0, 0, 0, 0.00);        // this is 'invisible' for utility needs

