$channel-ribbon-background: #1c1c1c; //TODO Same as $color-win10-light-black but thats not imported, colors need fixing
$filter-ribbon-height: 70px;
$ribbon-message-height: 175px;
$ribbon-spinner-size: 30px;
$ribbon-title-size: 36px;
