// ==== UTILITY MIXINS ==================================

@mixin ellipsis-able() {
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    // Height and width should be set on recipient.
}

@mixin fluid-bg-img() {
    height: 100%;
    width: 100%;
    position: relative;
    display: -webkit-box;
    -webkit-box-pack: center;
    box-pack: center;
    -webkit-box-align: center;
    box-align: center;
}

@mixin full-absolute() {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}

// easy animation styles
@mixin animate($name) {
    -webkit-transition: $name $animation-speed ease;
    -moz-transition: $name $animation-speed ease;
    -ms-transition: $name $animation-speed ease;
    -o-transition: $name $animation-speed ease;
}

// consistent border styling (non-vmins)
@mixin border-radius($size){
    border-radius: $size;
    -webkit-border-radius: $size;
}

// gradient inside shadows on the bottom row
@mixin bottom-shadow {
   -moz-box-shadow:    inset 0 -10px 15px -5px $color-black;
   -webkit-box-shadow: inset 0 -10px 15px -5px $color-black;
   box-shadow:         inset 0 -10px 15px -5px $color-black;
}

// gradient fills - invisible to black is the default
@mixin vertical-gradient ($startColor: $color-overlay-invisible, $endColor: $color-black) {
    background-color: $startColor;
    background: -webkit-gradient(linear, left top, left bottom, from($startColor), to($endColor));
    background: -webkit-linear-gradient(top, $startColor, $endColor);
    background: -moz-linear-gradient(top, $startColor, $endColor);
    background: -ms-linear-gradient(top, $startColor, $endColor);
    background: -o-linear-gradient(top, $startColor, $endColor);
}

@mixin horizontal-gradient ($startColor: $color-overlay-invisible, $endColor: $color-black) {
    background-color: $startColor;
    background: -webkit-gradient(linear, left top, right top, from($startColor), to($endColor));
    background: -webkit-linear-gradient(left, $startColor, $endColor);
    background: -moz-linear-gradient(left, $startColor, $endColor);
    background: -ms-linear-gradient(left, $startColor, $endColor);
    background: -o-linear-gradient(left, $startColor, $endColor);
}

// the collective styling for active assets in a ribbon
@mixin active-box {
    background-color: $color-gray;
    border-bottom: 3px solid $color-cool-gray;
    color: $color-white;
    outline: none;
    text-decoration: none;
    cursor: pointer;
    box-shadow: none;
}

// the collective styling for hovered assets in a ribbon
@mixin hovered-box {
    background-color: $color-gray;
    border-bottom: 3px solid $color-white;
    color: $color-white;
    outline: none;
    text-decoration: none;
    cursor: pointer;
}

// the collective styling for focused assets in a ribbon
@mixin focused-box {
    background-color: $color-dark-blue;
    border-bottom: 3px solid $color-light-blue;
    color: $color-white;
    outline: none;
    text-decoration: none;
    cursor: pointer;
}

// disable user select
@mixin disable-select(){
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

// format element to allow spacing for the next element on the row
@mixin multi-fieldset-row($width: 100px, $right-margin: 20px) {
    display: inline-block;
    width: $width;
    margin-right: $right-margin;
}

// Outputs responsive font size
@mixin font-size($size) {
    @if map_has_key($font-size-map, $size) {
        @media (min-width: 1920px) {
            $font-size: map_get($font-size-map, $size);
            font-size: $font-size;
        }
        @media (max-width: 1280px) {
            $font-size: map_get($font-size-map-hd-screen, $size);
            font-size: $font-size;
        }
    }
}

// FHD and UHD TV models specific styling(needs to be called in root/global level of the cascade)
@mixin scale-for-tv() {
    @media (min-width: 1920px) {
        @content;
    }

}

$focus-toggle-gutter: -10px;
@mixin toggle-border($color) {
    &::after {
        content: '';
        position: absolute;
    }

    @media (min-width: 1920px) {
        &::after {
            border: solid 3px $color;
            left: $focus-toggle-gutter;
            top: $focus-toggle-gutter;
            right: $focus-toggle-gutter;
            bottom: $focus-toggle-gutter;
            border-radius: 50px;
        }
    }

    @media (max-width: 1280px) {
        &::after {
            border: solid 2px $color;
            left: $focus-toggle-gutter-hd-tv;
            top: $focus-toggle-gutter-hd-tv;
            right: $focus-toggle-gutter-hd-tv;
            bottom: $focus-toggle-gutter-hd-tv;
            border-radius: 33px;
        }
    }
}

// vertically align the element in its parent container
@mixin center-vertically() {
    position: relative;
    top: 50%;
    transform: translateY(-50%);
}

#noInternetContainer{
    text-align: center;
    font-size: 26px;
    color: #fff;
    background: #333;
    width: 800px;
    padding: 20px;
    border: 2px solid #666;
    position: absolute;
    left: 50%;
    top: 50%;
    margin-left: -400px;
    z-index: 100000;
    line-height: normal;
}

//remove the default angular aria focus; we rely on our own focus grid
*:focus{
    outline: none;
}

.clearfix:before,
.clearfix:after{
    display: table;
    content: " ";
}

.clearfix:after {
    clear: both;
}

.hidden {
    display: none;
}
