@import '../../../styles/mixins.scss';

core-dvr-view-recordings {
    .btn.manage-cancel {
        position: absolute;
        top: -60px;
        right: $ribbon-left-gutter;
    }
    section.dvr-content-container {
        overflow: auto; // allow scrolling if needed
        height: 85vh; // constrain height, note more header on this screen
        padding-bottom: 27vh; // add extra room at the bottom

        @media screen and (max-height: 800px) {
            padding-bottom: 23vh; // more room on shorter screens
        }
    }

    .offscreen-text {
        border: 0;
        clip: rect(0, 0, 0, 0);
        height: 1px;
        margin: -1px;
        overflow: hidden;
        padding: 0;
        width: 1px;
        position: absolute;
    }
}
