@import '../../../../styles/variables.scss';
@import '../../../../styles/mixins';

.privacy-container {
    .privacy-heading {
        font-size: $font-size-biggest2;
        line-height: 42px;
        color: $color-white;

        &:not(:first-child) {
            margin-top: 14px;
        }
    }

    .privacy-text-header {
        margin-top: 26px;
        color: $color-off-white;
        font-size: $font-size-big2;
        line-height: 42px;
        font-weight: bold;
    }

    .toggle {
        float: none;
        display: flex;
        flex-flow: row;
        margin-top: 18px;

        .toggle-text, .toggle-status {
            margin-left: 20px;
            line-height: 42px;
            font-size: $font-size-bigger;
        }

        .toggle-status {
            color: $color-dark-red;
            margin-left: 10px;

            &.on {
                color: $color-green;
            }
        }
    }

    .privacy-text-body {
        position: absolute;
        text-decoration: none;
        font-size: $font-size-bigger;
        line-height: 42px;
        color: $color-off-white;

        .privacy-text {
            max-height: 336px;
            max-width: 1113px;
            overflow: hidden;
            white-space: pre-wrap;
        }

        &::after {
            content: attr(see-more);
            display: block;
            width: 100%;
            height: 1.5em;
            max-width: 1113px;
            margin-top: -42px;
            position: absolute;
            float: right;
            text-align: center;
            background: linear-gradient(to right, rgba(34, 34, 34, 0), rgba(34, 34, 34, 1) 40%);
        }

        &:hover:after {
            text-decoration: underline;
        }

        &.core-focused-item:after {
            text-decoration: underline;
            color: $color-light-blue;
        }
    }
}

@include scale-for-720p {
    .privacy-container {
        .privacy-heading {
            font-size: $font-size-big2;

            &:not(:first-child) {
                margin-top: 22px;
            }
        }

        .toggle {
            .toggle-text, .toggle-status {
                font-size: $font-size-medium;
            }
        }

        .privacy-text-body {
            font-size: $font-size-medium;
            .privacy-text {
                max-height: 300px;
                line-height: normal;
            }
            &::after {
                margin-top: -31px;
            }
        }
    }
}
