@import "variables.10ft";
@import "../../../styles/base";

// NOTE - these will be removed at release

// Development Tools
// -----------------------------------------------------------------------------

.dev-tools {

    .lg-tts-fix {
        visibility: hidden;
    }

    .quick-signin-container{
        margin-bottom: 20px;

        h1 {
            margin: 20px 0 10px 0;
        }

        a {
            padding: 0 10px 0 0;
            cursor: pointer;
        }
    }

    .development-toggle {
        position: fixed;
        top:0;
        left: 0;
        z-index: $index-front + 1;
        color: $color-dark-gray;
        cursor: pointer;
        background-color: $color-yellow;
        border-radius: 0 0 50px 0;
        padding: 4px 20px 6px 6px;

        &.active,
        &:hover,
        &:focus {
            .fa {
                color: $color-white;
            }
        }
        &.video-active {
            background-color: $color-red;
        }
    }

    .development-panel {
        position: absolute;
        margin-left: 20px;
        top: 20px;
        width: 80vw;
        background-color: $color-gray;
        border: 1px solid $color-off-white;
        z-index: $index-front;
        padding: 5px 15px 20px 15px;
        @include border-radius(5px);
        @include font-size(big2);

        h5 {
            @include font-size(big3);
            color: $color-white;
            margin: 0;
            margin-top: 5px;
            margin-bottom: 10px;
            clear: both;
        }

        h1 {
            @include font-size(big3);
            color: $color-off-white;
        }

        div {
            clear: both;
            display: block;
        }

        .btn {
            float: none;
            display: inline-block;
            margin: 0;
            margin-bottom: 10px;
            @include font-size(bigger2);
        }

        .btn-green {
            background-color: $color-green;
            color: $color-white;
        }
        .btn-blue {
            background-color: $color-dark-blue;
            color: $color-white;
        }
        .btn-red {
            background-color: $color-red;
            color: $color-white;
        }
        .btn-yellow {
            background-color: $color-yellow;
            color: $color-white;
        }
        .dev-toggle {
            @include font-size(big2);
            display: block;
            color: $color-off-white;
            margin-left: 50px;
            margin-bottom: 10px;
            float: left;
            width: 40%;

            &:focus,
            &:hover {
                text-decoration: none;
                cursor: pointer;
            }

            .fa {
                @include font-size(bigger);
                position: absolute;
                margin-left: -50px;
            }
            .fa-toggle-on {
                color: $color-green;
            }
            .fa-toggle-off {
                color: $color-off-white;
            }
        }
        .app-info {
            clear: left;
        }

        li[role="menuitem"] {
            cursor: pointer;
        }

        div[uib-dropdown] {
            margin-bottom: 10px;
        }

        a[uib-dropdown-toggle] {
            width: 160px;
            margin-bottom: 0;
        }

        ul[uib-dropdown-menu] {
            width: 160px;
            margin-top: 0;
        }

        .dev-tools{
            display: inline-block;
            > a {
                margin-bottom: 0;
            }

            .dropdown-menu {
                @include font-size(big2);
                text-transform: uppercase;
                min-width: 200px;
                background-color: #78bc43;

                li > a {
                    color: #ffffff;
                    &.active,
                    &:hover,
                    &:focus {
                        background-color: $color-overlay-dark-gray;
                    }
                }
            }
        }

    }

}
