@import "variables.10ft";
.privacy-modal-container {
    width: 80vw;
    .privacy-modal {
        max-height: 80vh;
        overflow-y: auto;
        white-space: pre-wrap;
    }

    .modal-header {
        .modal-header-text {
            color: $color-cool-white;
            font-size: 2.5vh;
            font-weight: 500;
            text-transform: uppercase;
            text-align: left;
        }
    }

    .modal-body {
        line-height: 1.3;
        font-size: 2.4vh;
    }
}
