@import "../../../styles/base";
@import "../../../styles/base.win10";

.channel-guide {
    background: $color-darkest-gray;
    overflow: hidden;
    padding: 0;
    margin: 0;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    min-height: 100%;

    .vertical-scroll-container:after{
        content: "";
        display: block;
        height: $vertical-scroll-padding-bottom;
    }

    .loading-spinner {
        background-color: transparent;
    }

    .guide-vertical-scroll-section{
        height: 840px;
    }

    &.scrollable{
        position: static;
    }

    &.mini-guide {
        height: 50%;
        margin-top: 50%;
    }

    .getting-started-btn{
        margin-left: $ribbon-gutter;
    }

    // inside the guide element
    .guide-inner {
        height: 100%;
        width: 100%;

        .mini-guide {
            margin-top: 0;
        }

        .channel-ribbon-container {
            background-color: $color-dark-gray;
            height: $channel-ribbon-height;
            text-align: center;
            position: relative;
            width: 100vw;
            border-bottom: 3px solid $color-near-black;

            &::-webkit-scrollbar {
                display: none;
            }

            .spinner {
                margin-top: 5px;
            }
        }

        .guide-content {
            background: $color-darkest-gray;
            width: 100%;
            padding-top: 10px;
            margin: 0;
            box-sizing: border-box;
            &.spinner-container {
                height: $ribbon-height + 30px;
                margin:0;
                position: relative;
            }

            .spinner, .loading-spinner {
                position: relative;
                background-color: transparent;
            }

            .spinner {
                top: 0;
                left: 0;
                margin-top: 0;
            }
        }

        .subs-body {
            margin-top: 160px;
            text-align: center;

            h1 {
                color: $color-white;
            }

            p {
                @include font-size(bigger);
                color: $color-off-white;
            }
        }
    }
}

@include scale-for-hd-tv {

    .channel-guide {

        .vertical-scroll-container:after{
            height: $vertical-scroll-padding-bottom-hd-tv;
        }

        .guide-vertical-scroll-section{
            height: 560px;
        }

        &.mini-guide {
            height: 50%;
            margin-top: 50%;
        }

        .getting-started-btn{
            margin-left: $ribbon-gutter-hd-tv;
        }

        // inside the guide element
        .guide-inner {
            height: 100%;
            width: 100%;

            .channel-ribbon-container {
                height: $channel-ribbon-height-hd-tv;
                border-bottom: 2px solid $color-near-black;

                .spinner {
                    margin-top: 4px;
                }

                // dynamic channel buttons in top row
                .channel-btn {
                    height: $channel-ribbon-height-hd-tv;
                    padding: 0 8px;
                    width: auto;

                    .image-wrapper {
                        height: $channel-ribbon-height-hd-tv;
                        padding: 8px 14px;
                    }

                    .image-text-container {
                        height: $channel-ribbon-height-hd-tv;
                        padding: 8px 14px;
                    }

                    .image-text {
                        width: 70px;
                        line-height: 23px;
                    }

                    &.watching {
                        border-bottom: 2px solid $color-near-black;
                    }
                }
            }

            .guide-content {
                padding-top: 7px;

                &.spinner-container {
                    height: $ribbon-height-hd-tv + 30px;
                }
            }

            .subs-body {
                margin-top: 107px;
            }
        }
    }
}
