@import "../../../styles/mixins";

// $dvr-status-bar-height is set in UI_TYPE sheets
$dvr-status-bar-radius: $dvr-status-bar-height / 2;
$dvr-status-bar-border-radius-left: $dvr-status-bar-radius 0 0 $dvr-status-bar-radius; // left edge rounded, right edge sharp
$dvr-status-bar-border-radius-all: $dvr-status-bar-radius; // left and right edges rounded
$dvr-status-bar-border-radius-none: 0; // square edges

// DVR storage space indicator component, including text title and the status bar
core-dvr-status-bar {
    display: inline-block;
    // height is set in platform specific style sheets
    position: relative;
    flex-grow: 1;

    // Title above the storage status bar
    h4 {
        color: $color-cool-white;
        white-space: nowrap;

        // '3h 15m free of 5h'
        .dvr-storage-text-summary {
            color: $color-off-white;
        }
    }

    // Shared by all the status bar layers
    .dvr-status-bar,
    .dvr-status-bar-total,
    .dvr-status-bar-used,
    .dvr-status-bar-selected {
        display: inline-block;
        height: $dvr-status-bar-height;
        position: absolute;
    }

    // Container for the bar graph DVR storage space indicator
    .dvr-status-bar {
        bottom: 0;
        width: 100%;

        // Indicates the total amount of space available (100%)
        .dvr-status-bar-total {
            background-color: $color-gray;
            border-radius: $dvr-status-bar-border-radius-all; // left and right edges rounded
            width: 100%;
        }

        // Indicates the amount of space currently used (0 - 100%)
        // width dynamically assigned in ng-style based on $ctrl.usedPercent
        .dvr-status-bar-used {
            background-color: $color-yellow;
            border-radius: $dvr-status-bar-border-radius-left; // left edge rounded, right sharp
        }

        // Rounds right corners of the used status bar when $ctrl.usedPercent value is 100%
        // Conditionally-added by the controller
        .used-one-hundred-percent {
            border-radius: $dvr-status-bar-border-radius-all;
        }

        // Indicates the amount of currently used space selected by the user by clicking
        // asset tiles. The value is expected to be between 0% and $ctrl.usedPercent. By
        // default, no corners are rounded. See following classes for trim details.
        // width dynamically assigned in ng-style based on $ctrl.selectedPercent
        .dvr-status-bar-selected {
            background: $color-off-white;
            z-index: $index-front + 3;
            border-radius: $dvr-status-bar-border-radius-none;
            // Note: the selected status indicator starts and the right and goes left
            right: 0;
        }

        // Classes conditionally-added by the controller, in this order...

        // Adds a small spacer to the left of the selected status indicator when
        // $ctrl.selectedPercent < $ctrl.usedPercent. Just a little visual separator is all.
        .selected-less-than-used {
            border-left: none;
        }

        // When $ctrl.selectedPercent >= $ctrl.usedPercent, round the left corners
        // of the selected status indicator.
        .selected-greater-than-or-equals-used {
            border-top-left-radius: $dvr-status-bar-radius;
            border-bottom-left-radius: $dvr-status-bar-radius;
        }

        // When $ctrl.usedPercent = 100, round the right corners of the selected
        // status indicator.
        .selected-used-one-hundred-percent {
            border-top-right-radius: $dvr-status-bar-radius;
            border-bottom-right-radius: $dvr-status-bar-radius;
        }

        // When no assets are selected and some storage is used, hide the selected status
        // bar so its spacer doesn't skew the display of rounded corners on the right side
        // of the used and total status bars
        .selected-zero-and-used-non-zero {
            display: none;
        }
    }

    .offscreen-text {
        border: 0;
        clip: rect(0, 0, 0, 0);
        height: 1px;
        margin: -1px;
        overflow: hidden;
        padding: 0;
        width: 1px;
        position: absolute;
    }
}
