@import "variables.10ft";
@import '../../../styles/mixins';

.grid-container {
    margin-top: 6.6vh;
}

.grid-container-3ft {
    margin: 0 auto;
    overflow-y: auto;
    overflow-x: hidden;
    padding: 10px 0;

    .grid-container-heading {
        text-align: left;
        margin-bottom: 15px;
        font-size: 20px;
        width: 100%;
    }

    @include scale-for-small-mobile {
        text-align: center;
        min-width: 320px;

        .grid-container-heading {
            text-align: center;
        }
    }
}

.row-container {
    display: flex;
    justify-content: left;
}
