@import "variables.10ft";
@import "../../../styles/base";


.concurrency-modal-container{
    .concurrency-deactivated-message{
        text-align: center;
    }
}

.deactivate-device-modal-container{
    text-align: center;
    @include font-size(big3);

    .concurrency-title{
        @include font-size(bigger2);
        color: $color-yellow;
        text-transform: uppercase;
        margin-top: 25px;
    }

    .concurrency-sub-title{
        line-height: $tv-font-size-medium + 10;
        margin: 25px 50px 0 50px;
    }

    .concurrency-device-list, .concurrency-devices-loading{
        margin-top: 25px;
    }

    .concurrency-device{
        color: $color-near-off-white;
        margin-bottom: 10px;
    }

    .concurrency-cancel{
        margin: 20px auto 0 auto;
        width: 220px;
        height:75px;
        line-height:75px;

        &.focus-grid-focused{
            @include focused-box();
            border-bottom: none;
        }
    }
}

@include scale-for-hd-tv {
    .deactivate-device-modal-container{
        .concurrency-title{
            margin-top: 16px;
        }

        .concurrency-sub-title{
            line-height: 23px;
            margin: 17px 33px 0 33px;
        }

        .concurrency-device-list, .concurrency-devices-loading{
            margin-top: 17px;
        }

        .concurrency-device{
            margin-bottom: 7px;
        }

        .concurrency-cancel {
            margin: 13px auto 0 auto;
            width: 147px;
            height:50px;
            line-height:50px;
        }
    }
}
