@import "variables.10ft";
@import '../../../styles/mixins';

core-modal-container {
    .react-modal-backdrop {
        background-color: $color-light-black;
        opacity: 0.8;
        z-index: 1040; // This should be set programmatically`
        @include position($position: fixed);
    }

    .react-modal-dialog {
        background-color: $color-black;
        border: solid $color-medium-gray;
        box-shadow: 0 10px 40px 0 rgba(0, 0, 0, 0.6);
        display: inline-block;
        position: fixed;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: 1041; // make sure to keep this lesser then uibmodals value
        min-width: 20vw;
        min-height: 20vh;
    }
}
