@import "variables.10ft";
@import "../../../../styles/base";
@import "../../../../styles/base.win10";

// Asset Item Directive Parent
// -----------------------------------------------------------------------------

.asset-item-wrapper{
    // asset that is currently in focus
    &.core-focused-item,
    &:focus,
    &:active {
        outline: none;

        .asset-item-container {
            .sub-content {
                @include focused-box();
            }
        }
    }

    // text styles for hover/focus/active tiles
    &.has-hover,
    &:focus,
    &:active,
    &.core-focused-item {
        .asset-item-container {
            .sub-content {
                .sub-content-details {

                    .sub-content-title {
                        color: $color-white;
                        text-decoration: none;

                        .dvr-icon {
                            line-height: 30px;

                            &.white {
                                color: $color-off-white;
                            }
                        }
                    }

                    span {
                        color: $color-white;
                        border-color: $color-white;
                        text-decoration: none;

                        &:before {
                            color: $color-white;
                        }
                    }

                    .sub-content-meta-data{
                        span {
                            &:after {
                                color: $color-white;
                            }

                        }
                    }

                    .new-content,
                    .free-content,
                    .on-now,
                    .expiration-date,
                    .on-now + .new-content {
                        color: $color-white;
                    }
                }
            }
        }
    }

    &.core-focused-item {
        .ribbon-button {
            background-color: $color-dark-blue;
            border-bottom: 3px solid $color-light-blue;
            color: $color-white;
            fill: $color-white;
        }
    }
}

.asset-item {
    height: $asset-item-height;
    -webkit-user-select: none;
    user-select: none;
    box-sizing: border-box;
    outline: none;
    z-index: $index-middle;
    margin-right: $asset-item-spacer;
    position: relative;

    &.empty {
        width: 40px;
        min-width: 40px;
    }

    &.has-hover {
        text-decoration: none;

        .asset-item-container {
            cursor: pointer;

            .sub-content {
                @include hovered-box();
            }
        }
    }

    // asset that is currently in focus
    &.core-focused-item,
    &:focus,
    &:active {
        outline: none;

        .asset-item-container {
            .sub-content {
                @include focused-box();
            }
        }
    }

    // text styles for hover/focus/active tiles
    &.has-hover,
    &:focus,
    &:active,
    &.core-focused-item {
        .asset-item-container {
            .sub-content {
                .sub-content-details {

                    .sub-content-title {
                        color: $color-white;
                        text-decoration: none;
                    }

                    span {
                        color: $color-white;
                        border-color: $color-white;
                        text-decoration: none;

                        &:before {
                            color: $color-white;
                        }
                    }

                    .new-content,
                    .free-content,
                    .on-now,
                    .expiration-date,
                    .on-now + .new-content {
                        color: $color-white;
                    }
                }
            }
        }
    }

    // on image load and "ready", fade in images
    &.ready {
        .asset-item-container {
            .image-wrapper {
                img {
                    transition: opacity $animation-speed ease-in;
                    opacity: 1;
                }
            }
        }
    }

    .image-overlay {
        display: none;
        background-color: $color-overlay-gray;
        position: absolute;
        height: $asset-image-height;
        width: 100%;
    }

    // locked content, overlay with lock icon
    &.locked {
        &:after {
            font-family: fontAwesome;
            content: '\f023';
            color: $color-light-gray;
            @include font-size(biggest13);
            width: 100%;
            text-align: center;
            position: absolute;
            z-index: $index-front;
            opacity: 1;
            top: 100px;
        }

        .image-overlay {
            display: block;
            z-index: $index-front;
        }

    }

    &.blank {
        background-color: $color-darkest-gray;
    }

    &.ribbon-button {
        background-color: $color-black;
        border-bottom: 3px solid $color-black;
        color: $color-light-gray;
        overflow: hidden;
        text-align: center;
        text-transform: uppercase;
        width: $asset-item-width * 0.4;
        fill: $color-light-gray;
        padding-top: 110px;

        svg {
            width: 60px;
            height: 60px;
            display: block;
            margin: 0 auto;
        }

        &.has-hover {
            background-color: $color-gray;
            border-bottom: 3px solid $color-white;
            color: $color-white;
            fill: $color-white;
        }

        &.core-focused-item {
            background-color: $color-dark-blue;
            border-bottom: 3px solid $color-light-blue;
            color: $color-white;
            fill: $color-white;
        }
    }

    &.channel-tile {
        width: $asset-item-width;
        height: $asset-image-height;
        line-height: $asset-image-height;
        background-color: $color-black;
        border: solid 3px #444444;
        color: $color-light-gray;
        text-align: center;

        &.ribbon-button{
            width: 124px;
            padding-top: 62px;
            line-height: 30px;
            float: left; // Added this to vertically align 'add' button in the mychannels ribbon for SAMSUNG Platform
        }

        .channel-tile-inner {
            align-items: center;
            display: flex;
            height: 100%;
            justify-content: center;
            width: 100%;

            img {
                max-width: $asset-item-my-channel-image-max-width;
                max-height: $asset-item-my-channel-image-max-height;
                opacity: 0.5;
                display: inline;
            }

            h3 {
                opacity: 0.5;
            }

            .selected-channel {
                opacity: 1;
            }
        }

        h3 {
            @include font-size(biggest);
            font-weight: 700;
            padding: 0 20px;
            text-align: center;
        }

        &.has-hover {
            background-color: $color-win10-gray;
            border: 3px solid $color-white;
            color: $color-white;
            img,
            h3 {
                opacity: 1;
            }

            &.edit-channels-tile {
                border: 3px solid $color-white;
            }
        }

        &.core-focused-item {
            background-color: $color-dark-blue;
            border: 3px solid $color-light-blue;
            color: $color-white;
            img,
            h3 {
                opacity: 1;
            }
        }
    }

    .asset-item-container {
        box-sizing: border-box;
        position: relative;
        text-decoration: none;
        text-align: center;
        height: 100%;
        width: 100%;
        z-index: $index-middle;

        // missing image icon is always there, if image loads, it stacks on top
        .image-placeholder {
            color: $color-off-white;
            height: $asset-image-height;
            width: 100%;
            @include font-size(biggest12);
            text-align: center;
            position: absolute;
            z-index: $index-front;
            background-color: $color-gray;

            .dish-icon-tv, .dish-icon-user {
                margin-top: 40px;
                display: block;
            }

            .search-icon {
                position: relative;
                height: 100%;
                width: 100%;
                display: block;
            }

            // FIX for UI-13103 - these are images instead of FA icons because of an LG TV rendering issue
            .search-recent {
                background: url("../../../../assets/images/search_recent.png") no-repeat center;
                background-size: 70px;
            }
            .search-person {
                background: url("../../../../assets/images/search_recent.png") no-repeat center;
                background-size: 70px;
            }
            .search-clear {
                background: url("../../../../assets/images/search_clear.png") no-repeat center;
                background-size: 70px;
            }

        }

        // the image area
        .image-wrapper {
            position: relative;
            box-sizing: border-box;
            text-decoration: none;
            height: $asset-image-height;
            width: $asset-item-width;
            text-align: center;
            overflow: hidden;
            z-index: $index-middle + 2;
            img {
                height: 100%;
                z-index: $index-middle + 3;
                opacity: 0;
            }

            &.person-image{
                min-width: $asset-item-person-min-width;
            }
        }

        // if no image loads, hide the broken image tag ("missing" icon is already there behind it)
        .no-image-asset {
            img {
                display: none;
            }
        }

        // for showing resume point progress bar
        .progress-line {
            height: $asset-item-height * 0.025;
            background-color: $color-dark-gray;
            width: 100%;
            position: absolute;
            z-index: $index-middle + 4; // 1 layer above .image-wrapper img which is defined above
            margin-top: -$asset-item-height * 0.025;

            .current-progress-line {
                height: 100%;
                width: 25%; // this will be dynamically changed based on resume points
                background-color: $color-yellow;
                position: absolute;
            }
        }

        // the "details" of an asset below the image
        .sub-content {
            position: relative;
            height: $asset-subcontent-height;
            width: 100%;
            background-color: $color-black;
            border-bottom: 3px solid $color-black;
            overflow: hidden;

            .sub-content-inner {
                padding: 0 15px;
                width: 100%;
                position: absolute; // This might seem strange, but this allows for the image to dictate the asset container width rather than the meta data
            }

            .sub-content-details {
                @include font-size(big2);
                width: 100%;
                text-align: left;
                color: $color-off-white;
                padding: 0;
                overflow: hidden;
                height: $asset-subcontent-height;

                .sub-content-meta-data, .sub-content-title{
                    @include ellipsis-able();
                }

                .sub-content-title {
                    color: $color-near-off-white;
                    @include font-size(bigger);
                    width: 100%;
                    text-align: left;
                    font-weight: 500;
                    margin: 10px 0 5px 0;
                    display: block;
                    padding: 0;
                    line-height: normal;
                }

                .sub-content-meta-data{
                    span {
                        &:after {
                            content: "|";
                            color: $color-light-gray;
                            padding-left: 6px;
                            margin: 0;
                        }

                        &:last-child:after{
                            content: "";
                            padding:0;
                        }
                    }
                }

                .new-content,
                .free-content,
                .on-now {
                    color: $color-yellow;
                }

                .on-now + .new-content {
                    color: $color-light-gray;
                }

                .expiration-date {
                    color: $color-yellow;
                }

                .episode-count {
                    width: auto;
                    white-space: nowrap;
                }
            }
        }
    }

    &.is-person-item{
        .sub-content-details{
            .sub-content-title{
                margin-bottom:0;
                &.actor-first-name{
                    margin: 10px 0 0 0;
                }
                &.actor-last-name{
                    margin: 0;
                }
            }
        }
    }

    &.clear-item{
        .asset-item-container{
            width: 150px;
            background: black;
            padding-top: 85px;
        }
        .sub-content-title{
            text-align: center;
            text-transform: uppercase;
            color: $color-light-gray;
            @include font-size(big3);
            padding-top: 10px;
        }
        .icon-close {
            width: 80px;
            height: 80px;
            display: block;
            margin:0 auto;
            border: 2px solid $color-light-gray;
            border-radius: 80px;
            &:before{
                font-size: 40px;
                position: relative;
                top: 20px;
                color: $color-light-gray;
            }
        }
        &.core-focused-item{
            .asset-item-container{
                @include focused-box();
            }
            .sub-content-title{
                color: $color-white;
            }
            .icon-close {
                border-color: $color-white;
                &:before {
                    color: $color-white;
                }
            }
        }
        &.has-hover{
            .icon-close{
                border: 2px solid $color-near-off-white;
                &:before{
                    color: $color-near-off-white;
                }
            }
            .sub-content-title{
                color: $color-near-off-white;
            }
            .asset-item-container{
                background: #383838;
            }
        }
    }
}

.recommendation-row {
    padding-top: 10px;
}

@include scale-for-hd-tv {

    .asset-item-wrapper{

        // text styles for hover/focus/active tiles
        &.has-hover,
        &:focus,
        &:active,
        &.core-focused-item {
            .asset-item-container {
                .sub-content {
                    .sub-content-details {

                        .sub-content-title {

                            .dvr-icon {
                                line-height: 20px;
                            }
                        }
                    }
                }
            }
        }

        &.core-focused-item {
            .ribbon-button {
                border-bottom: 2px solid $color-light-blue;
            }
        }
    }

    .asset-item {
        height: $asset-item-height-hd-tv;
        width: $asset-item-width-hd-tv;
        margin-right: $asset-item-spacer-hd-tv;

        &.empty {
            width: 27px;
            min-width: 27px;
        }

        .image-overlay {
            height: $asset-image-height-hd-tv;
        }

        // locked content, overlay with lock icon
        &.locked {
            &:after {
                top: 67px;
            }
        }

        &.ribbon-button {
            border-bottom: 2px solid $color-black;
            width: $asset-item-width-hd-tv * 0.4;
            padding-top: 74px;

            svg {
                width: 40px;
                height: 40px;
            }

            &.has-hover {
                border-bottom: 2px solid $color-white;
            }

            &.core-focused-item {
                border-bottom: 2px solid $color-light-blue;
            }
        }


        &.channel-tile {
            width: $asset-item-width-hd-tv;
            height: $asset-image-height-hd-tv;
            line-height: $asset-image-height-hd-tv;
            border: solid 2px #444444;
            font-size: 17px;

            &.ribbon-button{
                width: 83px;
                padding-top: 42px;
                line-height: 20px;
            }

            .channel-tile-inner {

                img {
                    max-width: $asset-item-my-channel-image-max-width-hd-tv;
                    max-height: $asset-item-my-channel-image-max-height-hd-tv;
                }
            }

            h3 {
                padding: 0 14px;
            }

            &.has-hover {
                border: 2px solid $color-white;

                &.edit-channels-tile {
                    border: 2px solid $color-white;
                }
            }

            &.core-focused-item {
                border: 2px solid $color-light-blue;
            }
        }

        .asset-item-container {

            .image-placeholder {
                height: $asset-image-height-hd-tv;

                .dish-icon-tv, .dish-icon-user {
                    margin-top: 27px;
                }

                .search-recent {
                    background-size: 47px;
                }
                .search-person {
                    background-size: 47px;
                }
                .search-clear {
                    background-size: 47px;
                }
            }

            // the image area
            .image-wrapper {
                height: $asset-image-height-hd-tv;
                width: $asset-item-width-hd-tv;

                &.person-image{
                    min-width: $asset-item-person-min-width-hd-tv;
                }
            }

            // for showing resume point progress bar
            .progress-line {
                height: $asset-item-height-hd-tv * 0.025;
                margin-top: -$asset-item-height-hd-tv * 0.025;
            }

            // for showing resume point progress bar
            .progress-line {
                height: $asset-item-height-hd-tv * 0.025;
                margin-top: -$asset-item-height-hd-tv * 0.025;
            }

            // the "details" of an asset below the image
            .sub-content {
                height: $asset-subcontent-height-hd-tv;
                width: 100%;
                border-bottom: 2px solid $color-black;

                .sub-content-inner {
                    padding: 0 13px;
                }

                .sub-content-details {
                    height: $asset-subcontent-height-hd-tv;

                    .sub-content-title {
                        margin: 7px 0 2px 0;
                    }

                    .sub-content-meta-data{
                        span {
                            &:after {
                                content: "|";
                                color: $color-light-gray;
                                padding-left: 4px;
                                margin: 0;
                            }

                            &:last-child:after{
                                content: "";
                                padding:0;
                            }
                        }
                    }
                }
            }
        }

        &.is-person-item{
            .sub-content-details{
                .sub-content-title{
                    margin-bottom:0;
                    &.actor-first-name{
                        margin: 7px 0 0 0;
                    }
                    &.actor-last-name{
                        margin: 0;
                    }
                }
            }
        }

        &.clear-item{
            .asset-item-container{
                width: 100px;
                padding-top: 57px;
            }

            .sub-content-title{
                padding-top: 7px;
            }

            .icon-close {
                width: 54px;
                height: 54px;
                border: 1px solid $color-light-gray;
                border-radius: 54px;
                &:before{
                    font-size: 27px;
                    top: 14px;
                }
            }

            &.has-hover {
                .icon-close {
                    border: 1px solid $color-near-off-white;
                }
            }
        }
    }

    .recommendation-row {
        padding-top: 7px;
    }
}
