@import '../iap.variables';
@import '../../../styles/mixins';

.iap-base-packs {
    display: flex;
    height: 100%;

    .load-indicator{
        width: 100%;
        height: 100%;
    }

    .btn {
        background-color: black;
        line-height: 1.42;
    }

    .vertical-scroll-container {
        background: none;
    }

    .packs-view {
        width: $packs-column-width;
        height: 100%;
        flex: 0 0 $packs-column-width;
        border-right: solid 0.1vmin $color-shadow-gray;

        .pack-list {
            overflow-y: auto;
            position: relative;
            height: 100%;

            .pack {
                height: 14.51vh;
                padding: 0.92vh 0 0 3.1vw;
                color: $color-off-white;
                font-size: 2.4vmin;
                border-bottom: solid 0.15vmin $color-shadow-gray;
                line-height: 2.5vh;

                .toggle-add {
                    &:hover {
                        background: $color-blue;
                    }
                }

                &:hover {
                    color: $color-white;
                    background: $color-shadow-gray;
                }

                &.core-focused-item {
                    color: $color-white;
                    background: $color-shadow-gray;

                    .toggle-add {
                        background: $color-blue;
                    }
                }

                &.core-focused-item {
                    /* This is the gray arrow to the right of the active pack */
                    &:after {
                        content: '';
                        position: absolute;
                        top: 5vh;
                        right: -1.25vw;
                        width: 0;
                        height: 0;
                        border-top: $active-pack-indicator-width solid transparent;
                        border-bottom: $active-pack-indicator-width solid transparent;
                        border-left: $active-pack-indicator-width solid $color-shadow-gray;
                    }
                }

                .pack-name {
                    width: 20.05vw;
                    font-size: 3vmin;
                    line-height: normal;
                    font-weight: bold;
                    margin: 1vmin 0;
                    @include ellipsis-able();
                }

                .pack-price {
                    font-size: 1.57vmin;
                    font-weight: 700;
                }

                .amount {
                    font-size: 2.3vmin;
                }

                .per-month {
                    font-size: 2.3vmin;
                }

                .amount, .per-month {
                    font-weight: normal;
                }

                .pack-toggle {
                    .info {
                        display: none;
                    }

                    position: absolute;
                    right: 2.08vw;
                    margin-top: -6.55vh;

                    .toggle-add {
                        text-transform: uppercase;
                        margin-top: 0;
                        min-width: 5.3vw;
                    }

                    .toggle-remove {
                        color: $color-green;
                        font-size: 3vw;
                        margin-top: 1vh;
                        margin-right: 1vw;
                    }
                }
            }

            .view-more-button-wrapper {
                text-align: center;
                height: $pack-row-height;
                text-decoration: none;
                color: $color-white;

                .view-more-button {
                    margin: 4.62vh 0;
                }

                &.core-focused-item {
                    .view-more-button {
                        background-color: $color-blue;
                    }
                }
            }

            .pack, .view-more-button-wrapper {
                position: relative;
            }

            .border-fill {
                width: $packs-column-width - $active-pack-indicator-width;
                position: fixed;
                top: $iap-top-info-bar-height;
                bottom: $iap-bottom-info-bar-height;
            }
        }
    }

    .pack-details {
        height: 9.25vh;

        .pack-description {
            font-size: 1.94vmin;
            line-height: normal;
            color: $color-off-white;
            margin: 2.31vmin 0.92vmin;
        }

        .pack-channels {
            .pack-channel-page {
                display: flex;
                flex-wrap: wrap;

                .pack-channel-image {
                    width: $iap-pack-image-width;
                    height: $iap-pack-image-height;
                    margin: 1.9vmin 4.9vmin;
                    background-size: contain;
                    background-position: center;
                    background-repeat: no-repeat;
                }

                .pack-channel-text{
                    width: $iap-pack-image-width;
                    height: $iap-pack-image-height;
                    padding: 1.85vmin;
                    margin: 0.92vmin;
                    text-align: center;
                }
            }
        }

        .page-indicator{
            width: $details-column-width;
            display: flex;
            position: absolute;
            bottom: 0;
            justify-content: center;

            .indicator-item{
                width: 2.22vh;
                height: 2.22vh;
                border-radius: 50%;
                background: $color-light-gray;
                margin: 0.5vmin;

                &.active{
                    background: $color-white;
                }
            }
        }
    }
}

.modal-base-packs .core-modal {
    width: 46.87vw;

    .modal-title {
        color: $color-white;
        text-transform: none;
        text-align: left;
        line-height: normal;
        font-weight: 300;
    }

    .modal-footer {
        margin-left: 13vw;
    }

    button {
        text-transform: uppercase;
    }
}
