@import "variables.10ft";
@import './style';
@import '../../styles/mixins';

// Focus styles - This covers the case when the focus class is applied the parent of the component, or on the component element itself
.core-focused-item core-toggle, core-toggle.core-focused-item {
    .toggle-wrapper {
        border-color: $color-light-blue;
    }
}

core-toggle {
    line-height: 27px;
}

@include scale-for-720p {
    core-toggle {
        line-height: 22px;

        &.disabled {
            .toggle-wrapper {
                &:hover {
                    border: 1px solid transparent;
                }
                &:focus {
                    border: 1px solid transparent;
                }
            }
        }

        .toggle-switch {
            height: 17px;
            width: 40px;
            border: 1px solid $color-off-white;
            border-radius: 33px;
            left: 4px;
            top: 3px;

            .slider {
                height: 10px;
                top: 2px;
                left: 1px;
                border-radius: 33px;
                width: 10px;
            }

            &.on {
                .slider {
                    left: 28px;
                    top: 3px;
                }
            }
        }

        .toggle-wrapper {
            border: 1px solid transparent;
            border-radius: 67px;
            height: 26px;
            width: 50px;

            &:hover {
                border: 1px solid $color-white;
            }
            &:focus {
                border: 1px solid $color-light-blue;
            }
        }
    }
}
