// Guide component variables shared by lower-level components on the 10ft/LG platform

// LG/10ft dimensions used for adjustments
$grid-guide-ruler-height-desktop: 42px;
$channel-header-height-desktop: 159px - $grid-guide-ruler-height-desktop;

// Channel ribbon for the channel guide
$channel-ribbon-height: 90px;

// Channel ribbon logo heights for channel guide
$channel-ribbon-logo-height: 72px;

$guide-header-component-font-size: 30px;

// 10ft variables for HD screen(1280x720)
$guide-header-component-font-size-720p: 20px;
$grid-guide-ruler-height-desktop-720p: 28px;

$channel-header-height-desktop-720p: 106px - $grid-guide-ruler-height-desktop-720p;

