@import "variables.10ft";
@import "../../../styles/base";
@import "../../../styles/base.win10.scss";

.spinner,
.generic-spinner,
.button-spinner {
    position: fixed;
    top: 50%;
    left: 50%;
    margin-top: -41px;
    margin-left: -41px;
    background: url("../../../assets/images/spinner.png") no-repeat;
    width: 83px;
    height: 83px;
    -webkit-animation: spinner 0.75s infinite linear;
    animation: spinner 0.75s infinite linear;
    z-index: 1;
}

.generic-spinner {
    margin-top: 0;
    top: 60%;
}

@-webkit-keyframes spinner {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(359deg);
        transform: rotate(359deg);
    }
}

@keyframes spinner {
  0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(359deg);
        transform: rotate(359deg);
    }
}

.loading-spinner {
    background-color: $color-overlay-black;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
}

.spinner-container {
    @include fluid-bg-img();
    background-color: $color-black;
    z-index: $index-front;
    text-align: center;
    position: fixed;
    margin-top: $top-nav-height;

    &.full-screen{
        margin-top: 0;
    }

    &.transparent{
        background-color: $color-overlay-invisible;
    }
}

#spinner-container-resolve {
    .spinner-container {
        visibility:hidden;
    }
}

.platform-win10 {
    .spinner-container {
        margin-left: 60px;
        margin-top: 60px;

        &.full-screen{
            margin: 0;
        }

        &.transparent{
            background-color: $color-overlay-invisible;
        }
    }
}

.platform-browser {
    .spinner-container {
        margin-top: $top-nav-height-browser;
    }
}

@include scale-for-tv {
    .spinner-container {
        margin-top: $top-nav-height-tv;
    }
}

@include scale-for-hd-tv {
    .spinner-container {
        margin-top: $top-nav-height-hd-tv;
    }

    .spinner,
    .generic-spinner,
    .button-spinner {
        height: 54px;
        width: 54px;
        background-size: cover;
        margin-top: -27px;
        margin-left: -27px;
    }

   .generic-spinner {
      margin-top: 0;
   }
}
