@import "variables.10ft";
@import "../../../styles/base";

.top-menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: $top-menu-height;
    z-index: $index-front;
    overflow: hidden;
    background-color: $color-overlay-gray;
    text-align: left;
    padding-left: $top-menu-left-padding;

    .channel-image {
        margin: 0 40px 0 0;
        height: 80px;
        vertical-align: middle;
    }

    .show-info {
        font-weight: bold;
        @include font-size(biggest);
        color: $color-yellow;
        line-height: $top-menu-height;

        .icon-rec-single {
            margin-top: 5px;
            margin-right: 10px;
            color: $color-red;
        }

        .icon-rec-multiple {
            margin-top: 5px;
            margin-right: 10px;
            color: $color-red;
        }

        .show-episode{
            font-weight: normal;
            color: #878787;
        }
    }

    .help-arrow{
        @include font-size(big3);
        float:right;
        margin-right: 102px;
        color: $color-off-white;
        line-height: $top-menu-height;
        padding:0;
        text-transform: uppercase;
        .fa{
            @include font-size(biggest2);
        }
    }

    &:after{
        clear:both;
    }
}

@include scale-for-hd-tv {
    .top-menu {
        height: $top-menu-height-hd-tv;
        padding-left: 70px;

        .channel-image {
            height: 53px;
            margin: 0 20px 0 0;
        }

        .show-info {
            line-height: $top-menu-height-hd-tv;
        }

        .help-arrow{
            line-height: $top-menu-height-hd-tv;
        }
    }
}
