@import "variables.10ft";
@import '../../../styles/mixins';

.cookies-banner {
    width: 100vw;
    min-height: 64px;
    height: auto;
    background: $color-medium-black;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 5px 30px;
    line-height: 21px;

    .cookies-banner-text {
        font-weight: 300;
        color: $color-cool-white;

        a {
            color: $color-blue; //todo: confirm that we are deviating from standard (button should be grey until focused)
        }
    }

    .banner-button {
        margin: 0 0 0 30px;
        background: $color-dark-blue;
    }

    @include scale-for-responsive {
        padding: 5px 20px 5px 65px;

        .cookies-banner-text {
            font-size: 14px;


        }
    }

    @include scale-for-mobile {
        flex-direction: column;

        .cookies-banner-text {
            text-align: center;
        }

        .banner-button {
            margin: 12px 0 6px;
        }
    }

    @include scale-for-small-mobile {
        padding-left: 20px;
    }
}