@import "../../../styles/variables";

core-iap-account {
    .iap-account-container {
        margin: 5.5vh 5.2vw;
        display: block;
        width: 33.33vw;

        form {
            .form-group{
                margin-bottom: 1.9vh;
                outline: none;
                border: none;
                padding: 0;
            }

            input{
                display: block;
                font-size: 26px;
                color: $color-white;
                padding-left: 6px;
                font-weight: 600;
                width: 100%;
                height: 5.5vh;
                box-shadow: none;
                padding-right: 5px;
                background-color: $color-medium-gray;
                margin: 0;
                border: 6px solid $color-medium-gray;
                border-radius: 0;
                outline: none;

                &.core-focused-item {
                    border-color: $color-dark-blue;
                }

                &.ng-invalid.ng-dirty {
                    border-color: $color-red;
                }
            }

            .coreValidationToolTip {
                margin-top: 10px;
            }
        }
    }
}

