@import "variables.10ft";
@import '../../ribbon-system/ribbon-system.variables.10ft';
@import './style';
@import '../../../styles/mixins';

core-dvr-container-header {
    font-size: 30px;
    line-height: 40px;
    padding: 40px $ribbon-left-gutter;

    // The individual headings
    span {
        font-size: 30px;

        // The current page
        &.core-breadcrumb-current {
            color: $color-yellow;
            font-weight: 500;
        }

        // Breadcrumb chevrons with a bit of padding
        i {
            font-size: 19px;
            padding: 0 10px;
            position: relative;
            top: -2px;
        }
    }
}

@include scale-for-720p {
    core-dvr-container-header {
        font-size: 20px;
        line-height: 27px;
        padding: 27px $ribbon-left-gutter-720p;

        // The individual headings
        span {
            font-size: 20px;

            // Breadcrumb chevrons with a bit of padding
            i.fa {
                font-size: 13px;
                padding: 0 7px;
                top: -1px;
            }
        }
    }
}
