@import "variables.10ft";
@import "../../../styles/base";
@import "../../modals/modals";

.no-connection-wrapper {
    width: 850px;
    text-align: center;

    .modal-body {
        color: $color-white;
        font-size: $tv-font-size-big;

        .alert-icon {
            color: $color-red;
            font-size: 35px;
        }

        .btn {
            margin-top: 25px;
        }
    }
}

@include scale-for-hd-tv {
    .no-connection-wrapper {
        width: 675px;

        .modal-body {
            font-size: $tv-font-size-medium;
        }
    }
}
