@import "variables.10ft";
@import '../../../../styles/mixins.scss';
@import '../../../../styles/variables.scss';

.search-container {
    .search-keypad-container {
        transition: all 400ms;
        overflow: hidden;
        float: left;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        line-height: 27px;
    }

    .key {
        height: 5.1vh;
        line-height: 5.1vh;
        font-size: 3.7vh;
        font-weight: 300;
        text-align: center;
        outline: none;
        border-radius: .4vh;
        cursor: pointer;

        &.core-focused-item {
            @include focused-box();
        }

        & svg {
            width: 80%;
            height: 100%;
        }

        &.character-key {
            width: 16.66%;
            color: $color-white;

            .text {
                width: 100%;
                height: 100%;
            }
        }

        &.space-key {
            width: 33.3%;
            padding: 0.4vh 0.6vw;
            font-size: 2.6vh;
            line-height: 4.5vh;
            color: $color-white;
        }

        &.back-space-key {
            width: 33.3%;
            padding: 0.4vh 0.6vw;
            background-color: transparent;
        }

        &.clear-key {
            width: 33.3%;
            padding: 0.4vh 0.6vw;
            font-size: 2.6vh;
            line-height: 4.5vh;
            color: $color-white;
        }
    }
}
