core-grid-schedule-row {
    display: block;
    position: absolute;
    left: $grid-guide-channel-width;
    .grid_guide__schedule-row {
        position: relative;
        // value for $grid-guide-row-height is imported in platform specific sheets
        height: $grid-guide-row-height;

        //TODO: WEB-2866 this is just placeholder to know that the channel has no schedule
        .no-linear-schedule {
            box-shadow: -1px -1px 0 1px $color-medium-gray;
            height: 100%;
            // value for $grid-guide-item-padding is imported in platform specific sheets
            padding: $grid-guide-item-padding;
            width: 100%;
        }

        // Text displayed over other content if the controller is loading the schedule row.
        // Note the loading-text span should appear above any other content in the template.
        .loading-text {
            position: absolute;
            top: 0;
            left: 0;
            box-shadow: -1px -1px 0 1px $color-medium-gray;
            width: 200%;
            height: 100%;
            z-index: $index-front;
            padding: $grid-guide-item-padding;
            color: $color-light-gray;
            background-color: $color-light-black;
            flex: 0 0 auto;
        }

        // Styles shared by both left and right row edge gradients
        .shared-gradient {
            position: absolute;
            display: block;
            width: 120px; // important must match GRID_GUIDE.SCHEDULE_ROW_GRADIENT_WIDTH
            height: 100%;
            z-index: $index-front;
            pointer-events: none;
            opacity: 1;
        }

        // Conditionally-visible left side gradient. See the controller for the positioning computation.
        .schedule-left-gradient {
            background: linear-gradient(90deg, rgba(28,28,28,0.7) 0%, rgba(28,28,28,0) 100%);
            transition-property: opacity;

            // ng-show animation classes for the left gradient edge
            &.ng-hide {
                opacity: 0;
            }
            &.ng-hide-add,
            &.ng-hide-remove {
                transition: opacity linear 0.4s;
            }
        }

        // Conditionally-visible right side gradient. See the controller for the positioning computation.
        // Note no animation on this element as it looked funny on fast vertical scrolling.
        .schedule-right-gradient {
            background: linear-gradient(90deg, rgba(28,28,28,0) 0%, rgba(28,28,28,0.8) 100%);
        }

        .error {
            padding: 25px 13px;
        }
    }
}
