@import "variables.10ft";
@import '../details.variables.10ft';
@import './style';
@import '../../../styles/mixins';

core-details-container {
    .details {
        width: 960px;
        &.full-width {
            width: 100vw;
        }
    }

    .details-content {
        padding-top: 10px;

        .drawer-details .meta-data {
            font-size: $details-font-size;
            margin-top: 10px;
        }

        .description {
            font-size: $details-font-size;
            line-height: normal;
            margin: 40px 0 25px 0;
        }
    }

    .banner-image {
        height: 310px;
    }

    hr {
        height: 2px;
        margin-top: 20px;
    }

    core-scrolling-ribbon .ribbon-title {
        padding-left: 0;
    }

    core-program-details, core-series-details {
        height: 100%;
        width: 960px;
        position: fixed;
        overflow-y: scroll;
    }
}

@include scale-for-720p {
    core-details-container {

        .details {
            width: 640px;
        }

        .vertical-scroll-container .details-content {
            padding: 7px $details-side-gutter-720p 0 $details-side-gutter-720p;

            section {
                margin-bottom: 13px;
            }

            .core-ui-ribbon-content {
                padding-left: 0;
            }

            .drawer-details .meta-data {
                font-size: $details-font-size-720p;
                margin-top: 7px;
            }

            .description {
                font-size: $details-font-size-720p;
                margin: 27px 0 17px 0;
            }

            .btn,
            &.focus-grid-focused {
                font-size: 19px;
                min-width: 120px;
                height: 50px;
                padding: 12px 20px;

                &.no-min-width {
                    min-width: 0;
                }
            }

            .details-actions .btn,
            &.focus-grid-focused{
                padding: 12px 16px;
            }
        }

        .banner-image {
            height: 207px;
        }

        hr {
            height: 1px;
            margin-top: 13px;
        }

        .details-content section core-scrolling-ribbon .ribbon-title {
            padding-left: 0;
        }

        core-program-details, core-series-details {
            width: 640px;
        }
    }
}
