@import "base";


// Font Size Restrictions (min/max)
$font-size-map-min-win10: (
    smallest:   12px,
    smaller:    13px,
    small:      14px,
    medium:     15px,
    medium2:    16px,
    big:        16px,
    big2:       17px,
    bigger:     20px,
    bigger2:    24px,
    biggest:    24px
);

$font-size-map-win10: (
    smallest:   15px,
    smaller:    16px,
    small:      16px,
    medium:     18px,
    medium2:    18px,
    big:        20px,
    big2:       20px,
    bigger:     26px,
    bigger2:    28px,
    biggest:    32px
);

$win10-guide-container-inner-top-margin: 88px;
$win10-top-nav-logo-padding: 16px;

// Win10 Colors
$color-win10-off-white:     #cecece;
$color-win10-light-gray:    #505050;
$color-win10-gray:          #383838;
$color-win10-dark-gray:     #323232;
$color-win10-off-black:     #2f2f2f;
$color-win10-light-black:   #1c1c1c;
$color-win10-full-black:    #000000;
$color-win10-blue:          #384b77;
$color-win10-green:         #4E8054;
$color-win10-red:           #571E1F;
$color-win10-orange:        #ffab15;
$color-win10-cool-gray:     #2b2b2b;
$color-win10-light-white:   #ebebeb;

// Win10 Overlay Colors
$color-win10-overlay-black:       rgba(15, 15, 15, 0.9);
$color-win10-overlay-gray:        rgba(28, 28, 28, 0.8);
$color-win10-search-overlay-gray: rgb(28, 28, 28);

// ==== COMPONENT-BASED VARIABLES ==================================

// Buttons
$button-win10-short-width: 100px;

//z-index
$index-side-nav-win10: 5;
$index-side-nav-overlay-win10: 4;

// ==== UTILITY MIXINS ==================================

@mixin formLabels {
    @include font-size(big);
    display: block;
    color: $color-warm-gray;
}

// win10 gradient inside shadows on the bottom row
@mixin win10-bottom-shadow {
    -moz-box-shadow:    inset 0 -20px 30px -30px $color-black;
    -webkit-box-shadow: inset 0 -20px 30px -30px $color-black;
    box-shadow:         inset 0 -20px 30px -30px $color-black;
}

@mixin win10-drawer-shadow {
    -webkit-box-shadow: -2px 0 8px 0 rgba(0, 0, 0, 0.4);
    -moz-box-shadow: -2px 0 8px 0 rgba(0, 0, 0, 0.4);
    box-shadow: -2px 0 8px 0 rgba(0, 0, 0, 0.4);
}

// Outputs responsive font size
@mixin font-size-win10($size) {
    @if map_has_key($font-size-map-win10, $size) {
        @media (max-width: 1007px) {
            $font-size: map_get($font-size-map-min-win10, $size);
            font-size: $font-size;
        }
        @media (min-width: 1008px) {
            $font-size: map_get($font-size-map-win10, $size);
            font-size: $font-size;
        }
    }
}

// responsive break points for devices (note - this will need to be more granular in the future)
@mixin respond-to($breakpoint) {
    @if $breakpoint == "mobile" {
        @media (max-width: 500px) {
            @content;
        }
    }
    @if $breakpoint == "tablet" {
        @media (max-width: 1007px) {
            @content;
        }
    }
    @if $breakpoint == "desktop" {
        @media (min-width: 1008px) {
            @content;
        }
    }
}
