@import "variables.10ft";
@import './style';
@import 'guide-container.variables.10ft';
@import '../../styles/mixins.scss';

// LG/10ft style overrides for the guide container
core-guide-container {
    height: 87vh;

    .channel-header {
        height: $channel-header-height-desktop;
        padding: 20px 100px;
        position: relative;
    }

    .guide-content {
        top: 0;
        height: calc(100% - #{$channel-header-height-desktop});
    }
}

@include scale-for-720p {
    core-guide-container {
        .channel-header {
            height: $channel-header-height-desktop-720p;
            padding: 13px 67px;
        }

        .guide-content {
            top: $channel-header-height-desktop-720p;
            height: calc(100% - #{$channel-header-height-desktop-720p});
        }
    }
}
