@import "variables.10ft";
@import './style';
@import '../../guide-container.variables.10ft';
@import '../grid-guide.variables.10ft';
@import '../../../../styles/mixins';

// LG/10ft style overrides for the grid guide ruler
core-grid-guide-ruler {
    height: $grid-guide-ruler-height-desktop;   // set in variables.10ft

    div.ruler-unit {
        height: $grid-guide-ruler-height-desktop;
        font-size: 22px;
        color: $color-off-white;
    }
}

@include scale-for-720p {
    core-grid-guide-ruler {
        height: $grid-guide-ruler-height-desktop-720p;   // set in variables.10ft

        div.ruler-unit {
            height: $grid-guide-ruler-height-desktop-720p;
            font-size: 15px;
            padding-left: 3px;
        }

        div.current-time {
            height: 3px;

            &:after {
                height: 7px;
                width: 7px;
                border-radius: 7px;
                top: -2px;
            }
        }
    }
}