//This style file is for HD 1280x720 screen

// for HD screen
$font-size-map-hd-screen: (
        smallest:   10px,
        smaller:    11px,
        small:      11px,
        medium:     12px,
        big:        14px,
        big2:       15px,
        big3:       16px,
        bigger:     17px,
        bigger2:    19px,
        biggest:    20px,
        biggest2:   22px,
        biggest3:   27px,
        biggest4:   35px,
        biggest5:   38px,
        biggest6:   40px,
        biggest8:   53px,
        biggest12:  80px,
        biggest13:  87px
);


// HD TV models specific styling(needs to be called in root/global level of the cascade)
// Removing HD styles for Win10 since HD specific styles were causing UI alignment issues in Win10
@mixin scale-for-hd-tv() {
    @media (max-width: 1280px) {
        html:not(.platform-win10) {
            @content;
        }
    }
}

// TV Variables
$tv-font-size-medium-hd-tv: 16px;
$tv-font-size-big-hd-tv: 19px;
$tv-font-size-bigger-hd-tv: 22px;
$tv-font-size-biggest-hd-tv: 27px;

// Dashboard
$top-nav-height-hd-tv: 94px;
$top-menu-height-hd-tv: 94px;
$top-bar-margin-top-hd-tv: 30px;
$dashboard-page-height-hd-tv: 623px;

// Asset Items
$asset-image-height-hd-tv: 143px;
$asset-subcontent-height-hd-tv: 60px;
$asset-item-height-hd-tv: $asset-image-height-hd-tv + $asset-subcontent-height-hd-tv;
$asset-item-spacer-hd-tv: 14px;
$asset-item-person-min-width-hd-tv: 100px;
$asset-item-my-channel-image-max-width-hd-tv: 147px;
$asset-item-my-channel-image-max-height-hd-tv: 60px;
$asset-item-width-hd-tv: 254px;
$asset-item-min-width-hd-tv: 188px;
$asset-item-max-width-hd-tv: 534px;
$asset-item-my-channel-height-hd-tv: 106px;
$asset-item-channel-spacer-hd-tv: 16px;
$asset-item-channel-check-offset-hd-tv: 7px;
$asset-item-channel-check-width-hd-tv: 16px;
