
core-upgrade-details {
    .upgrade-content {
        height: 100%;
        width: 100%;
        background-repeat: no-repeat;
        background-position: center top;
        background-size: 100% 100%;

        a {
            color: white;
        }

        .upgrade-title {
            color: $color-white;
        }

        .upgrade-text-medium {
            color: $color-white;
        }

        .upgrade-details-left {
            display: inline-block;
        }

        .upgrade-details-right {
            color: $color-white;
            text-align: center;
            display: inline-block;
            vertical-align: top;

            .upgrade-thumbnail {
                width: 100%;
            }
        }
    }

    .upgrade-included-channel-icon {
        display: inline-block;
        vertical-align: top;
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
    }

    .featured-channels {
        padding-top: 1vh;
    }

    .featured-channel-dots {
        color: $color-light-gray;
        margin: 0;
        float: right;
        padding-top: .5vmin;

        .current {
            color: $color-yellow;
        }
    }
}
