@import "variables.10ft";
@import "./style";

core-iap-restart {
    .core-spinner {
        z-index: 2;
    }

    .core-iap-restart {
        .iap-restart-sub-title {
            .sub-text {
                overflow: hidden;
                position: relative;
                line-height: 4.44vh;
                max-height: 8.88vh;
                text-align: center;
                margin-right: -1em;
                padding-right: 1em;

                &:before {
                    content: '...';
                    position: absolute;
                    right: 0;
                    bottom: 0;
                }

                &:after {
                    content: '';
                    position: absolute;
                    right: 0;
                    width: 1em;
                    height: 1em;
                    margin-top: 0.2em;
                    background: $color-dark-gray;
                }
            }
        }

        .restart-now-btn.btn {
            height: 11vh;
            width: 32vw;
            padding: 0;
        }

        .bottom-btn.btn {
            width: 26.98vw;
            height: 9.33vh;
        }
    }
}
