@import '../../../styles/mixins.scss';

// $asset-item-spacer is imported in platform specific sheets
$grid-tile-left-right-margin: $asset-item-spacer / 2;
$grid-tile-top-bottom-margin: 20px;

core-recordings-grid {
    .grid-spinner-container {
        @include position();
    }

    .item-grid {
        display: flex;
        flex-flow: row wrap;
        justify-content: flex-start;
        align-content: space-between;

        // $grid-tile-top-bottom-margin $ribbon-left-gutter are defined or imported in platforms specific sheets
        margin: -$grid-tile-top-bottom-margin #{$ribbon-left-gutter - $grid-tile-left-right-margin};
    }

    // Individual asset item in the Manage Recordings screen
    core-ribbon-item {
        list-style-type: none;
        position: relative;
        // Selection styles are UI_TYPE specific, see the platform stylesheets

        .ribbon-item {
            // $grid-tile-top-bottom-margin and $asset-item-width are defined or imported in platforms specific sheets
            margin: $grid-tile-top-bottom-margin $grid-tile-left-right-margin;
            width: $asset-item-width;

            img {
                width: 100%;
            }
        }
    }
}
