@import "variables.10ft";
@import "../base";

// Global standard styles
// -----------------------------------------------------------------------------

// Generate Brandon font faces.
@each $path, $weight, $style in
        ('../../assets/fonts/brandon/Thin', 100),
        ('../../assets/fonts/brandon/Light', 300),
        ('../../assets/fonts/brandon/Regular', 400),
        ('../../assets/fonts/brandon/Medium', 500),
        ('../../assets/fonts/brandon/Bold', 700),
        ('../../assets/fonts/brandon/Black', 900)
{
    @font-face {
        font-family: Brandon;
        src: url('#{$path}.woff'), url('#{$path}.woff2');
        font-weight: $weight;
    }

    @font-face {
        font-family: Brandon;
        src: url('#{$path}-italic.woff'), url('#{$path}-italic.woff2');
        font-weight: $weight;
        font-style: italic;
    }
}

body {
    line-height: $line-height-default;
}

.nonLinearMessage {

    h2 {
        @include font-size(bigger);
        font-weight: thin;
        text-align: left;
        color: $color-yellow;
        margin: 20px 0 20px 100px;
    }

    a {
        margin-left: 100px;
    }

    .btn {
        margin-bottom: 20px;
    }
}

#app-container {
    box-sizing: border-box;
    background-color: $color-dark-gray;
    background-size: 100%;
    color: $color-white;
    width: 100%;
    height: 100%;
    position: relative;
    overflow-x: hidden;

    &.black {
        background-color: $color-black;
    }
    .opened {
        display: block;
    }

    .closed {
        display: none;
    }

    #app-content-container {
        @include full-absolute();
        height: 100%;
        overflow: hidden;
        z-index: $index-middle;

        #app-content {
            @include full-absolute();
            height: 100%;
            overflow-y: auto;
        }
    }

    #video-container {
        z-index: $index-middle;
        height: 100%;
        width: 100%;
    }

    // hit area for clicking on video
    .smart-remote-hitbox {
        position: fixed;
        height: 100%;
        width: 100%;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        z-index: $index-middle;
        cursor: pointer;

        &:focus {
            outline: none;
        }
    }
}


@include scale-for-hd-tv {
    body, p,h1, h2, h3, h4, h5 {
        line-height: $line-height-default-hd-tv;
    }

    .nonLinearMessage {
        h2 {
            margin: 13px 0 13px 67px;
        }

        a {
            margin-left: 67px;
        }

        .btn {
            margin-bottom: 13px;
        }
    }
}
