@import "../../../styles/mixins";

core-details-meta-data {
    display: block;

    .drawer-details {
        .title {
            margin-bottom: 10px;
            color: $color-white;
        }

        .recording-icon {
            color: $color-red;
            float: left;
            font-size: $font-size-big;
            margin-right: 10px;

            i {
                font-size: $font-size-smaller;
            }
        }

        .ribbon-title {
            font-weight: normal;
        }

        .episode-name {
            margin-bottom: 10px;
            margin-top: 0;
            color: $color-off-white;
            padding: 0;
            padding-right: 30px;
        }

        .start-time {
            color: $color-off-white;
            display: inline-block;
        }

        .new {
            color: $color-yellow;
            padding-bottom: 0;
            margin: 0;
        }

        .free-content,
        .upcoming,
        .on-now {
            color: $color-yellow;
        }

        .expiration-date {
            color: $color-yellow;
            padding-top: 18px;
            position: relative;
        }

        .meta-data {
            span:before {
                content: "|";
                margin: 0 10px 0 5px;
                color: $color-off-white;
            }

            span:first-child:before {
                content: "";
                border: none;
                margin: 0;
            }
        }
    }

    .tomato {
        background-image: url("../../../../assets/images/rt-fresh.png");
        background-size: contain;
        background-repeat: no-repeat;
        height: 24px;
        width: 24px;
        display: inline-block;
        position: absolute;
        margin-top: -20px;

        &.rotten {
            background-image: url("../../../../assets/images/rt-rotten.png");
        }
    }

    .rt-score-icon {
        width: 25px;
        display: inline-block;
    }
}
