@import "variables.10ft";
core-watch-options-modal .core-modal {
    button.btn {
        font-size: 28px;
        height: 75px;
        margin-bottom: 12px;
        width: 370px;

        &.close {
            margin-top: 18px;
        }
    }

    .modal-footer {
        display: flex;
        flex-direction: column;

        .btn + .btn {
            margin-left: 0;
        }
    }

    .modal-header {
        margin-bottom: 40px;

        h2.modal-title {
            color: $color-yellow;
            font-size: 30px;
            font-weight: bold;
            text-align: center;
        }
    }
}
