// Import global platform agnostic variables
@import './variables';

// ==== 3FT VARIABLES ======================================
// 3ft specific global variables should be defined here.

// Menu and top navigation dimensions for 3ft
$nav-dimensions-desktop: 75px;
$nav-dimensions-tablet: 48px;

// Top header height for mytv pages like continue watching and mychannels
$top-header-height: 75px;

// Side Menu
$side-menu-width-expanded: 256px;

// 3ft Device Screen Widths per current Zeplin sketches
// Device Screen Widths
$screen-width-desktop: 1200px;
$max-screen-width-tablet: 1024px;
$max-screen-width-mobile: 767px;
$max-screen-width-mobile-portrait: 480px;

// Tile / iView Progress Bar
$asset-progress-bar-height: 3px;

// Hovering over certain elements should transition to a "highlighted" state.
$hover-transition-speed: 200ms;

// width of vertical scroll bar
$vertical-scroll-bar-width: 5px;

// width of settings left menu for tablet
$settings-left-menu-dimensions-tablet: 200px;

// width of settings side menu
$settings-left-menu-width: 320px;

// === REACT ===============================================
$play-icon-size: 28px;
$play-icon-size-hover: 32px;

$mobile-size-percentage: .8;
