// Shared styles for the ruler above the grid guide
core-grid-guide-ruler {
    position: absolute;
    .ruler-unit {
        border-right: solid 2px $color-medium-gray;
        padding-left: 5px;
        background-color: $color-dark-gray;
        position: absolute;
        top:0;

        &.start-of-day {
            color: $color-dark-blue;
        }
    }

    // See style.win10 and style.10ft platform stylesheets for grid guide ruler arrow buttons

    // blue ruler indicating current time
    .current-time {
        position: absolute;
        display: block;
        width: 100%;
        height: 4px;
        left: 0;
        bottom: 0;
        background: $color-dark-blue;
        z-index: $index-front + 1 !important;
        // blue circle at right end, bottom of circle gets clipped but got ok for this
        &:after {
            content: " ";
            height: 10px;
            width: 10px;
            border-radius: 10px;
            display: block;
            position: absolute;
            top: -3px;
            right: 0;
            background-color: $color-dark-blue;
        }
    }
}
