@import "variables.10ft";
@import '../../ribbon-system.variables.10ft';
@import '../../../../styles/mixins.scss';
@import './style';

core-item-thumbnail {
    .core-item-thumbnail:not(.person) {
        i {
            font-size: 20px;
        }

        .image-wrapper {
            img:before {
                font-size: 20px;
            }
        }
    }

    .core-item-thumbnail.person {
        .image-wrapper {
            img:before {
                font-size: 120px;
            }
        }
        .no-image-wrapper {
            font-size: 120px;
        }
    }
}


@include scale-for-720p {
    core-item-thumbnail {
        .core-item-thumbnail{
            .image-wrapper {
                img:before {
                    font-size: 13px;
                }
            }

            .no-image-wrapper {
                font-size: 13px;
            }
        }

        .core-item-thumbnail.person {
            .image-wrapper {
                img {
                    height: $asset-image-height-720p;
                }
            }
        }

        .core-item-thumbnail:not(.person) {
            i {
                font-size: 14px;
            }
        }

        .core-item-thumbnail.person {
            .image-wrapper {
                img:before {
                    font-size: 80px;
                }
            }
            .no-image-wrapper {
                font-size: 80px;
            }
        }
    }
}
