@import "variables.10ft";
@import "../../styles/base";
@import "../../styles/base.win10";

$top-nav-img-width: $top-nav-width - 90px;

collections {
    .dashboard-page {
        position: fixed;
        top: $top-nav-height-tv;
    }
}

.dashboard-page {
    .vertical-scroll-container:after {
        content: "";
        display: block;
        height: $vertical-scroll-padding-bottom;
    }
}
dashboard{
    core-main-nav {
        z-index: 1;
    }
}

.dashboard-nav {
    display: block;
    z-index: $index-front;
    height: $top-nav-height-tv;
    position: relative;
    background-color: $color-black;
    border-bottom: 3px solid $color-win10-gray;

    &.scrollable {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
    }
}

.dashboard-filler {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: $color-dark-gray;
}

// set the correct logo image url based on brand
[brand="sling"] .dashboard-menu-item .branding-image {
    background-image: url("../../assets/images/sling.png");
}
[brand="slinglatino"] .dashboard-menu-item .branding-image {
    background-image: url("../../assets/images/sling-slinglatino-loading-screen.png");
}
[brand="slinginternational"] .dashboard-menu-item .branding-image {
    background-image: url("../../assets/images/sling-slinginternational-loading-screen.png");
}

.dashboard-page {
    z-index: $index-middle;
    background-color: $color-dark-gray;
    height: $dashboard-page-height;
    width: 100%;
    position: fixed;

    &.scrollable{
        min-height: 100vmin;
        z-index: $index-middle;
        padding-top: $top-nav-height;
        position: relative;
    }

    .dashboard-page-content {
        overflow: visible;

        .dashboard-page-header {
            margin-top: 18px;
            margin-bottom: -18px; // ribbon title has 36px top margin so this is pull it up
            .breadcrumbs {
                padding-left: $ribbon-gutter;
                color: $color-off-white;
                @include font-size(bigger);
                .fa-chevron-right {
                    padding: 0 10px;
                    @include font-size(big2);
                }
                .breadcrumb-collection {
                    color: $color-yellow;
                }
            }
        }

        // this overrides collections images to fill the asset container
        .collections {
            .asset-item .asset-item-container .image-wrapper {
                img {
                    height: 100%;
                    width: auto;
                }
            }
        }

        .saved-movies {
            margin-top: 40px;
            width: 100%;
            text-align: center;
        }

        .no-filter-results {
            text-align: center;
            margin-top: 200px;
            position: relative;
            color: $color-off-white;
            @include font-size(biggest3);
        }
    }
}

.filter-button-row {
    width: 100%;
    display: block;
    @include font-size(bigger);
    font-weight: 500;
}

.filter-row {
    width: 100%;
    display: block;
    padding: 18px 0 0 $ribbon-gutter;
    @include font-size(bigger);
    font-weight: 500;

    .breadcrumb-title {
        display: inline-block;
        color: $color-yellow;
        position: relative;
        padding-left: 50px;

        .dish-icon-filter {
            @include font-size(biggest3);
            position: absolute;
            left: 0;
            top: -5px;
        }
    }

    .breadcrumb-filter {
        color: $color-silver;
    }
}

.filter-row.guide-page {

    .breadcrumb-title {
        display: inline-block;
        color: $color-yellow;
        margin-left: 40px;
        margin-top: 0;
        @include font-size(smallest);

        .dish-icon-filter {
            @include font-size(biggest6);
            position: absolute;
            margin-left: -45px;
            margin-top: -2px;
        }
    }

    .breadcrumb-filter {
        color: $color-silver;
        @include font-size(smallest);
    }

    .btn.open-filters {
        margin: 0;
        height: auto;
    }

}

.filter-row.with-tags {

    .btn.filters-tag {
        @include font-size(smallest);
        margin: 13px 0 0 10px;
        padding: 6px 15px;
        height: auto;
        min-width: 0;
        background-color: $color-gray;
        border: 0;
    }

    .btn.clear-tags {
        border: none;
        background-color: $color-black;
        @include font-size(smallest);
        color: $color-yellow;
        padding: 6px 15px;
        min-width: 0;

        &:hover,
        &:focus,
        &:active {
            background-color: $color-black;
            text-decoration: underline;
        }
    }
}

@include scale-for-hd-tv {
    core-main-nav {
        .top-nav-bar {
            .dashboard-menu-item {
                padding: 0 3px;
                display: inline-block;

                span {
                    font-size: 24px;
                    line-height: $top-nav-height-hd-tv;

                    svg {
                        width: 26px;
                        height: 26px;
                    }
                }

                img {
                    width: 26px;
                    height: 26px;
                }
                .filter-icons {
                    display: none;
                    font-size: 38px;
                    right: 3px;
                    margin-top: -40px;
                    text-align: center;
                    &.hidden {
                        display: none;
                    }

                    .icon-filter1 {
                        color: $color-white;
                    }
                }
                .active-filter {
                    display: block;
                    .icon-filter1 {
                        color: $color-yellow;
                    }
                }
                &.active, &:hover {
                    .filter-icons {
                        display: block;
                    }
                }
                &:focus {
                    .filter-icons {
                        margin-bottom: -0.6px;
                    }
                }
            }

            .icon-wrapper {
                width: 66px;
            }

            #search {
                margin-left: auto;
            }
        }
    }
    core-rental-confirm {
        .core-modal .modal-title {
            font-size: 30px;
        }
        .rental-controls {
            margin-top: 13px;
            font-size: 24px;
        }
    }
    core-rental-no-payment {
        .core-modal .modal-body {
            .header {
                font-size: $tv-font-size-big;
                font-weight: normal;
            }
            p.message {
                font-size: $tv-font-size-medium;
                font-weight: normal;
            }
        }
    }
    collections {
        .dashboard-page {
            top: $top-nav-height-hd-tv;
        }
    }
    .dashboard-page .dashboard-page-content  {
        .dashboard-page-header .breadcrumbs {
            padding-left: $ribbon-gutter-hd-tv;
        }
    }
}
