@import '../../../styles/mixins.scss';

core-ribbon-item-favorite-channels {
    line-height: normal;
    li {
        list-style-type: none;
        flex: none;
        position: relative;
        cursor: pointer;
        // padding, width, height, margin, default border-bottom set in platform stylesheets

        &:hover {
            @include hovered-box();
            h3, h4, p {
                color: $color-white;
            }
        }

        &:focus {
            @include focused-box();
            h3, h4, p {
                color: $color-white;
            }
        }

        // Text items
        h3, h4, p {
            @include ellipsis-able();
            // width, font-size and padding set in platform stylesheets
        }

        // Channel title
        h3 {
            color: $color-white;
            font-weight: 500;
            // font size set in platform stylesheets
        }

        // Metadata & up next
        h4, p {
            color: $color-near-white;
            position: absolute;
            // font size, position and width set in platform stylesheets
        }

        // Channel logo
        span.channel-logo {
            position: absolute;
            display: inline-block;
            text-align: right;
            // width and position set in platform stylesheets

            img {
                width: auto;
                // height set in platform stylesheets
            }
        }

        .asset-locked-icon {
            float: left;
            margin-right: 8px;
        }

        // logo-only styles set in platform stylesheets
    }

}
