@import "./variables";
@import "./mixins";

// Global standard styles
// -----------------------------------------------------------------------------

// Generate Brandon font faces.
@each $file-name, $weight, $style in ('Thin', 100),
    ('ThinItalic', 100, italic),
    ('Light', 300),
    ('LightItalic', 300, italic),
    ('Regular', 400),
    ('RegularItalic', 400, italic),
    ('Medium', 500),
    ('MediumItalic', 500, italic),
    ('Bold', 700),
    ('BoldItalic', 700, italic),
    ('Black', 900),
    ('BlackItalic', 900, italic) {
    @font-face {
        font-family: Brandon;
        src: url("../../assets/fonts/brandon/#{$file-name}.woff") format("woff");
        font-weight: $weight;
        font-style: $style;
    }
}

// The html element is set to 100% height with all scrolling disabled.
// We enable scrolling within specific components to preserve top headers.
html {
    height: 100%;
    overflow: hidden; // disable scrolling generally, enable specifically per component
    box-sizing: border-box;
    // disable scroll bars
    @include hide-scrollbar;
    ::-webkit-scrollbar {
        display: none;
    }
}

*, *:before, *:after {
    box-sizing: border-box;
}

body {
    height: 100%;
    width: 100%;
    color: $color-white;
    // font-size is set in UI_TYPE sheets
    font-family: Brandon, sans-serif;
    background-color: $color-dark-gray;
}

body,
h1,
h2,
h3,
h4,
h5,
p,
a,
ul,
ol,
li,
dl,
dt,
dd {
    margin: 0;
    padding: 0;
}

h1,
h2,
h3,
h4,
h5,
p,
button,
input {
    font-family: Brandon, sans-serif;
    font-weight: normal;
}

h1,
h2,
h3 {
    color: $color-white;
    // font-size is set in platform specific sheets
    font-weight: 300;
}

h4,
h5,
a,
p {
    color: $color-off-white;
}

select{
    display: block;
    margin: 10px 0;
}
