@import "variables.10ft";
@import "../../../styles/base";
@import "../../../styles/mixins";

.playback-controls-container.inactive {
    display: none;
}

.playback-controls-container {
    z-index: $index-front;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    text-align: center;
    margin: 0 auto;
    height: 288px;
    background-color: $color-overlay-gray;

    &.hideButtons {
        background: none;
    }

    // TODO this is a bit of a hack to make sure webpack includes these files, these styles are not actually used.
    .my-tv-button {
        background: url('../../../assets/images/my-tv.svg');
    }

    .info-button {
        background: url('../../../assets/images/info.svg');
    }

    .play-button {
        background: url('../../../assets/images/play.svg');
    }

    .pause-button {
        background: url('../../../assets/images/pause.svg');
    }

    .closed-caption-on-button {
        background: url('../../../assets/images/close-caption-on.svg');
    }

    .closed-caption-off-button {
        background: url('../../../assets/images/close-caption-off.svg');
    }

    .playback-controls {
        padding-top: 30px;

        core-record-button-player-control {
            div.record-button {
                span.button-text {
                    text-transform: uppercase;
                    @include font-size(big2);
                    margin: auto;
                }
            }
        }

        .action-buttons {
            width: 100%;

            .action-button, .record-button, .channel-recall-button {
                color: $color-light-gray;
                @include font-size(biggest);
                position: relative;
                border: 0;
                background-color: transparent;
                width: 180px;
                padding: 0;
                fill: $color-off-white;

                .playback-button {
                    width: $playback-button-width;
                    height: $playback-button-height;
                    display: inline-block;
                    border: 2px solid $color-overlay-invisible;
                    color: $color-light-gray;
                    line-height: $playback-button-height;
                    @include font-size(biggest6);
                    text-align: center;

                    svg {
                        @include include-icon($playback-controls-icon-width, $playback-controls-icon-height);
                    }
                }

                .text {
                    margin-top: 7px;
                    @include font-size(big2);
                    display: block;
                    visibility: hidden;
                    position: absolute;
                    width: 180px;
                    text-align: center;
                }

                &:hover {
                    color: $color-white;
                    text-decoration: none;
                    outline: none;
                    fill: $color-white;
                    -webkit-text-fill-color : $color-white;

                    .playback-button, .button-icon {
                        color: $color-white;
                        border: 1px solid $color-white;
                        text-decoration: none;
                        outline: none;
                    }

                    .text {
                        color: $color-white;
                        visibility: visible;
                    }
                }

                &.core-focused-item {
                    fill: $color-white;

                    .playback-button {
                        color: $color-white;
                        background-color: $color-dark-blue;
                        text-decoration: none;
                        outline: none;
                    }

                    .text{
                        color: $color-white;
                        visibility: visible;
                    }
                }
            }
        }

        .below-bar-group {
            text-align: center;
            width: 100%;
            position: absolute;
            color: $color-off-white;
            bottom: 0;
            padding: 10px 0 20px 0;
            @include font-size(biggest);
        }
    }
}


@include scale-for-hd-tv {
    .playback-controls-container {
        height: 192px;

        .playback-controls {
            padding-top: 20px;

            .action-buttons {
                .action-button, .channel-recall-button, .record-button{
                    width: 120px;

                    .playback-button {
                        width: $playback-button-width-hd-tv;
                        height: $playback-button-height-hd-tv;
                        line-height: $playback-button-height-hd-tv;

                        svg {
                            @include include-icon($playback-controls-icon-width-hd-tv, $playback-controls-icon-height-hd-tv);
                        }
                    }

                    .text {
                        margin-top: 5px;
                        width: 120px;
                        line-height: 16px;
                    }
                }
            }

            .below-bar-group {
                padding: 7px 0 14px 0;
            }
        }
    }
}
