@import "variables.10ft";
@import './style';
@import '../guide-container.variables.10ft.scss';
@import 'grid-guide.variables.10ft';
@import '../../../styles/mixins';

.grid_guide__container {
    .ruler-container {
        .empty-box {
            width: $grid-guide-channel-width;
            height: $grid-guide-ruler-height-desktop;
            position: absolute;
            &:after {
                content: '';
                width: 100%;
                height: 5px;
                position: absolute;
                bottom: -5px;
                background: $color-light-black;
            }
        }
        .grid-guide-ruler {
            left: $grid-guide-channel-width;
        }
    }

    .schedule-container {
        //Offset the schedule container by the ruler height
        transform: translate3d(0px, $grid-guide-ruler-height-desktop, 0px);
        backface-visibility: hidden;
        font-size: $grid-guide-schedule-container-font-size;
    }
}

@include scale-for-720p {
    .grid_guide__container {
        .ruler-container {
            .empty-box {
                width: $grid-guide-channel-width-720p;
                height: $grid-guide-ruler-height-desktop-720p;
                &:after {
                    height: 3px;
                    bottom: -3px;
                }
            }

            .grid-guide-ruler {
                left: $grid-guide-channel-width-720p;
            }
        }

        .schedule-container {
            //Offset the schedule container by the ruler height
            transform: translate3d(0px, $grid-guide-ruler-height-desktop-720p, 0px);
            font-size: $grid-guide-schedule-container-font-size-720p;
        }
    }
}
