// Platform-agnostic button styles
.btn {
    background-color: $color-medium-gray;
    box-sizing: border-box;
    border: none;
    border-radius: 0;
    color: $color-cool-white;
    cursor: pointer;
    display: inline-block;
    // font-size, font-weight, and padding are set in UI_TYPE sheets
    margin: 0;
    position: relative;
    text-align: center;
    text-decoration: none;

    &:hover:not(.border-bottom) {
        background-color: $color-near-gray;
        color: $color-white;
        text-decoration: none;

        // icon hover state color
        i:before {
            color: $color-white;
        }
    }

    &.delete:not(:disabled) {
        &:hover,
        &:focus {
            background-color: $color-red;
        }
    }

    &[disabled="disabled"],
    &:disabled,
    &.disabled {
        color: $color-light-gray;
        opacity: 1;
        cursor: default;
        background-color: $color-medium-gray;
    }

    &.no-min-width {
        min-width: 0;

        .fa-heart {
            color: $color-pink;
        }
    }

    .icon-minimize {
        // font-size is set in UI_TYPE sheets
        font-weight: bold;
        margin-left: 5px;
    }
}

.block-buttons {
    .btn {
        width: 100%;
        display: block;
        margin: 0;

        & + .btn {
            margin-left: 0;
            // margin-top set in UI_TYPE sheets
        }
    }
}

.button-spinner {
    background: url("../../assets/images/spinner.png") no-repeat;
    animation: spinner 0.75s infinite linear;
    background-size: contain;
    width: 45px;
    height: 45px;
}
