@import "variables.10ft";
@import "../../styles/base";
@import "../../styles/base.win10";

.modal-backdrop.in {
    background-color: $color-overlay-gray; // default is gray (drawers are overwritten as black)
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: $index-front;
    opacity: 1;
}

.modal {
    display: flex;
    align-items: center;
    justify-content: center;

    .modal-dialog {

        .modal-content {

            .modal-body {
                padding: 0;

                .input-row {
                    width: 844px;
                }
            }
        }
    }

    .modal-list-button {
        display: block;
        width: 370px;
        margin: 12px 28px 0;

        &.modal-close {
            margin-top: 30px;
        }
    }

    .modal-content {
        border: 1px solid $color-gray;
        color: $color-silver;
        z-index: $index-front + 1;
        background-color: $color-black;
        padding: 50px 40px;
        border-radius: 0;

        .modal-title {
            color: $color-yellow;
            text-transform: uppercase;
            @include font-size(biggest);
            letter-spacing: 0.5px;
            line-height: 36px;
            margin-bottom: 40px;

            &.centered {
                text-align: center;
            }
        }
    }

    .signout-modal {
        width: 700px;
        padding: 25px 100px;

        .modal-header,
        .modal-footer {
            border: none;
        }

        h4 {
            @include font-size(bigger2);
            line-height: 36px;
            color: $color-white;
            text-align: center;
            margin-bottom: 20px;
        }

        .modal-footer {
            margin: 0;
        }
    }

    .modal-header {
        border: none;
        padding: 0 0 20px 0;
        .modal-banner { // xbox only style.
            margin-left: -15px;
            margin-bottom: 30px;
        }
        h4 {
            font-size: 24px;
            color: $color-white;
        }
    }

    .modal-close-btn {
        position: absolute;
        right: 6px;
        top: 6px;
        border: 1px solid $color-gray;
        height: 25px;
        width: 25px;

        @include border-radius(100px);

        .custom-close-x {
            color: $color-light-gray;
            @include font-size(smaller);
            font-weight: 100;
            text-transform: none;
            position: absolute;
            top: 0;
            left: 0;
            padding-left: 7px;
            line-height: 20px;
        }

        &:hover {
            border-color: $color-white;

            .custom-close-x {
                color: $color-white;
                text-decoration: none;
                outline: none;
                cursor: pointer;
            }
        }
    }

    .modal-footer {
        border: none;
        text-align: center;

        .modal-buttons {
            display: flex;
            justify-content: center;
        }

        .btn {
            margin: 0;
            position: static;
        }

        .btn + .btn {
            margin-left: 20px;
        }
    }

    .modal-content.transparent-modal {
        background: $color-overlay-invisible;
        box-shadow: none;
        border: 0;

        .modal-footer,
        .modal-header {
            border-bottom: 0;
            border-top: 0;
        }
    }

    .guide-filter-modal {

        padding: 20px 100px;

        .filter-container {

            position: relative;

            &:before, &:after {
                position: absolute;
                height: 200px;
                width: 100%;
                z-index: 10;
                text-align: center;
                transform: scale(1, 0.70); // descrease the arrow size
                display: none;
            }
            &:before {
                content: "\25B2";
                top: -35px;
                background: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
            }
            &:after {
                content: "\25BC";
                bottom: -35px;
                background: linear-gradient(to top, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
                padding-top: 200px;
            }
            &.indicator-top {
                &:before {
                    display:block;
                }
            }
            &.indicator-bottom {
                &:after {
                    display:block;
                }
            }
        }

        .vertical-scroll-container {
            background: none;
            max-height: 600px;
        }

        // radio button styles scoped to the guide modal
        // inner fa circle positions are specific to the modal
        .ds-radio {
            white-space: nowrap;

            .radio + label {
                .fa-circle {
                    left: 0;
                    right: 0;
                    top: 0;
                    bottom: 0;
                    margin: auto;
                    height: 28px;
                    line-height: 28px;
                    position: absolute;
                }
            }
            .label-name {
                white-space: pre-wrap;
                word-wrap: break-word;
                padding-right: 20px;  // give a space to right to prevent word clipping
            }
        }
    }

    .movie-filter-modal {
        // radio button styles scoped to the movie filter modal
        // inner fa circle positions are specific to the modal
        .ds-radio {
            .radio + label {
                .fa-circle {
                    left: 0;
                    right: 0;
                    top: 0;
                    bottom: 0;
                    margin: auto;
                    height: 28px;
                    line-height: 28px;
                    position: absolute;
                }
            }
        }

        .filter-container {
            display: flex;
            flex-wrap: nowrap;

            .filter-column {
                margin-bottom: 20px;

                &:first-child {
                    margin-right: 75px;
                    padding-right: 50px;
                    border-right: 1px solid $color-light-gray;
                }
            }
        }
    }

    .ds-radio {
        .radio + label {
            .radio-wrapper {
                margin-top: 4px;
                position: relative;
                text-align: center;
            }

            &.has-hover {
                .radio-wrapper {
                    margin-top: 0;
                }

                .fa-circle {
                    left: 0;
                    right: 0;
                    top: 0;
                    bottom: 0;
                    margin: auto;
                    height: 28px;
                    line-height: 28px;
                    position: absolute;
                }
            }
        }

        &.core-focused-item {
            .radio + label {
                .radio-wrapper {
                    margin-top: 0;
                }

                .fa-circle {
                    left: 0;
                    right: 0;
                    top: 0;
                    bottom: 0;
                    margin: auto;
                    height: 28px;
                    line-height: 28px;
                    position: absolute;
                }
            }
        }
    }

    .warning {
        color: $color-orange;
        display: block;
        text-align: center;
        padding: 0 20px;
        @include font-size(smaller);
    }

    .message-modal .modal-header p {
        color: $color-white;
    }
}

.modal-wide .modal-dialog {
    width: 1000px;
}

.modal-rental .modal-dialog {
    width: 700px;
}

.modal-options .modal-dialog {
    width: 506px;
}

.modal-filter .modal-dialog {
    width: 900px;
}

// Sign In modal
.modal-signin .modal-dialog {
    width: 640px;

    .loading-spinner{
        z-index: 5;
    }

    .modal-content {
        padding: 40px 50px;

        .modal-header {
            color: $color-white;
            @include font-size(bigger);
            font-weight: 500;
            padding: 0;
            margin-bottom: 30px;
        }

        .modal-body {
            text-align: center;

            .form-group { //overwrite bootstrap styles
                margin-bottom: 20px;
            }

            .modal-input {
                width: 540px;
                height: 58px;

                .form-control {
                    height: 60px;
                    text-align: left;

                    .non-input-text {
                        position: relative;
                        @include font-size(bigger);
                        font-weight: 500;
                        color: $color-white;

                        &.placeholder-text {
                            color: $color-off-white;
                        }
                    }
                }

                .password-input-overlay-text {
                    top: 6px;
                    text-align: left;
                }
            }

            .modal-footer {
                .modal-buttons {
                    margin-bottom: 20px;

                    [disabled='disabled'].btn, .btn.disabled {
                        border-color: $color-gray;
                        background-color: $button-background-disabled;
                        color: $color-near-off-white;
                    }
                }
            }
        }

        .form-control {
            width: 100%;
            height: 58px;
            @include font-size(bigger);
            font-weight: 500;
            color: $color-white;
        }

        // Reset modal
        .forgot-password-text {
            color: $color-yellow;
            text-align: center;
            margin-top: 15px;
            margin-bottom: 15px;
            @include font-size(bigger);
            font-weight: 500;
            border-bottom: 3px solid $color-overlay-invisible; // why do we have an invisible border?
            position: relative;
            top: 30px;
            display: inline;

            &:focus,
            &.core-focused-item {
                color: $color-dark-blue;
                outline: $color-overlay-invisible;
                text-decoration: none;
                border-bottom: 3px solid $color-dark-blue;
            }
        }
    }
}

.modal-resume .modal-dialog {
    width: 720px;
}

.modal-keypad .modal-dialog {
    width: $keypad-width * 1.1;
}

// IAP modals

.modal-terms .modal-dialog {
    width: 900px;
}

.modal-privacy .modal-dialog {
    width: 900px;
}

// All modals
.center-text {
    text-align: center;
    line-height: 40px;
}

.center-link {
    text-align: center;
    color: $color-yellow;
}

.label-link {
    color: $color-light-gray;

    &:focus {
        color: $color-dark-blue;
        outline: $color-overlay-invisible;
        text-decoration: none;

        + div > input {
            border: $color-dark-blue 1px solid;
        }
    }

    &.sign-in {
        text-align: right;
        line-height: 44px;

        label {
            padding-top: 0px;
        }
    }
}

// removing "display: flex" from ng-style and adding it in modals.scss file as a separate class "display-modal", since we need to prefix every
// occurence of "flex" with "-webkit-flex" for SAMSUNG platform
.modal-open div.display-modal {
    display: flex;
}

