@import "variables.10ft";
@import "./style";
@import '../../styles/mixins';

core-splash {

  .splash-page {

      .header-row {
          height: 140px;

          .sling-logo {
              height: 100px;
              width: 192px;
              margin: 0 0 0 100px;
          }

          .iap-elements {
              margin-right: 50px;
              flex: 1;
              display: flex;
              align-items: center;

              .iap-headline {
                  color: $color-off-white;
                  font-weight: 700;
                  display: inline;
                  margin: 0 10px 0 auto;
                  font-size: $font-size-medium;
              }

              .dropdown-toggle {
                  width: 300px;
                  height: 75px;
                  line-height: 75px;
                  padding: 0 0 0 25px;
                  font-weight: 600;
                  font-size: $font-size-medium;

                  .plan-name {
                      font-size: $font-size-big;
                  }

                  .arrow {
                      line-height: 75px;
                      font-size: $font-size-bigger;

                      &.fa-sort-desc {
                          top: -5px;
                      }
                  }
              }

              .dropdown-list {
                  height: 600px;
                  width: 300px;
                  background-color: $color-gray;

                  .list-item {
                      font-weight: 600;
                      padding: 20px 0 19px 25px;
                      font-size: $font-size-medium;

                      &.core-focused-item {
                          @include focused-box();
                      }
                  }
              }
          }
      }

      .splash-background {
          .core-spinner {
              top: 0;
              position: fixed;
          }
      }

      .splash-footer {
          height: 150px;

          .verbage {
              margin: 24px 24px 24px 100px;

              .tagline {
                  font-size: $font-size-biggest2;
                  font-weight: 800;
                  line-height: normal;
              }

              .subtagline {
                  margin-top: 5px;
                  font-size: $font-size-medium;
                  font-weight: 700;
                  text-transform: none;
              }
          }

          .button-container {
              padding: 0px;

              .btn {
                  padding: 17px 77px;
                  margin-right: 60px;
              }
          }
      }

  }

}

@include scale-for-720p {
    core-splash {

        .splash-page {
            .header-row {
                height: 93px;

                .sling-logo {
                    height: 61px;
                    width: 301px;
                    margin: 0 0 0 67px;
                }

                .iap-elements {
                    margin-right: 33px;

                    .iap-headline {
                        margin: 0 7px 0 auto;
                        font-size: $font-size-smallest;
                    }
                    .dropdown-container {
                        .dropdown-toggle {
                            width: 200px;
                            height: 50px;
                            line-height: 50px;
                            padding: 0 0 0 17px;
                            font-size: $font-size-smallest;

                            .plan-name {
                                font-size: $font-size-smaller;
                            }

                            .arrow {
                                line-height: 50px;
                                font-size: $font-size-small;

                                &.fa-sort-desc {
                                    top: -3px;
                                }
                            }
                        }

                        .dropdown-list {
                            top: 72px;
                            height: 400px;
                            width: 200px;

                            .list-item {
                                padding: 13px 0 13px 17px;
                                font-size: $font-size-smallest;
                            }
                        }
                    }
                }
            }

            .splash-footer {
                height: 100px;

                .verbage {
                    margin: 16px 16px 16px 67px;

                    .tagline {
                        font-size: $font-size-big;
                    }

                    .subtagline {
                        margin-top: 3px;
                        font-size: $font-size-smallest;
                    }
                }

                .button-container {
                    margin-top: 25px;

                    .btn {
                        padding: 11px 51px;
                        margin-right: 40px;
                    }
                }
            }
        }
    }
}

