@import "variables.10ft";
@import "../../../styles/base";

.modal-content.appresume {
    padding: 50px 0px;

    .modal-header {
        color: $color-yellow;
        text-transform: uppercase;
        @include font-size(biggest);
        font-weight: bold;
        font-style: normal;
        font-stretch: normal;
        line-height: 1.2;
        letter-spacing: 0.5px;
        text-align: center;
        padding: 0;
        margin: 0;
    }

    .modal-body {
        margin: 40px 80px 50px 80px;
        text-align: center;
        display: flex;
        justify-content: flex-start;
        align-items: center;

        .resume-thumb {
            max-width: 240px;
            width: auto;
            height: auto;
            display: inline-block;
        }

        .resume-title {
            max-width: 280px;
            margin-left: 40px;
            @include font-size(bigger);
            line-height: 1.38;
            color: $color-off-white;
            text-align: left;
            display: inline-block;
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
        }
    }

    .modal-footer {
        padding: 0;
        margin: 0;
        text-align: center;

        .btn {
            margin-top: 0px;
        }
    }
}

@include scale-for-hd-tv {
    .modal-content.appresume {
        padding: 34px 0px;

        .modal-body {
            margin: 27px 54px 34px 54px;

            .resume-thumb {
                max-width: 160px;
            }

            .resume-title {
                max-width: 187px;
                margin-left: 27px;
            }
        }
    }
}
