@import "variables.10ft";
@import "../base";

$border-size: 6px;

// -- Inputs
// NOTE for TV Platforms - do not use actual <INPUT> as that triggers native keyboards,
// use '<div class="form-control">{{data}}</div>' instead

.label-link {
    @include font-size(big);
    margin-top: 5px;
}

.form-control {
    display: block;
    height: 44px;
    background-color: $color-disabled;
    margin: 0;
    border: $border-size solid $color-disabled;
    -webkit-border-radius: 0;
    -moz-border-radius: 0;
    border-radius: 0;
    outline: none;
    @include font-size(smaller);

    .non-input-text {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        width: 100%;
        display: block;
    }

    // overriding bootstrap CSS
    box-shadow: none !important; // overriding bootstrap CSS
    &:focus{
        border-color: $color-dark-blue;
    }
}

fieldset {
    outline: none;
    border: none;
    padding: 0;
}

input {
    padding-left: 6px;

    &.ng-invalid.ng-dirty {
        border-color: $color-red;
    }
    &.focus-grid-focused, &.focus-grid-focused.ng-dirty {
        border-color: $color-dark-blue;
    }
    &.has-hover, &:hover, &.focus-grid-focused:hover, &.ng-dirty:hover{
        border-color: $color-white;
    }
}

// Remove the little spinner incrementer for number text inputs
input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    margin: 0;
}

.focus-grid-focused{
    .form-control, .ng-invalid.ng-dirty{
        border-color: $color-dark-blue;
    }
}

.password-input-overlay-text {
    color: $color-light-gray;
    @include font-size(bigger);
    height: 40px;
    padding-left: 20px;
    width: 98%;
    margin-top: -50px;

    &.password-hidden {
        letter-spacing: 5px;
        @include font-size(smaller);
        color: $color-white;
        white-space: nowrap;
        overflow: hidden;

        &.password-sign-in {
            position: relative;
            top: 15px;
        }
    }
}

// Toggle Switch element /////////////////////////////
.toggle {
    float: left;

    &:hover + .toggle-label,
    &:focus + .toggle-label,
    &:active + .toggle-label {
        color: $color-white;
    }

    &.disabled:hover {
        cursor: not-allowed;
    }
}

.toggle-label {
    letter-spacing: 1px;
    font-weight: normal;
    color: $color-off-white;
    padding: 5px 0 0 15px;
}

.toggle-switch {
    background-color: transparent;
    height: $toggle-switch-height;
    width: $toggle-switch-width;
    border: 2px solid $color-off-white;
    border-radius: 50px;

    .fa-circle {
        color: $color-off-white;
        position: absolute;
        top: 2px;
        left: 5px;
        margin-top: 0;
        height: 14px;
        width: 14px;
        @include font-size(medium);
    }

    &.disabled {
        border-color: $color-light-gray;
        .fa-circle {
            color: $color-light-gray;
        }
    }

    &.on {
        background-color: $color-yellow;
        border-color: $color-yellow;

        .fa-circle {
            color: $color-white;
            left: 37px;
        }
    }
}

@include scale-for-hd-tv {

    .label-link {
        margin-top: 4px;
    }

    .form-control {
        border-width: $border-size-hd-tv;
        height: 30px;
    }

    input {
        border-width: $border-size-hd-tv;
    }

    .password-input-overlay-text {
        height: 23px;
        padding-left: 14px;
        width: 98%;
        margin-top: -34px;

        &.password-hidden {
            letter-spacing: 4px;

            &.password-sign-in {
                top: 10px;
            }
        }
    }

    .toggle-label {
        padding: 3px 0 0 10px;
    }

    .toggle-switch {
        height: $toggle-switch-height-hd-tv;
        width: $toggle-switch-width-hd-tv;
        border: 1px solid $color-off-white;
        border-radius: 33px;

        .fa-circle {
            top: 2px;
            left: 3px;
            height: 9px;
            width: 9px;
        }

        &.on {
            .fa-circle {
                left: 25px;
            }
        }
    }
}
