@import "variables.10ft";
@import '../ribbon-system.variables.10ft';
@import './style';

// Tile and Image dimensions picked up from https://app.zeplin.io/project/564a14798677223661103f46/screen/59ce9d3a1d0f7c067984f1ca
core-ribbon-item-my-channels {
    .ribbon-item.channel-tile {
        &.core-focused-item {
            @include focused-box();
        }
        border-width: 2px;

        img {
            max-width: 173px;
            opacity: 0.5;
        }
    }
}


@include scale-for-720p {
    // My channels asset item
    core-ribbon-item-my-channels {
        .ribbon-item.channel-tile {
            height: $asset-image-height-720p;

            img {
                max-width: 73px;
            }
        }
    }
}
