// Shared styles for the date filter button
core-date-filter {
    text-align: right;

    button.btn {
        background-color: $color-medium-gray;
        color: $color-cool-white;
        white-space: nowrap;
    }

    button.btn:hover {
        background-color: $color-dark-blue;
        color: $color-white;
    }

    // Calendar icon only displayed on mobile
    .icon-date-select {
        display: none;
    }
}
