@import '../../styles/mixins';
@import '../../styles/variables';

core-modal-change-device-name {
    .core-modal {
        width: 800px;
        @include fade-in();
        backface-visibility: hidden;
        background-color: $color-black ;

        .modal-header {
            h4.device-name-change-title {
                font-weight: 500;
                color: $color-white;
            }
        }
        .modal-body {
            .device-name-input {
                width: 100%;
                height: 50px;
                font-weight: 500;
                color: $color-white;
                margin: 0;
                border-radius: 0;
                outline: none;
                box-shadow: none;
            }
        }
        .modal-footer {
            text-align: left;
            margin-top: 0;
        }
    }
}
