@import "variables.10ft";
@import './style';
@import 'micro-guide-variables.10ft';
@import '../../styles/mixins';

// Micro guide ribbon and container overrides for 10ft
core-micro-guide {
    bottom: 0;
    top: initial;

    // micro guide displayed
    &.open {
        bottom: 0;
    }

    // micro guide hidden moves down
    &.closed {
        bottom: $micro-guide-bottom-position-closed;
    }

    // Channel filter
    nav.channel-filter-container {
        height: $micro-guide-channel-filter-height;
        background-color: rgba(17,17,17,0.8);

        ul {
            margin: 0 50px;
            overflow: scroll;

            li {
                display: inline-block;
                height: $micro-guide-channel-filter-height - 2px;
                padding: 11px 25px 0 25px;
                font-size: $micro-guide-item-text-font-size;
                font-weight: bold;

                // Blue with white text for focus or hover
                &.core-focused-item {
                    background-color: $color-dark-blue;
                    span {
                        border-bottom: none;
                    }
                }

                &:hover {
                    background-color: $color-gray;
                    color: $color-white;
                }

                // Selected filter gets a blue underline, which changes to white
                // when it has focus or you hover over it
                &.selected span{
                    border-bottom: 3px solid $color-dark-blue;
                }

                &.selected.core-focused-item span,
                &.selected:hover span {
                    border-bottom: 3px solid $color-white;
                }

            }
        }
    }

    div.channel-list-container.horizontal-scroll-container {
        overflow-x: auto;
        width: 100%;
    }
}

@include scale-for-720p {
    core-micro-guide {
        // micro guide hidden moves down
        &.closed {
            bottom: $micro-guide-bottom-position-closed-720p;
        }

        // Channel filter
        nav.channel-filter-container {
            height: $micro-guide-channel-filter-height-720p;

            ul {
                margin: 0 33px;

                li {
                    height: $micro-guide-channel-filter-height-720p - 2px;
                    padding: 5px 16px 0 16px;
                    font-size: $micro-guide-item-text-font-size-720p;
                }
            }
        }
    }
}

