@import "../../styles/mixins";

.prompt-for-review-modal {
    .core-modal {
        width: 575px;
        background-color: $color-black;
        box-shadow: 0 10px 40px 0 rgba(0, 0, 0, 0.27);
        border: solid 2px $color-gray;
    }

    .modal-body {
        padding: 0;

        .modal-title {
            width: 233px;
            margin: 0 auto;
            padding-top: 10px;
            font-size: 27px;
            font-weight: bold;
            line-height: 1.2;
            letter-spacing: 0.5px;
            text-align: center;
            color: $color-yellow;
        }

        .modal-text {
            text-align: center;
            margin: 0 auto;
            padding-top: 10px;
            width: 368px;
            font-size: 26px;
            line-height: 1.38;
            color: $color-off-white;
        }

        .btn {
            width: 370px;
            height: 75px;
            object-fit: contain;

            &:hover {
                background-color: $color-dark-blue;
            }

            &.focus-grid-focused {
                background-color: $color-dark-blue;
            }
        }
        .prompt-for-review-options {
            margin: 32px 55px 0 55px;
        }

        .review-options:last-child {
            margin-top: 12px;
        }

        a.mail-link:link {
            color: $color-yellow;
            text-decoration: none;
        }

        .mail-link {
            color: $color-yellow;
        }

        .feedback-form .modal-text {
            line-height: normal;
        }

        .feedback-text {
            text-align: center;
            margin: 0 auto;
            padding-top: 17px;
            font-size: 26px;
            color: $color-off-white;
            width: 462px;
            line-height: 1.38;
        }
    }
}
