@import "variables.10ft";
@import "../../../styles/base";
@import "../../../styles/base.win10";

// TODO - wrap in a master container for shared CSS

.first-ribbon-asset{
    padding-left: $ribbon-gutter;
}

.drawer-ribbon {
    width: $drawer-width;
    .first-ribbon-asset {
        margin-left: 0;
    }
    &.platform-ribbon-title {
        padding: 0;
    }
    .platform-ribbon-title {
        padding: 30px 0 20px;
        line-height: 30px;
    }
    .vs-repeat-before-content {
        margin-left: -50px;
    }
}

.ribbon-switch {
    line-height: 0;
}

.platform-ribbon-title {
    color: $color-white;
    font-size: $ribbon-title-size;
    font-weight: normal;
    line-height: $ribbon-title-line-height;
    padding: $ribbon-title-padding-top 0 $ribbon-title-padding-bottom $ribbon-gutter;
    position: relative;

    span.count {
        position: relative;
        @include font-size(big3);
        color: $color-off-white;
    }

    span.message {
        color: $color-yellow;
        text-transform: none;
        margin-left: 20px;
        font-weight: 400;
    }
}

//Todo: Remove ribbon-title class from here once core-ui adds font-size in ribbon-title class for 10ft ui
.ribbon-title {
    font-size: $ribbon-title-size;
}

.shifting-ribbon-content{
    display: inline-flex;
    flex-wrap: nowrap;
    float: left;
}

.horizontal-scroll-container {
    overflow-x: auto;
    width: 100%;
}

.channel-ribbon-container {
    width: 100%;
    height: $my-channel-ribbon-height;
    .vs-repeat-before-content {
        height: initial!important;
    }
    .vs-repeat-after-content {
        height: initial!important;
    }

    // TODO this is a bit of a hack to make sure webpack includes these files, the styles are not actually used.
    .edit-button {
        background: url('../../../assets/images/edit-icon.svg');
    }
    .add-button {
        background: url('../../../assets/images/add-icon.svg');
    }
}

.asset-item-wrapper {
    flex: 0 0 auto;
}

.platform-ribbon-container {
    height: $ribbon-height;
    position: relative;
    z-index: $index-middle;

    .ribbon-message {
        background-color: $color-overlay-gray;
        z-index: $index-front;
        position: absolute;
        height: 100%;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;

        .ribbon-message-content {
            // centered
            margin-top: $ribbon-message-height * -0.5;
            height: $ribbon-message-height;
            position: absolute;
            top: 50%;
            left: 0;
            right: 0;
            text-align: center;

            p {
                // liquid width inside centered container
                display: inline-block;
                position: relative;
                @include font-size(medium);
                line-height: $ribbon-message-height;
                font-weight: 200;
                font-style: italic;

                .spinner {
                    // centered from top, but to the side of liquid paragraph text
                    position: absolute;
                    font-size: $ribbon-spinner-size;
                    height: $ribbon-spinner-size;
                    top: 50%;
                    margin-top: $ribbon-spinner-size * -0.5;
                    left: -$ribbon-spinner-size;
                }
            }
        }
    }



    .ribbon-content {
        width: 100vw;
        height: 100%;
        overflow-x: hidden;

        .focus-box {
            height: $asset-item-height;
            width: $asset-item-width;
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            z-index: $index-front;
            outline: none;

            &:focus {
                @include focused-box();
            }
        }

        &.empty {
            margin-left: 100px;
            white-space : nowrap;

            .ribbon-button, .asset-item{
                display: inline-block;
                height: $asset-image-height;
            }

            .asset-item{
                &.blank{
                    width: $asset-item-width;
                }
            }
        }
    }

    .ribbon-shift-middle {
        position: relative;
        left: -$asset-item-width / 3.5;
    }

    .ribbon-shift-right {
        position: relative;
        left: -($asset-item-width) + ($asset-item-width / 3) - $asset-item-spacer;
    }

}

.platform-ribbon-container, .channel-ribbon-container {
    .ribbon-width-large {
        width: 19999px;
    }
}

.vertical-scroll-container {
    overflow-y: scroll;
    height: 100%;

    .vs-repeat-before-content {
        height: initial!important;
    }
    .vs-repeat-after-content {
        height: initial!important;
    }
}

.empty-ribbon {
    overflow-x: hidden;
    white-space: nowrap;
    width: 100%;

    .empty-items {
        padding-left: $ribbon-gutter;

        .empty-item {
            display: inline-block;
            min-width: $asset-item-width;
            width: $asset-item-width;
            height: $asset-image-height;
            margin-right: $asset-item-spacer;
            background-color: $color-gray;

            .empty-item-icon {
                height: 100%;
                background: url('../../../assets/images/TV-icon.svg') no-repeat center;
                background-size: 124px 154px;
            }
        }

    }

    .platform-ribbon-title {
        span.message {
            white-space: normal;
        }
    }
}

@include scale-for-hd-tv {

    .first-ribbon-asset{
        padding-left: $ribbon-gutter-hd-tv;
    }

    .drawer-ribbon {
        width: $drawer-width-hd-tv;

        .platform-ribbon-title, ribbon-title {
            padding: 20px 0 14px;
            line-height: 20px;
        }

        .vs-repeat-before-content {
            margin-left: -34px;
        }
    }

    .platform-ribbon-title, ribbon-title {
        font-size: $ribbon-title-size-hd-tv;
        line-height: $ribbon-title-line-height-hd-tv;
        padding: $ribbon-title-padding-top-hd-tv 0 $ribbon-title-padding-bottom-hd-tv $ribbon-gutter-hd-tv;
        position: relative;

        span.message {
            margin-left: 14px;
        }
    }

    .ribbon-title {
        font-size: $ribbon-title-size-hd-tv;
    }

    .channel-ribbon-container {
        width: 100%;
        height: $my-channel-ribbon-height-hd-tv;
    }

    .platform-ribbon-container {
        height: $ribbon-height-hd-tv;

        .ribbon-message {

            .ribbon-message-content {
                // centered
                margin-top: $ribbon-message-height-hd-tv * -0.5;
                height: $ribbon-message-height-hd-tv;

                p {
                    // liquid width inside centered container
                    line-height: $ribbon-message-height-hd-tv;
                }

                .spinner {
                    // centered from top, but to the side of liquid paragraph text
                    font-size: $ribbon-spinner-size-hd-tv;
                    height: $ribbon-spinner-size-hd-tv;
                    margin-top: $ribbon-spinner-size-hd-tv * -0.5;
                    left: -$ribbon-spinner-size-hd-tv;
                }
            }
        }

        .ribbon-content {
            .focus-box {
                height: $asset-item-height-hd-tv;
                width: $asset-item-width-hd-tv;
            }

            &.empty {
                margin-left: 70px;

                .ribbon-button, .asset-item{
                    height: $asset-image-height-hd-tv;
                }

                .asset-item {
                    &.blank {
                        width: $asset-item-width-hd-tv;
                    }
                }
            }
        }

        .ribbon-shift-middle {
            left: -$asset-item-width-hd-tv / 3.5;
        }

        .ribbon-shift-right {
            left: -($asset-item-width-hd-tv) + ($asset-item-width-hd-tv / 3) - $asset-item-spacer-hd-tv;
        }
    }

    .platform-ribbon-container, .channel-ribbon-container {
        .ribbon-width-large {
            width: 13340px;
        }
    }

    .empty-ribbon {

        .empty-items {
            padding-left: $ribbon-gutter-hd-tv;

            .empty-item {
                min-width: $asset-item-width-hd-tv;
                width: $asset-item-width-hd-tv;
                height: $asset-image-height-hd-tv;
                margin-right: $asset-item-spacer-hd-tv;

                .empty-item-icon {
                    background-size: 83px 103px;
                }
            }

        }
    }
}
