@import "variables.10ft";
@import "../../../styles/base";
@import "../../../styles/base.win10";
@import "./channel_guide";

$mini-guide-height: 510px;
$mini-guide-bg: rgba(33, 33, 33, 0.90);
$mini-guide-filter-bg: rgba(16, 16, 16, 0.75);

.channel-guide.mini-guide {
    margin: 0;
    padding: 0;
    height: $mini-guide-height;
    min-height: $mini-guide-height;
    bottom: 0;
    top: initial;
    position: absolute;
    overflow: hidden;
    background: $mini-guide-bg;
    z-index: $index-front;

    .no-sub{
        margin-top: 135px;
    }

    .spinner-container {
        padding: 0;
        margin:0;
        position: relative;
        background-color: transparent;
    }

    .spinner, .loading-spinner {
        position: absolute;
        background-color: transparent;
    }

    .guide-inner {
        margin:0;
        background: none;

        .mini-guide-inner-content{
            margin-top: 20px;
            position: relative;
        }

        .spinner-container{
            margin-top: 150px;
        }

        .no-schedule-data {
            margin-top: 55px;
            text-align: center;

            .dish-icon-tv {
                color: $color-off-white;
                @include font-size(biggest8);
                display: inline-block;
                margin-bottom: 10px;
            }
        }

        .no-channels{
            margin-top: 150px;
        }

        .no-data-title{
            @include font-size(bigger);
            font-weight: bold;
            color: $color-off-white;
        }

        core-channel-ribbon {
            padding: 0;
            .core-ribbon-container {
                height: $channel-ribbon-height;
                text-align: center;
                position: relative;
                width: 100vw;

                .spinner {
                    margin-top: 5px;
                }

                .core-channel-ribbon__items {
                    display: inline-flex;
                    float: left;
                }

                .core-channel-ribbon__item {
                    flex: 0 0 auto;
                    // margin inside core-ui is being set inline for core-channel-ribbon__items(by core-ui),
                    // to override that !important is used here
                    margin: 0 !important;
                    height: $channel-ribbon-height;
                    overflow: visible;
                    box-sizing: border-box;
                    cursor: pointer;
                    padding: 0 18px;
                    z-index: $index-front + 1;
                    img {
                        opacity: 0.5;
                    }
                }
                .core-focused-item {
                    box-shadow: inset 0 -3px 0 0 #24c1ff;
                    img {
                        opacity: 1;
                    }
                }
            }
        }

        .channel-ribbon-container {
            background: #1b1b1b;
            .scrolling-content {
                padding-left: 0px;

                a:first-of-type.channel-btn {
                    margin-left: 40px;
                }
            }
        }

        .core-channel-ribbon-container {
            background: $mini-guide-filter-bg;
        }

        .filter-ribbon-container {
            height: $filter-ribbon-height;
            line-height: $filter-ribbon-height;
            overflow: hidden;
            -ms-overflow-style: none;
            background: $mini-guide-filter-bg;

            .shifting-ribbon-content:after{
                background: none;
            }

            &.scrollable {
                overflow-x: visible;
            }

            .scrolling-ribbon::-webkit-scrollbar {
                display: none;
            }

            .filter-item {
                display: inline-block;
                position: relative;
                color: $color-light-gray;
                @include font-size(big3);
                font-weight: bold;
                text-transform: uppercase;
                text-decoration: none;
                padding: 0 25px;
                float: left;
                white-space: nowrap;

                &:hover {
                    @include hovered-box();
                }

                &.first-item{
                    margin-left: 100px;
                }

                &.active-filter {
                    color: $color-white;
                    background: $color-win10-gray;
                    border-bottom: 3px solid $color-win10-gray;
                }

                &.core-focused-item {
                    @include focused-box();
                }

                &.tertiary-filter {
                    @include hovered-box();
                }
            }

            .active-filter .filter-item {
                color: $color-white;
            }
        }
    }
}


@include scale-for-hd-tv {
    .channel-guide.mini-guide {
        height: $mini-guide-height-hd-tv;
        min-height: $mini-guide-height-hd-tv;

        .no-sub{
            margin-top: 90px;
        }

        .guide-inner {
            .mini-guide-inner-content{
                margin-top: 13px;
            }

            .spinner-container{
                margin-top: 100px;
            }

            .no-schedule-data {
                margin-top: 37px;

                .dish-icon-tv {
                    margin-bottom: 7px;
                }
            }

            .no-channels{
                margin-top: 100px;
            }

            core-channel-ribbon {
                padding: 0px;
                height: 53px;
                .core-ribbon-container {
                    height: 52px;
                    text-align: center;
                    position: relative;
                    width: 100vw;

                    &::-webkit-scrollbar {
                        display: none;
                    }

                    .spinner {
                        margin-top: 5px;
                    }

                    .core-channel-ribbon__items {
                        display: inline-flex;
                        float: left;
                    }

                    .core-channel-ribbon__item {
                        flex: 0 0 auto;
                        // margin inside core-ui is being set inline for core-channel-ribbon__items(by core-ui),
                        // to override that !important is used here
                        margin: 0 !important;
                        overflow: visible;
                        box-sizing: border-box;
                        height: 50px;
                        cursor: pointer;
                        padding: 0 12px;
                        z-index: $index-front + 1;
                        img {
                            opacity: 0.5;
                        }
                    }

                }
            }
            .channel-ribbon-container {
                .scrolling-content {
                    a:first-of-type.channel-btn {
                        margin-left: 27px;
                    }
                }
            }

            .filter-ribbon-container {
                height: $filter-ribbon-height-hd-tv;
                line-height: $filter-ribbon-height-hd-tv;

                .filter-item {
                    padding: 0 17px;

                    &.first-item{
                        margin-left: 70px;
                    }

                    &.active-filter {
                        border-bottom: 2px solid $color-win10-gray;
                    }
                }
            }
        }
    }
}
