@import "variables.10ft";
@import '../../../styles/mixins';
@import './style';

core-details-button-row {

    .details-actions .btn {
        margin: 24px 20px 0 0;
        padding: 18px 25px;

        &:hover {
            // fav icon hover state color
            .fa-heart:before {
                color: $color-pink;
            }
        }

        &.primary,
        &.active,
        &.core-focused-item {
            // fav icon hover state color
            .fa-heart:before {
                color: $color-pink;
            }
        }

        + core-record-button > .btn {
            margin-left: 0;
        }

        + .btn {
            margin-left: 0;
        }
    }

    .purchase {
        .icon {
            font-size: 3vmin;
            font-weight: bold;
            position: relative;
            top: .3vmin;
        }
        @include scale-for-720p {
            .icon {
                top: -.1vmin;
            }
            span {
                position: relative;
                top: -.5vmin;
            }
        }
    }
}

@include scale-for-720p {
    core-details-button-row {

        .details-actions .btn,
        &.focus-grid-focused{
            margin: 16px 13px 0 0;
            padding: 12px 16px;
        }
    }
}
