@import "variables.10ft";
@import '../../../styles/mixins';
@import '../../../styles/variables';

.inline-error {
    @include flex-center();
    position: relative;
    font-size: inherit;
    line-height: normal;
    font-weight: 300;
    color: $color-yellow;
    flex: 1;
    top: 35vh;
}

.toast-message {
    position: fixed;
    bottom: 1.11vh;
    left: 50%;
    transform: translate(-50%,0);
    min-height: 3.7vh;
    width: 75%;
    font-weight: 300;
    padding: 2.7vh 2.6vw;
    box-shadow: 0 0 1.11vh #999;
    background-color: rgba(28, 28, 28, 0.8);
    border-radius: 1.38vh;
    cursor: pointer;
    z-index: 1;

    .errorCode {
        color: $color-dark-text-gray;
    }

    &:before {
        font-family: fontAwesome;
        font-size: inherit;
        float: left;
        margin-right: 1.3vw;
    }
    &.error {
        &:before {
            content: "\f071";
            color: $color-red;
        }
    }
    &.warning {
        &:before {
            content: "\f071";
            color: $color-yellow;
        }
    }
    &.success {
        &:before {
            content: "\f087";
            color: $color-green;
        }
    }
    &.info {
        &:before {
            content: "\f06a";
            color: $color-white;
        }
    }
}
