@import "variables.10ft";
@import './style';

core-details-meta-data {
    .drawer-details {
        .recording-icon {
            line-height: $font-size-bigger * 2;
            i {
                font-size: $font-size-bigger;
            }
        }
    }
}

@include scale-for-720p {
    core-details-meta-data {
        .drawer-details {
            .title {
                margin-bottom: 7px;
            }

            .recording-icon {
                font-size: 17px;
                margin-right: 7px;

                i {
                    font-size: 12px;
                }
            }

            .episode-name {
                margin-bottom: 7px;
                padding-right: 20px;
            }

            .expiration-date {
                padding-top: 12px;
            }

            .meta-data {
                line-height: 23px;

                span:before {
                    margin: 0 7px 0 3px;
                }
            }
        }

        .tomato {
            height: 16px;
            width: 16px;
            margin-top: -13px;
        }

        .rt-score-icon {
            width: 17px;
        }
    }
}
