@import "variables.10ft";
@import './style';
@import '../../styles/variables';

core-modal-in-app-upgrade {

    .core-modal .modal-title {
        font-size: 45px;
        text-transform: none;
        line-height: normal;
    }

    .modal-body {
        width: 45vw;
    }

    div.modal-body p.modal-description {
        font-size: 3.3vmin;
        padding-bottom: 2vmin;
        line-height: normal;
    }

    div.modal-body p.modal-fine-print {
        font-size: 2.3vmin;
        font-weight: 300;
        padding-bottom: .5vmin;
    }

    .terms-of-service-button {
        color: $color-near-white;
        margin: 0;
        padding: 0;
        border: 0;
        background: transparent;
        outline: none;
        text-decoration: none;
        font-size: 2.3vmin;
        cursor: pointer;

        &.core-focused-item {
            font-size: 2.3vmin;
            color: $color-dark-blue;
            text-decoration: underline;
        }
    }

    .modal-footer .btn {
        text-transform: uppercase;
    }
}
