@import "variables.10ft";
@import '../../../../styles/mixins.scss';
@import './ManageViewingHistoryHeader';

.manage-viewing-history-container {

    .manage-viewing-history-header {
        height: 12.96vh;
        padding: 3.7vh 2.2vw;
        position: fixed;
        width: 100vw;
        z-index: 3;

        .manage-viewing-history-header-title {
            font-size: 3.33vh;
            line-height: 5.56vh;
            letter-spacing: 1.5px;
        }

        .manage-viewing-history-header-button-container {
            width: 11vw;

            .manage-viewing-history-header-button {
                width: 9.4vw;
                height: 5.6vh;
                margin-left: 1.6vw;
                font-size: 2.4vh;
                font-weight: 500;
                &.core-focused-item {
                    @include focused-box();
                }
            }
        }
    }
}
