@import "variables.10ft";
@import './style';
@import '../../../styles/mixins';

core-dvr-settings {
    .setting-section {
        max-width: 1200px;
        h3 {
            font-size: $font-size-biggest2;
            font-weight: 500;
            margin-bottom: 30px;
        }
    }

    core-dvr-status-bar {
        margin-bottom: 50px;
        margin-left: 0;
    }

    p {
        margin-top: 20px;
        font-size: $font-size-bigger2;
        font-weight: normal;
        line-height: normal;
    }

    .btn.core-focused-item {
        color: $color-white;
    }

    .setting-sub-header {
        font-size: $font-size-bigger2;
    }

    .settings-manage-btn {
        width: 213px;
        height: 75px;
        background-color: $color-medium-light-gray;
        color: $color-off-white;
        padding: 9px 18px 20px;
        font-weight: 500;
        font-size: $font-size-bigger2;

        &:active,
        &.core-focused-item {
            padding: 9px 18px 20px;
            font-size: $font-size-bigger2;
            color: $color-white;
            font-weight: 500;
        }
    }
}

@include scale-for-720p {
    core-dvr-settings {
        .setting-section {
            max-width: 792px;
            h3 {
                font-size: $font-size-medium;
            }
        }

        core-dvr-status-bar {
            margin-bottom: 33px;
        }

        p {
            margin-top: 9px;
            font-size: $font-size-small;
        }

        .setting-sub-header {
            font-size: $font-size-medium;
        }

        .settings-manage-btn {
            width: 150px;
            height: 50px;
            padding: 6px 11px 13px;
            font-size: $font-size-small;

            &:active,
            &.core-focused-item {
                padding: 6px 11px 13px;
                font-size: 20px;
            }
        }
    }
}
