@import "variables.10ft";
@import '../../../../styles/mixins';

// LG/10ft styling for the grid guide date filter modal
core-date-filter-modal {
    .core-modal {
        width: 600px;

        .modal-header {
            margin-bottom: 40px;
            text-align: center;
        }
    }

    .btn {

        &.active {
            background-color: $color-near-gray !important;
            color: $color-cool-white;

            &.has-hover {
                background-color: $color-near-gray;
            }

            &.focus-grid-focused {
                background-color: $color-dark-blue;
            }
        }

        &:hover {
            background-color: $color-dark-blue;
            color: $color-white;
        }

        &.has-hover {
            background-color: $color-near-gray;
            color: $color-white;
            text-decoration: none;
        }

        // This is added by the focus system
        &.core-focused-item {
            background-color: $color-dark-blue !important;
        }
    }
}

core-modal-close-btn {
    display: none;
}

@include scale-for-720p {
    core-date-filter-modal {
        .core-modal {
            width: 400px;

            .modal-header {
                margin-bottom: 27px;
            }

            button.btn {
                height: 50px;
            }
        }
    }
}
