@import "variables.10ft";
@import './style';

// Content overrides for UI_TYPE 10ft (aka LG).
// These build on the base styles in style.scss.
core-radio {
    label {
        line-height: 44px;
        font-size: 28px;
    }

    // 10ft has an inner circle always visible
    i.radio-circle.inner {
        height: 32px;
        min-width: 32px;
        top: 6px;
        left: 6px;
        background-color: $color-off-white;
        border: 7px solid $color-off-white;
    }

    // 10ft outer circle ring is conditionally visible (hover/focus)
    i.radio-circle.outer {
        display: inline-block;
        position: absolute;
        top: 0;
        left: 0;
        height: 44px;
        width: 44px;
        background-color: transparent;
        border-radius: 50%;
        box-sizing: border-box;
        z-index: -1;
        border: 2px solid transparent;
    }

    // 10ft space the text to allow the outer circle some room
    span.radio-text {
        margin-left: 26px;
        color: $color-off-white;
    }

    // On hover, light the outer circle
    &:hover {
        i.radio-circle.outer {
            border-color: $color-white;
        }
        span {
            color: $color-white;
        }
    }

    // When checked the inner yellow circle gets a white dot
    input[type='radio']:checked + label .radio-circle.inner {
        border-color: $color-yellow;
        background-color: $color-yellow;
        &:before {
            width: 16px;
            height: 16px;
            margin: 1px;
            background-color: $color-white;
        }
    }

    // LG has a focus state, which will apply the .focus-grid-focused class to the outer core-radio component
    // You get a blue outer ring when focused.
    &.core-focused-item i.radio-circle.outer {
        border-color: $color-light-blue;
    }

    // No disabled state I can find on this platform for radio buttons,
    // so none here.
}

@include scale-for-720p {
    core-radio {
        label {
            line-height: 29px;
            font-size: 19px;
        }

        i.radio-circle.inner {
            height: 21px;
            min-width: 21px;
            top: 4px;
            left: 4px;
            border: 5px solid $color-off-white;
        }

        i.radio-circle.outer {
            height: 29px;
            width: 29px;
            border: 2px solid transparent;
        }

        span.radio-text {
            margin-left: 17px;
        }

        input[type='radio']:checked + label .radio-circle.inner {
            &:before {
                width: 11px;
                height: 11px;
                margin: 0;
            }
        }
    }
}

