.manage-viewing-history-container {

    .manage-viewing-history-header {
        background-color: $color-black;
        color: $color-white;
        display: flex;
        justify-content: space-between;

        .manage-viewing-history-header-button-container {
            display: inherit;

            .manage-viewing-history-header-button {
                background-color: $color-medium-gray;
                color: $color-white;
                line-height: inherit;
                border: none;
                outline: none;

                &:focus {
                    background-color: $color-dark-blue
                }

                &:hover {
                    background-color: $color-near-gray;
                }

                &:disabled {
                    color: $color-light-gray;
                    cursor: default;
                }
            }
        }
    }
}
