// Main guide container page
core-guide-container {
    flex: 1;
    display: block;
    position: relative;

    & > section {
        height: 100%;
    }

    // Header above the guide with channel filter, date filter, toggle
    .channel-header {
        display: flex;
        justify-content: space-between;
        flex-wrap: nowrap;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        background-color: $color-dark-gray;
        z-index: $index-front;
        // height and padding are set in platform specific sheets
    }

    .guide-content {
        position: relative;
        overflow: hidden;
    }

    core-date-filter .btn {
        margin-right: 0; // This button doesn't need a right margin.
    }
}
