@import "variables.10ft";
@import "../../../styles/base";
@import "../../../styles/base.hd";
@import "../../../styles/base.win10";

.ds-radio {
    float: left;
    width: 100%;
    outline: 0;

    .radio {
        display: none;
    }

    // inactive
    .radio + label {
        .radio-wrapper {
            background-color: $color-light-gray;
            margin-top: 5px;
            .fa-circle {
                color: $color-overlay-invisible;
                font-size: $radio-height;
                position: absolute;
            }
        }

        .label-name {
            margin-left: 20px;
            color: $color-off-white;
        }

        &.has-hover {
            cursor: pointer;

            .radio-wrapper {
                box-shadow:0px 0px 0px 3px $color-black inset;
                border: 4px solid $color-white !important;
                height: $tv-font-size-biggest;
                width: $tv-font-size-biggest;
                margin-left: -6px;
            }

            .label-name {
                margin-left: 14px;
            }
        }
    }

    // active
    .radio:checked + label {
        .radio-wrapper {
            border-color: $color-yellow;
            background-color: $color-yellow;

            .fa-circle {
                color: $color-white;
                @include font-size(medium);;
            }
        }

        .label-name {
            color: $color-white;
        }

        &.has-hover {
            cursor: pointer;

            .radio-wrapper {
                border-color: $color-white;
                height: $tv-font-size-biggest;
                width: $tv-font-size-biggest;
                margin-left: -6px;
                margin-top: -2px;
            }

            .label-name {
                margin-left: 14px;
            }
        }
    }

    // disabled
    &.disabled {
        .radio + label {
            .radio-wrapper {
                border-color: $color-gray;
                .fa-circle {
                    color: $color-overlay-invisible;
                }
            }

            .label-name {
                color: $color-gray;
            }

            &.has-hover {
                cursor: not-allowed;

                .radio-wrapper {
                    margin-left: -6px;
                }

                .label-name {
                    margin-left: 8px;
                }
            }
        }
    }

    // focused (TVs)
    &.core-focused-item {
        .radio + label {
            .radio-wrapper {
                border-color: $color-blue;
                box-shadow:0px 0px 0px 3px black inset;
                height: $tv-font-size-biggest;
                width: $tv-font-size-biggest;
                margin-left: -6px;
                margin-top: -2px;
            }

            .label-name {
                margin-left: 14px;
            }
        }
    }

    .radio-wrapper {
        display: inline-block;
        @include border-radius(50px);
    }

    .label-name {
        font-weight: normal;
        @include font-size(medium);
        display: inline-block;
        vertical-align: top;
    }
}

.platform-win10 {
    .ds-radio {
        .radio + label {
            .radio-wrapper {
                .fa-circle {
                    margin-top: 1.75px;
                    margin-left: 2.25px;
                }
            }
        }

        &.core-focused-item {
            .radio + label {
                .radio-wrapper {
                    border-color: $color-yellow;
                }
                &.has-hover {
                    .radio-wrapper {
                        border-color: $color-white;
                    }
                }
            }
        }
    }
}

@include scale-for-tv {

    .ds-radio {

        &.core-focused-item {
            .radio + label {
                .radio-wrapper {
                    border-color: $color-blue;
                }
            }
        }

        .radio + label {
            .radio-wrapper {
                border: 4px solid $color-light-gray;
                height: $tv-font-size-big;
                width: $tv-font-size-big;
            }

            .fa-circle {
                font-size: $tv-font-size-biggest - 9px;
            }

            .label-name {
                @include font-size(bigger2);
                margin-left: 20px;
                padding-top: 4px;
                margin-bottom: 35px;
            }
        }
    }
}

@include scale-for-hd-tv {
    .ds-radio {
        .radio + label {
            .radio-wrapper {
                margin-top: 3px;
                border: 2px solid $color-light-gray;
                height: $tv-font-size-big-hd-tv;
                width: $tv-font-size-big-hd-tv;

                .fa-circle {
                    font-size: $radio-height-hd-tv;
                }
            }

            .label-name {
                margin-left: 13px;
                @include font-size(bigger2);
                padding-top: 2px;
                margin-bottom: 23px;
            }

            &.has-hover {
                .radio-wrapper {
                    box-shadow:0px 0px 0px 2px $color-black inset;
                    border: 3px solid $color-white !important;
                    height: $tv-font-size-biggest-hd-tv;
                    width: $tv-font-size-biggest-hd-tv;
                    margin-left: -4px;
                }

                .label-name {
                    margin-left: 9px;
                }
            }
        }

        .radio:checked + label {
            &.has-hover {
                .radio-wrapper {
                    height: $tv-font-size-biggest-hd-tv;
                    width: $tv-font-size-biggest-hd-tv;
                    margin-left: -4px;
                    margin-top: -1px;
                }

                .label-name {
                    margin-left: 9px;
                }
            }
        }

        &.disabled {
            .radio + label {
                &.has-hover {
                    .radio-wrapper {
                        margin-left: -4px;
                    }

                    .label-name {
                        margin-left: 5px;
                    }
                }
            }
        }

        &.core-focused-item {
            .radio + label {
                .radio-wrapper {
                    border-color: $color-blue;
                    box-shadow:0px 0px 0px 2px black inset;
                    height: $tv-font-size-biggest-hd-tv;
                    width: $tv-font-size-biggest-hd-tv;
                    margin-left: -4px;
                    margin-top: -1px;
                }

                .label-name {
                    margin-left: 9px;
                }
            }
        }

        .radio-wrapper {
            @include border-radius(33px);
        }
    }
}
