@import '../../../../styles/mixins.scss';

core-item-thumbnail {
    .core-item-thumbnail {
        display: flex;
        justify-content: center;
        position: relative;
        height: 100%;

        .image-wrapper {
            overflow: hidden;
            background-color: $color-gray;
            display: flex;
            height: 100%;
            justify-content: center;
            position: relative;
            width: 100%;

            img {
                width: 100%;
                height: 100%;
                position: relative;
                /* Styling incase of broken img */
                font-family: 'Sling-Actions';
                -webkit-font-smoothing: antialiased;
                -moz-osx-font-smoothing: grayscale;
            }
            img:before {
                /* Content for broken img - displayed only when
                    image url returns 404 */
                content: "\e91d";
                display: flex;
                align-items: center;
                justify-content: center;
                height: 100%;
                width: 100%;
                color: #222;
                background-color: #444;
                border: none 0;
                outline: 0;
                font-size: 92px;
            }
        }

        .no-image-wrapper {
            height: 100%;
            display: flex;
            font-size: 92px;
            color: $color-off-white;
            background: $color-gray;
            align-items: center;
            justify-content: center;
        }

        &:hover .no-image-wrapper i:before {
            color: $color-dark-gray;
        }

        .background-thumbnail {
            background-size: cover;
            background-position: center center;
            height: 100%;
            opacity: 0.3;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
        }
    }

    .core-item-thumbnail.person {
        img {
            height: $asset-image-height;
            width: 100%;
        }
        img:before {
            /* Content for broken img - displayed only when
                person image url returns 404 */
            content: "\e801";
        }
    }
}
