@import "variables.10ft";
@import '../../styles/mixins';

#toast-container {
    position: fixed;
    z-index: 999999;
    margin-top: 23.5%;

    /*overrides*/
    &.toast-center, &.toast-top-center, &.toast-bottom-center {
        width: 100%;
    }
    &.toast-center > div, &.toast-top-center > div, &.toast-bottom-center > div {
        margin: auto;
    }
}

.toast {
    @include font-size(medium);
    background-position: 15px center;
    background-repeat: no-repeat;
    box-shadow: 0 0 12px #999;
    display: block;
    margin: 0 auto;
    color: $color-white;
    padding: 20px 80px;
    width: 50%;
    background-color: $color-overlay-dark-gray;
    background-image: none !important; // this hurts my soul but they do it in Angular Toast css
    min-height: 40px;
    @include border-radius(5px);

    &:before {
        font-family: fontAwesome;
        @include font-size(big);
        float: left;
        position: absolute;
        margin-left: -60px;
    }

    .toaster-error-code {
        color: $color-light-gray;
        margin-left: 15px;
    }

    &.error {
        &:before {
            content: "\f071";
            color: $color-red;
        }
    }
    &.warning {
        &:before {
            content: "\f071";
            color: $color-yellow;
        }
    }
    &.success {
        &:before {
            content: "\f087";
            color: $color-green;
        }
    }
    &.info {
        &:before {
            content: "\f06a";
            color: $color-white;
        }
    }

    .lock-ui {
        width: 100%;
        height: 100%;
        position: fixed;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        z-index: $index-back;
        background-color: $color-overlay-black;
    }

    @include scale-for-responsive {
        width: 98%;
        max-width: 550px;
        padding: 10px 20px;
        font-size: 16px;

        &:before {
            display: none;
        }
    }

}

$toastPositionTop: 12px;
$toastPositionLeft: 12px;
$toastPositionRight: 12px;
$toastPositionBottom: 12px;

.toast-top-left {
    top: $toastPositionTop;
    left: $toastPositionLeft;
}

.toast-top-center {
    top: $toastPositionTop;
}

.toast-top-right {
    top: $toastPositionTop;
    right: $toastPositionRight;
}

.toast-bottom-right {
    right: $toastPositionRight;
    bottom: $toastPositionBottom;
}

.toast-bottom-center {
    bottom: $toastPositionBottom;
}

.toast-bottom-left {
    bottom: $toastPositionBottom;
    left: $toastPositionLeft;
}

.toast-center {
    top: 45%;
}

@include scale-for-mobile {
    .toast {
        font-size: map_get($font-size-map-min, smaller);
    }
}
