@import '../../../styles/mixins.scss';

// Shared styling attributes for the core-scrolling-ribbon, assets and arrows
core-scrolling-ribbon {
    // Title above each ribbon
    h2.ribbon-title {
        color: $color-white;
        letter-spacing: 0.4px;
        // line-height is set in platform specific style sheets
        // margin is set in platform specific style sheets

        // Optional message in yellow text within the ribbon title
        span.message {
            // font-size is set in platform stylesheets
            color: $color-yellow;
            text-transform: none;
            margin-left: 20px;
            font-weight: 400;
        }
    }
    .core-ribbon-section {
        position: relative;
        min-height: $asset-item-height;
    }

    .core-ribbon-container {
        // eliminate unwanted vertical space above and below inline flex container
        // line-height is specified in the ribbon items
        line-height: 0;
    }

    .ribbon-content {
        display: inline-flex;
        flex: 1;
    }

    &.scrolling-ribbon__empty .ribbon-content::after {
        background: repeating-linear-gradient(to right, rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0.25) 315px, transparent 315px, transparent 330px);
        content: '';
        flex: 1;
    }

    &.favorite-channels-ribbon .core-ribbon-section{
        min-height: $my-channels-asset-tile-height;
    }

    .core-spinner {
        min-height: $asset-item-height;
    }

    .core-ribbon__error {
        @include full-absolute();
        padding: $asset-item-height / 2;
        color: $color-yellow;
        text-align: center;
        font-weight: 300;
    }
}
