@import '../../../../styles/variables';
@import '../../../../styles/mixins.scss';

core-screen-tile-standard {
    li.standard-tile {
        list-style-type: none;
        height: $asset-item-height;
        width: $asset-item-width;
        cursor: pointer;
        position: relative;
        overflow: hidden;

        .thumbnail-wrapper {
            height: $asset-image-height;
            background-color: $color-gray;
            position: relative;
        }

        div.delete-icon {
            @include position($left: auto);
            color: $color-white;
            background-color: $color-overlay-dark-gray;
            width: 25px;
            height: 25px;
            line-height: 25px;
            visibility: hidden;
            opacity: 0.8;
            font-size: 36px;
            font-weight: 100;
            text-align: center;
            z-index: 1;
            &:hover { opacity: 1; }
        }

        &:hover {
            .title {
                color: $color-white;
            }
            div.delete-icon {
                visibility: visible;
            }

            .icon-quick-play {
                display: initial;
            }
        }

        .sub-content {
            @include black-box($asset-border-size);
            // font-size is set in platform specific sheets
            height: $asset-metadata-height;
            padding: 0 15px;
            position: absolute;
            width: 100%;
            border: none;

            .icon-record-single {
                padding-right: 5px;
            }

            .protected {
                color: $color-yellow;
            }

            dd,
            dt {
                @include ellipsis-able();
                line-height: normal;
                width: 100%;
            }
        }

        &:hover .sub-content {
            @include hovered-box();
        }

        &:focus .sub-content {
            @include focused-box();
        }

        dt {
            color: $color-near-white;
            display: block;
            @include ellipsis-able();
            // font-size is set in platform specific style sheets
            font-weight: 500;
            // margin is set in platform specific style sheets
            padding: 0;
            text-align: left;
            width: 100%;
            max-width: $asset-item-width;
        }

        .meta-data, span.sub-content--details {
            @include ellipsis-able();
            width: 100%;
        }

        dd {
            display: inline;
        }

        .sub-content core-screen-tile-attribute.last dd:after {
            content: "";
            padding-left: 0;
        }

        .important {
            color: $color-yellow;
        }

        i {
            // font-size is set in platform specific style sheets
            // top is set in platform specific style sheets
            position: relative;
        }

        .asset-locked-overlay {
            background: $color-overlay-black;
            @include full-absolute();
            @include flex-center();
        }
    }
}