@import "variables.10ft";
@import "../../../styles/base";
@import "../../../styles/base.win10";

.ds-checkbox {
    float: left;
    width: 100%;
    outline: 0;

    .checkbox {
        display: none;
    }

    // inactive
    .checkbox + label {
        .checkbox-wrapper {
            height: $checkbox-height;
            width: $checkbox-width;
            background-color: $color-overlay-invisible;
            border: 2px solid $color-light-gray;
            display: inline-block;
            margin-top: 6px;

            .fa-check {
                color: $color-overlay-invisible;
                font-size: $checkbox-height - 1px;
                position: absolute;
                margin-top: -1px;
                margin-left: -1px;
            }
        }

        .label-name {
            font-weight: normal;
            @include font-size(medium);
            margin-left: 10px;
            display: inline-block;
            vertical-align: top;
            color: $color-off-white;
        }

        &:hover {
            cursor: pointer;

            .checkbox-wrapper {
                border-color: $color-white;
            }
        }
    }

    // active
    .checkbox:checked + label {
        .checkbox-wrapper {
            background-color: $color-yellow;
            border-color: $color-yellow;

            .fa-check {
                color: $color-black;
            }
        }

        .label-name {
            color: $color-white;
        }

        &:hover {
            cursor: pointer;

            .checkbox-wrapper {
                border-color: $color-white;
            }
        }
    }

    // disabled
    &.disabled {
        .checkbox + label {
            .checkbox-wrapper {
                border-color: $color-gray;
                background-color: $color-overlay-invisible;

                .fa-check {
                    color: $color-overlay-invisible;
                }
            }

            .label-name {
                color: $color-gray;
            }

            &:hover {
                cursor: not-allowed;

                .checkbox-wrapper {
                    border-color: $color-gray;
                }
            }
        }
    }

    // focused (TVs)
    &.core-focused-item {
        .checkbox + label {
            .checkbox-wrapper {
                border-color: $color-blue;
            }
        }
    }
}

.platform-win10 {
    .ds-checkbox {
        &.core-focused-item {
            .checkbox + label {
                .checkbox-wrapper {
                    border-color: $color-yellow;
                }
                &:hover {
                    .checkbox-wrapper {
                        border-color: $color-white;
                    }
                }
            }
        }
    }
}

@include scale-for-tv {
    .ds-checkbox {

        &.core-focused-item {
            .checkbox + label {
                .checkbox-wrapper {
                    border-color: $color-blue;
                }
            }
        }

        .checkbox + label {
            .checkbox-wrapper {
                height: $checkbox-height * 2;
                width: $checkbox-width * 2;
                border: 4px solid $color-light-gray;
                margin-top: 0;
                margin-bottom: 25px;

                .fa-check {
                    font-size: $checkbox-height * 2;
                    position: absolute;
                    margin-top: -4px;
                    margin-left: -4px;
                }
            }

            .label-name {
                font-weight: normal;
                @include font-size(bigger2);
                margin-left: 20px;
                vertical-align: top;
                margin-top: 4px;
            }
        }
    }
}

@include scale-for-hd-tv {
    .ds-checkbox {

        &.core-focused-item {
            .checkbox + label {
                .checkbox-wrapper {
                    border-color: $color-blue;
                }
            }
        }

        .checkbox + label {
            .checkbox-wrapper {
                height: $checkbox-height-hd-tv * 2;
                width: $checkbox-width-hd-tv * 2;
                border: 2.5px solid $color-light-gray;
                margin-top: 0;
                margin-bottom: 17px;

                .fa-check {
                    font-size: $checkbox-height-hd-tv * 2;
                    position: absolute;
                    margin-top: -2px;
                    margin-left: -2px;
                }
            }

            .label-name {
                font-weight: normal;
                font-size: 19px;
                margin-left: 13px;
                vertical-align: top;
                line-height: 27px;
            }
        }
    }
}
