@import "variables.10ft";
@import '../details.variables.10ft';
@import '../../../styles/mixins';
@import './style';

core-series-details {
    .btn + core-record-button > .btn {
        margin-left: 20px;
    }

    .btn {
        &:hover {
            // fav icon hover state color
            .fa-heart:before {
                color: $color-pink;
            }
        }

        &.primary,
        &.active,
        &.core-focused-item {
            // fav icon hover state color
            .fa-heart:before {
                color: $color-pink;
            }
        }
    }

    .season-list {
        position: relative;
        overflow-x: scroll;

        .horizontal-scroll-container {
            white-space: nowrap;
        }
    }

    .season-menu {
        font-size: $details-font-size;
        text-transform: uppercase;
    }

    .season-menu-content {
        padding-top: 10px;
        padding-bottom: 20px;
    }

    .season-menu-label {
        margin-right: 30px;
        padding-bottom: 15px;
        padding-top: 15px;
        border-bottom: 3px solid transparent;
        min-width: 120px;

        &:hover {
            border-bottom: 3px solid;
        }
    }

    .season-number-wrapper {
        padding: 15px 25px;
        border-bottom: 3px solid transparent;

        &.core-focused-item {
            @include focused-box();
        }

        &:hover {
            border-bottom: 3px solid;
        }
    }

    .episode {
        font-size: $details-font-size;
        padding: 15px 30px 35px $details-side-gutter;

        &:hover {
            border-color: $color-white;
            background-color: $color-black;
        }

        &:hover a {
            color: $color-white;
            text-decoration: none;
        }
    }

    .episode-description {
        font-size: $details-font-size;
        line-height: normal;
        padding-bottom: 0;
        padding-left: 30px;
    }

    .episode-button-row {
        margin: 20px $details-side-gutter 0 30px;
        .btn {
            padding-top: 22px;
            &.core-focused-item {
                @include focused-box();
            }
        }
    }
}

@include scale-for-720p {
    core-series-details {
        .btn + core-record-button > .btn {
            margin-left: 13px;
        }

        .season-menu {
            font-size: $details-font-size-720p;
            padding: 0 $details-side-gutter-720p;
        }

        .season-menu-content {
            padding-top: 7px;
            padding-bottom: 13px;
        }

        .season-menu-label {
            margin-right: 20px;
            padding-bottom: 10px;
            padding-top: 10px;
            border-bottom-width: 2px;
            min-width: 80px;

            &:hover {
                border-bottom-width: 2px;
            }
        }

        .season-number-wrapper {
            padding: 10px 17px;
            border-bottom-width: 2px;

            &:hover {
                border-bottom-width: 2px;
            }
        }

        .episode {
            font-size: $details-font-size-720p;
            padding: 10px 20px 23px 33px;
        }

        .episode-content .episode-description {
            font-size: $details-font-size-720p;
            padding-left: 20px;
            line-height: normal;
        }

        .episode-content .episode-button-row {
            margin: 13px 33px 0 20px;

            .btn,
            &.core-focused-item {
                &.no-min-width {
                    min-width: 0;
                }
            }
        }
    }
}
