@import "../../styles/mixins.scss";

// These styles pertain to the main-nav items, and the main-nav container itself
core-main-nav {
    background-color: $color-black;
    color: $color-warm-gray;
    font-size: $font-size-smallest;
    font-weight: bold;
    // height is set in platform specific sheets
    letter-spacing: 0.8px;
    z-index: 1;

    @include disable-select;

    .dashboard-menu-item {
        align-items: center;
        cursor: pointer;
        display: flex;
        height: 100%;
        opacity: 0.5;
        span {
            color: $color-white;
            font-weight: 500;
            letter-spacing: 0.8px;
            padding: 0 30px;
            transition: opacity $hover-transition-speed, filter $hover-transition-speed;
            text-decoration: none;
            white-space: nowrap;
        }
        &:focus {
            @include focused-nav;
            span {
                margin-bottom: -2px;
            }
        }
        &.active {
            @include active-nav;
        }
        &.settings {
            display: none;
        }
    }

    .icon-wrapper, .settings-link {
        opacity: 0.5;
        transition: opacity $hover-transition-speed;
        svg g {
            fill: $color-white;
        }
        &:hover, &:focus, &.active {
            background-color: transparent;
            opacity: 1;
            outline: none;
            border: none;
            transition: opacity $hover-transition-speed;
        }
        &:focus {
            svg g {
                transition: fill $hover-transition-speed;
                fill: $color-blue;
            }
        }
    }

    .top-nav-bar {
        .sling-logo {
            &.fallback-logo {
                content: url("../../../assets/images/sling_logo.svg");
            }
        }
    }

    // TODO WEB-3500 Fix 3ft/Browser navigation, which is where this gets used
    .main-nav-status-container {
        cursor: pointer;
        display: none;
        height: $nav-dimensions-desktop;
        line-height: $nav-dimensions-desktop;

        // Hamburger menu icon
        i {
            font-size: 22px;
            float: left;
            height: 100%;
            line-height: $nav-dimensions-desktop;
            text-align: center;
            width: $nav-dimensions-desktop;
        }
    }

    .offscreen-text {
        @include offscreen-accessible-element();
    }

    @include scale-for-responsive {
        height: 100vh;
        // width is set in platform specific sheets

        .main-nav-status-container {
            display: block;
            height: $nav-dimensions-tablet;
            line-height: $nav-dimensions-tablet;

            i {
                line-height: $nav-dimensions-tablet;
                width: $nav-dimensions-tablet;
            }
        }
    }
}
