@import "variables.10ft";
// https://wiki.movenetworks.com/pages/viewpage.action?pageId=73433504
@import "../base";

/* This is a non bootstrap class on purpose so we don't inherit any styles from bootstrap, especially pseudo classes. TODO - remove bootstrap CSS completely */
.btn-dark {
    box-sizing: border-box;
    @include font-size(bigger2);
    position: relative;
    text-align: center;
    border: none;
    background-color: $color-black;
    padding: 18px 32px;
    min-width: $button-short-width;
    display: inline-block;
    vertical-align: middle;
    height: 75px;
    line-height: 1.42857143;
    outline: none;
    color: $color-white;

    &.has-hover {
        background-color: $color-hover-gray;
    }

    &:active,
    &.core-focused-item {
        background-color: $color-dark-blue;
    }

    &:active,
    &:focus {
        outline: none;
    }
}

.btn,
.btn:focus {
    @include font-size(bigger2);
    box-sizing: border-box;
    position: relative;
    text-align: center;
    text-decoration: none;
    border-radius: 0;
    border: none;
    min-width: $button-short-width;
    min-height: $button-height;
    color: $color-near-off-white;
    background-color: $color-disabled;
    padding: 18px 30px;
    height: 75px;
    outline: none;

    &.has-hover {
        background-color: $color-mid-gray;
        color: $color-white;
    }

    &:active,
    &.core-focused-item {
        background-color: $color-dark-blue;
        color: $color-white;
    }

    &.button-short {
        min-width: $button-short-width;
        width: $button-short-width;
    }

    // for inline spinners on actions
    .fa-spinner {
        @include font-size(bigger);
        color: $color-white;
        position: absolute;
        top: 6px;
    }

    &.no-min-width {
        min-width: 0;
    }
}

.btn-keyboard {
    border-radius: 0;
    background-color: $color-cool-gray;
    color: $color-white;
    border: none;
    border-bottom: 3px solid transparent;
    @include border-radius(5px);
    height: 50px;
    margin: 20px 0;
    letter-spacing: 0;
    min-width: 0;

    &.has-hover {
        background-color: $color-hover-gray;
        border-color: $color-hover-gray;
    }

    &:active,
    &.core-focused-item {
        color: $color-white;
        background-color: $color-dark-blue;
        border-bottom: 3px solid $color-light-blue;
        outline: none;
        text-decoration: none;
    }
}

/* Square buttons -> 75px by 75px. the info button and favor button are examples of these */
.icon-btn {
    width: 75px;
    height: 75px;
    line-height: 75px;
    padding: 0;
    min-width: 0;
    outline: none;
}

.dev-btn {
    margin: 0 5px 5px 0;
}

//For buttons that act like links
.link {
    margin: 0;
    padding: 0;
    border: 0;

    background: transparent;
    font-size: inherit;
}

@include scale-for-hd-tv {

    .btn-dark {
        padding: 12px 22px;
        min-width: $button-short-width-hd-tv;
        line-height: 0.94714;
        height: 50px;
    }

    .btn,
    .btn:focus {
        min-width: $button-short-width-hd-tv;
        min-height: $button-height-hd-tv;
        padding: 12px 20px;
        height: 50px;

        &.button-short {
            min-width: $button-short-width-hd-tv;
            width: $button-short-width-hd-tv;
        }

        .fa-spinner {
            top: 4px;
        }
    }

    .btn-keyboard {
        border-bottom: 2px solid transparent;
        height: 34px;
        margin: 14px 0;

        &:active,
        &.core-focused-item {
            border-bottom: 2px solid $color-light-blue;
        }
    }

    .icon-btn {
        width: 50px;
        height: 50px;
        line-height: 50px;
        padding: 0;
        min-width: 0;
        outline: none;
    }

    .dev-btn {
        margin: 0 4px 4px 0;
    }
}
