@import "variables.10ft";
@import '../ribbon-system.variables.10ft';
@import './style';

core-ribbon-utility-tile {
    .utility-tile {
        font-size: 24px;
        .utility-icon {
            font-size: 80px;
        }
    }

    // shorter little utility tiles in the new My Channels ribbon
    &.favorite-channels__utility-tile .utility-tile {
        height: $my-channels-asset-tile-height;
    }
}

@include scale-for-720p {
    core-ribbon-utility-tile {
        .utility-tile {
            height: $asset-item-height-720p;
            width: 100%;
            margin-right: $asset-item-spacer-720p;
            text-align: center;
            font-size: 16px;

            .utility-icon {
                font-size: 53px;
                margin-bottom: 7px;
            }
        }

        &.favorite-channels__utility-tile .utility-tile {
            height: $my-channels-asset-tile-height-720p;
        }
    }
}
