@import "../../../../styles/mixins";

// Outer container for the edit channels screen
core-edit-my-channels {
    @include position($position: fixed);
    background: $color-dark-gray;

    section.edit-my-channels-grid {
        overflow: auto;
        display: flex;
        flex-direction: column;
        flex: 1;

        p.edit-my-channels-instruction {
            display: inline-block;
            font-size: $font-size-medium;
            font-weight: normal;
            color: $color-off-white;
        }
    }


    // Box all the channel tiles live in
    #edit-my-channels-tiles-container {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        align-content: flex-start;
        justify-content: flex-start;

        .channel-tile {
            margin-bottom: $asset-item-spacer;
            margin-right: $asset-item-spacer;
            position: relative;
            height: $asset-image-height;
            width: $asset-item-width;
        }

        core-ribbon-item-my-channels {
            &.active {
                .check-mark {
                    i {
                        display: block;
                    }
                }
            }
        }

        core-tile-play-icon {
            display: none;
        }
    }
}
