@import "variables.10ft";
@import 'style';

core-parental-controls .parental-controls {
    .clickable-row, .clickable-row-disabled {
        cursor: pointer;
        height: 36px;
        margin: 36px 0;
        outline: none;
    }
    .toggle-header {
        margin: 0 0 36px 0;

        label {
            font-size: $font-size-biggest2;
            font-weight: 500;
            color: $color-white;
        }
        span {
            font-size: $font-size-bigger2;
        }
    }

    core-toggle {
        margin-right: 10px;
    }

    hr {
        height: 2px;
    }

    .setting-sub-header {
        font-size: $font-size-biggest2;
        font-weight: 500;
    }

    .toggle-rating, .lock-purchase {
        label, span {
            font-size: $font-size-bigger2;
            font-weight: normal;
        }
    }

    .change-pin-btn {
        width: 250px;
        height: 75px;
        background-color: $color-medium-light-gray;
        color: $color-off-white;
        padding: 9px 18px;
        font-weight: 500;
        font-size: $font-size-bigger2;

        &:active,
        &.core-focused-item {
            color: $color-white;
        }
    }

    span.toggle-status {
        font-size: $font-size-bigger;
        padding: 2px;
        font-weight: normal;
        text-align: left;
    }
}

@include scale-for-720p {
    core-parental-controls .parental-controls {
        .clickable-row, .clickable-row-disabled {
            height: 22px;
            margin: 21px 0;
        }
        .toggle-header {
            label {
                font-size: $font-size-medium;
            }
            span {
                font-size: $font-size-small;
            }
        }

        core-toggle {
            margin-right: 6px;
        }

        hr {
            height: 1px;
        }

        .setting-sub-header {
            font-size: $font-size-medium;
        }

        .toggle-rating, .lock-purchase {
            label, span {
                font-size: $font-size-small;
            }
        }

        .change-pin-btn {
            width: 150px;
            height: 45px;
            padding: 5px 11px;
            font-size: $font-size-small;

            &:active,
            &.core-focused-item {
                padding: 5px 11px;
                height: 45px;
            }
        }

        span.toggle-status {
            font-size: $font-size-small;
            padding: 1px;
        }
    }
}
