@import "variables.10ft";
@import "../base";

#toast-container.custom-toast {
    margin-top: 23.5%;

    div {
        opacity: 1;
    }

    // Default toast fade animations seem way too slow
    div.ng-leave, div.ng-enter {
        transition-duration: 0.3s;
    }


    .toast {
        @include font-size(big3);
        color: $color-white;
        padding: 20px;
        width: 50%;
        background-color: $color-black;
        background-image: none !important; // this hurts my soul but they do it in Angular Toast css
        border: 3px solid $color-gray;
        min-height: 40px;
        padding-left: 80px;
        margin-bottom: 12px;
        box-shadow: none;
        border-radius: 0;

        &:before {
            font-family: fontAwesome;
            @include font-size(big);
            float: left;
            position: absolute;
            margin-left: -60px;
        }

        .toaster-error-code {
            color: $color-light-gray;
            margin-left: 15px;
        }
    }
    .toast-error {
        &:before {
            content: "\f071";
            color: $color-red;
        }
    }
    .toast-warning {
        &:before {
            content: "\f071";
            color: $color-yellow;
        }
    }
    .toast-success {
        &:before {
            content: "\f087";
            color: $color-green;
        }
    }
    .toast-info {
        &:before {
            content: "\f06a";
            color: $color-white;
        }
    }

    .lock-ui {
        width: 100%;
        height: 100%;
        position: fixed;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        z-index: $index-back;
        background-color: $color-overlay-black;
    }
}

@include scale-for-tv {
    .toast {
        @include font-size(biggest);
    }
}

@include scale-for-hd-tv {
    #toast-container.custom-toast {
        .toast {
            padding: 13px;
            border: 2px solid $color-gray;
            min-height: 27px;
            padding-left: 53px;
            margin-bottom: 8px;

            &:before {
                margin-left: -40px;
            }

            .toaster-error-code {
                margin-left: 10px;
            }
        }
    }
}
