core-dvr-container-header {
    display: block;
    background: $color-black;

    // The individual headings
    span {
        text-transform: uppercase;

        // The host page or pages
        &.core-breadcrumb-primary {
            color: $color-off-white;
        }

        // The current page
        &.core-breadcrumb-current {
            color: $color-white;
        }
    }

    .btn {
        margin-right: 10px;
    }
}
