@import '../../../../../styles/variables';
@import '../../../../../styles/mixins';

core-screen-tile-progress-bar {
    .progress-line {
        @include position($top: auto);
        height: $asset-progress-bar-height;
        background-color: $color-dark-gray;
        width: 100%;

        .current-progress-line {
            height: 100%;
            background-color: $color-yellow;
            position: absolute;
        }
    }

    &.episode-progress {
        .progress-line {
            position: relative;
            border-radius: 6px;
            overflow: hidden;
            background-color: transparent;
        }

        .current-progress-line {
            background-color: $color-yellow;
        }
    }
}

