@import "variables.10ft";
@import '../../../../ribbon-system/ribbon-system.variables.10ft';
@import './style';

core-continue-watching-filter {

    button.btn {
        background-color: $color-black;
        width: 324px;

        &.focus-grid-focused {
            width: 324px;
        }

        &:focus {
            width: 324px;
        }
    }

    button.btn.selected {
        background-color: $color-medium-gray;
    }

    .continue-watching-filter-container {
        padding-bottom: 40px;
    }
}

@include scale-for-720p {
    core-continue-watching-filter {
        button.btn {
            width: 216px;

            &.focus-grid-focused {
                padding: 0;
                width: 216px;
            }

            &:focus {
                padding: 0;
                width: 216px;
            }
        }

        .continue-watching-filter-container {
            padding-bottom: 27px;
        }
    }
}
