@import "variables.10ft";
*[core-marquee-text] {
    :first-child {
        &.no-ellipsis {
            text-overflow: initial;
        }

        .scrolling {
            display: inline-block;

            &:after {
                padding-left: 30px;
                content: attr(data-content);
            }
        }
    }
}
