@import "variables.10ft";
@import "style";
@import "../../../../app/styles/mixins.scss";

core-account {

    .user-details {
        margin-top: 11px;
    }

    .accounts-button {
        .button-text-normal {
            color: $color-off-white;
        }

        .btn.core-focused-item {
            color: $color-white;
        }
    }

    .account-unsubscribed-content {
        margin-top: 4vh;

        .account-unsubscribed-content-header {
            height: auto;
            font-size: 3.25vh;
            font-weight: 500;
            color: $color-white;
            margin-bottom: 1.1vh;
            line-height: normal;
        }

        .unsubscribed-content-status, core-toggle {
            display: inline-block;
        }
    }

    @include scale-for-720p {
        .accounts-button {
            .button-text-normal {
                font-size: 21px;
            }

            .btn.core-focused-item {
                height : 7.8vh;
            }
        }

        .services-heading {
            font-size: 21px;
        }

        .user-account-details {
            .user-details {
                font-size: 21px;
            }

            .accounts-heading {
                font-size: 21px;
            }
        }
    }
}
