$border-color: #d84e4e;
$background-color: #c23030;
$arrow-size: 10px;
$outer-arrow-size: 12px;

.coreValidationToolTip {
    position: absolute;
    border: solid 2px $border-color;
    background-color: $background-color;
    padding: 15px 20px;
    margin-top: -5px;
    font-size: 21px;
    color: $color-white;
    text-align: left;
    // Larger sized triangle that acts as the border
    &:after {
        content: '';
        width: 0;
        height: 0;
        border-left: $outer-arrow-size solid transparent;
        border-right: $outer-arrow-size solid transparent;
        border-bottom: $outer-arrow-size solid $border-color;
        position: absolute;
        left: 20px;
        top: -12px;
        z-index: 2;
    }
    // inner triangle that acts as the background
    &:before {
        content: '';
        width: 0;
        height: 0;
        border-left: $arrow-size solid transparent;
        border-right: $arrow-size solid transparent;
        border-bottom: $arrow-size solid $background-color;
        position: absolute;
        left: 22px;
        top: -8px;
        z-index: 3;
    }
}
