@import "variables.10ft";
// Import local variables shared by guide/grid-guide components
@import '../grid-guide.variables.10ft';
@import './style';
@import '../../../../styles/mixins.scss';

$icon-description-font-size: 21px;
$icon-description-font-size-720p: 14px; // for HD screen(1280x720)

core-grid-item {
  i:before {
    font-size: 20px;
  }

  .schedule-item {

    &:hover {
        color: $color-off-white;
        background: inherit;

        i:before {
            color: inherit;
        }
    }

    &.on-now {
        color: $color-white;
    }

    &.has-hover {
        color: $color-white;
        background: $color-light-medium-gray;

        i:before {
          color: $color-white;
        }

        .description {
            font-size: $icon-description-font-size;
        }
    }

    &.core-focused-item:focus {
        .description {
            font-size: $icon-description-font-size;
        }
        @include focused-box();
    }
  }

  .core-focused-item:focus {
    @include focused-box();
  }
}

@include scale-for-720p {
  core-grid-item {
    i:before {
      font-size: 13px;
    }

    .schedule-item {
      height: $grid-guide-row-height-720p;

      &.has-hover {
        .description {
          font-size: $icon-description-font-size-720p;
        }
      }

      &.focus-grid-focused {
        .description {
          font-size: $icon-description-font-size-720p;
        }
      }

      .schedule-item-shell {
        padding: $grid-guide-item-padding-720p;
      }
    }
  }
}
