@import "variables.10ft";
@import '../../../styles/variables';
@import '../../../styles/mixins';

core-tile-play-icon {
    &:hover .play-icon { opacity: 1; }

    > .icon-quick-play {
        @include position(
            $position: relative,
            $bottom: 38px,
            $top: initial,
            $left: calc(100% - 38px)
        );
        transition: opacity $hover-transition-speed ease-in-out;
        display: none;
        background-color: $color-overlay-black60;
        border-radius: 32px;
        box-shadow: 0 0 6px 2px rgba(0, 0, 0, 0.6);
        font-size: 28px;
        opacity: 0.7;

        &:hover {
            font-size: 32px;
            opacity: 1;
        }
    }
}
