// Variables
$transition-duration: 0.3s;

// Toggle Switch
core-toggle {
    display: block;

    &.disabled {
        .toggle-switch {
            border-color: $color-cool-gray;

            &.on {
                background-color: $color-black;

                .slider {
                    background-color: $color-light-gray;
                }
            }
        }

        .toggle-wrapper {
            &:hover {
                border: 2px solid transparent;
            }
            &:focus {
                border: 2px solid transparent;
            }
        }
    }

    .toggle-switch {
        background-color: $color-black;
        height: 25px;
        width: 60px;
        border: 2px solid $color-off-white;
        border-radius: 50px;
        border-color: $color-off-white;
        left: 5px;
        display: inline-block;
        position: relative;
        top: 5px;
        transition: background-color $transition-duration;
        cursor: pointer;

        .slider {
            background-color: $color-light-gray;
            height: 15px;
            position: absolute;
            top: 3px;
            left: 2px;
            transition: left $transition-duration;
            display: inline-block;
            border-radius: 50px;
            width: 15px;
        }

        &.on {
            background-color: $color-yellow;
            border: none;

            .slider {
                background-color: $color-white;
                left: 42px;
                top: 5px;
            }
        }
    }

    .toggle-wrapper {
        border: 2px solid transparent;
        border-radius: 100px;
        height: 39px;
        width: 74px;
    }
}
