@import "variables.10ft";
@import '../../screen.variables.10ft';
@import '../../../../styles/mixins.scss';
@import './style';

core-screen-tile-standard {
    li.standard-tile {
        .sub-content {
            font-size: 22px;
            @include ellipsis-able();

            dt {
                font-size: 26px;
                margin: 10px 0 5px 0;
            }
        }

        i {
            font-size: 20px;
        }

        .delete-icon {
            width: 45px;
            height: 45px;
            line-height: 45px;
            font-size: 75px;
        }

        &:hover {
            .icon-quick-play {
                display: none;
            }
        }

        .asset-locked-overlay {
            background: $color-overlay-dark-gray;
            .fa-lock {
                font-size: 130px;
                color: $color-light-gray;
            }
        }

        &.focus-grid-focused dl.sub-content {
            @include focused-box();
        }
    }
}

@include scale-for-720p {
    core-screen-tile-standard {
        li.standard-tile {
            height: $asset-item-height-720p;
            width: $asset-item-width-720p;

            .thumbnail-wrapper {
                height: $asset-image-height-720p;
            }

            .sub-content {
                font-size: $asset-metadata-font-size-720p;
                height: $asset-metadata-height-720p;
                dt {
                    font-size: $asset-metadata-font-size-720p;
                }
            }

            i {
                font-size: 13px;
            }

            .asset-locked-overlay {
                .fa-lock {
                    font-size: 86px;
                }
            }
        }
    }
}

