@import "variables.10ft";
@import "./style";


core-rental-confirm {

    .rental-poster {
        height: 240px;

        img {
            height: 100%;
            width: 360px;
        }
    }

    .core-modal .modal-title {
        text-transform: none;
        font-size: 45px;
        line-height: normal;
    }

    .rental-controls {
        width: 850px;
        font-size: 35px;
        line-height: normal;
    }
}

@include scale-for-720p {
    core-rental-confirm {

        .rental-poster {
            height: 187px;

            img {
                border-width: 1px;
            }
        }

        .rental-buttons {
            margin: 13px auto;
        }

        .rental-controls p {
            margin-bottom: 13px;
        }

        .rental-controls .rental-buttons .rental-error {
            border-radius: 3px;
            margin-top: 3px;
            padding: 3px;

            .modal-close-btn {
                right: 0;
                top: 0;
            }
        }

        .rental-poster {
            height: 160px;

            img {
                width: 240px;
            }
        }
    }

}
