@import "../../styles/mixins";

.core-spinner {
    align-items: center;
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
    justify-content: center;
    z-index: $index-front;

    .dish-logo {
        display: none;
    }

    i {
        background-image: url('../../../assets/images/spinner.png');
        background-size: contain;
        height: 60px;
        width: 60px;
        animation-duration: 0.75s;
    }
}

.show-dish-logo {
    .core-spinner {
        .dish-logo {
            display: inline-block;
            background-image: url('../../../assets/images/sling@2x.png');
            height: 150px;
            margin-bottom: 80px;
            width: 284px;
        }

        i {
            height: 50px;
            width: 50px;
        }
    }
}
