@import "variables.10ft";
@import "../../../styles/base";

.rzslider{
    height: 100%;
    width: $progress-bar-width;
    touch-action: none;
    -ms-touch-action: none;
    margin-top: 63px;
}

.progress-bar-group {
    width: 100%;
    height: 130px;
    bottom: 0;
    left: 0;
    position: absolute;
    box-sizing: border-box;
    background-color: $color-overlay-gray;
    display: inline-block;
    text-align: center;

    &.embedded  {
        background: none;
    }

    .rz-bubble-ish {
        position: absolute;
        padding: 1px 3px;
        @include font-size(bigger);
        font-weight: bold;
        color: $color-off-white;
        width: 8%;
        left: 1.25%;
        text-align: right;
        display: block;
        top: 49px;
        bottom: 19px;
    }

    .rzslider {
        .rz-bar-wrapper {
            margin: auto;
            height: 100%;
            padding-top: 0;
            width: 100%;
            box-sizing: border-box;
        }
        .rz-bar {
            height: $progress-bar-height;
            margin: auto;
            background-color: $color-off-white;

            &.rz-selection {
                border-width: 1px;
                background-color: $color-yellow;
            }
        }

        .rz-pointer {
            color: $color-white;
            outline: none;
            top: -16px;
            height: 38px;
            width: 38px;
            border-radius: 50px;
            border-color: $color-yellow;
            border-style: solid;
            border-width: 4px;
            background: $color-dark-gray;

            &:hover {
                background: $color-yellow;
            }

            &:active {
                background: $color-yellow;
            }
        }
        .rz-pointer:after {
            display: none;
        }
        .rz-bubble {
            width: auto;
            @include font-size(smaller);
            bottom: -3px;
            padding: 1px 3px;
            color: $color-off-white;
            display: none;

            &.rz-limit {
                position: fixed;
                display: block;
                left: 81% !important;
                width: 100px;
                bottom: 19px;
                text-align: left;
                display: block;
            }
        }
    }

    // not timeshiftable
    .rzslider[disabled] {
        .rz-bar.rz-selection {
            background-color: $color-off-white;
        }
    }

    .rzslider-valid-range {
        left: 0%;
        top: 0%;
        width: 100%;
        height: 100%;
        background-color: $color-yellow;
        border-radius: 2px;
    }

    .rzslider-full-range {
        margin-top: 63px;
        z-index: 2;
        position: absolute;
        left: 10%;
        top: 0;
        height: 5px;
        width: $progress-bar-width;
        background-color: $color-off-white;
        border-radius: 2px;
    }

    .remaining-time-label {
        position: absolute;
        top: 49px;
        @include font-size(bigger);
        font-weight: bold;
        right: 1.25%;
        width: 8%;
        text-align: left;
        color: $color-off-white;
    }

    .preview-group {
        display: block;
        position: fixed;
        z-index: $index-front;
        left: 0;
        width: 100%;
        bottom: 150px;
        pointer-events: none;

        .preview-image-track {
            display: inline-block;
            width: 80%;
            left: 10%;
        }

        .preview-frame {
            width: $scrubber-preview-image-width + 30px;
            margin-bottom: 21px;
            padding: 15px 15px 0;
            background-color: $color-overlay-gray;
            pointer-events: all;
            transform: TranslateX(-50%);

            &:after {
                position: absolute;
                bottom: 1px;
                border-left: 21px solid transparent;
                border-right: 21px solid transparent;
                border-top: 23px solid $color-overlay-gray;
                margin-left: -15px;
                transform: TranslateY(104%);
                content: '';
            }

            .preview-frame-marker {
                display: none;
            }
        }

        .preview-image {
            height: $scrubber-preview-image-height;
            width: $scrubber-preview-image-width;
            background-size: 100%;
            background-position: top left;
            background-repeat: no-repeat;
            background-color: $color-black;
            border: none;
        }

        .preview-playback-details {
            color: $color-white;
            width: 100%;
            height: 53px;
            @include font-size(bigger);
            font-weight: bold;
            padding-top: 12px;
            text-align: center;
            z-index: $index-front + 1;

            .fa-repeat {
                padding-left: 5px;
            }
        }
    }
}

@include scale-for-hd-tv {
    .rzslider{
        margin-top: 42px;
    }

    .progress-bar-group {
        height: 87px;

        .rzslider{
            .rz-pointer {
                width: 25px;
                height: 25px;
                top: -10px;
                border-radius: 33px;
                border-width: 3px;
            }

            .rz-bar {
                height: $progress-bar-height-hd-tv;
            }

            .rz-bubble {
                bottom: -2px;
                padding: 1px 2px;

                &.rz-limit {
                    width: 67px;
                    bottom: 13px;
                }
            }
        }

        .rzslider-full-range {
            margin-top: 42px;
        }

        .remaining-time-label {
            top: 28px;
        }

        .rz-bubble-ish {
            top: 28px;
            bottom: 13px;
        }

        .preview-group {
            bottom: 100px;

            .preview-frame {
                width: $scrubber-preview-image-width-hd-tv + 20px;
                margin-bottom: 14px;
                padding: 10px 10px 0;

                &:after {
                    border-left: 14px solid transparent;
                    border-right: 14px solid transparent;
                    border-top: 15px solid $color-overlay-gray;
                    margin-left: -14px;
                }
            }

            .preview-image {
                height: $scrubber-preview-image-height-hd-tv;
                width: $scrubber-preview-image-width-hd-tv;
            }

            .preview-playback-details {
                height: 35px;
                padding-top: 6px;
            }
        }
    }
}
