@import "variables.10ft";
@import '../../styles/variables';
@import '../../styles/mixins';

core-modal-terms-of-service {
    .modal-body {
        .terms-of-service-text {
            height: 500px;
            white-space: pre-wrap;
            overflow-y: scroll;
            color: $color-near-white;
            font-size: 21px;
            background-color: $color-black;
        }
    }
}

@include scale-for-720p {
    core-modal-terms-of-service {
        .modal-body {
            .terms-of-service-text {
                height: 334px;
                font-size: 14px;
            }
        }
    }
}
