@import "variables.10ft";
@import "../../../styles/mixins";

core-connection {

    .setting-sub-header {
        font-size: $font-size-biggest2;
        text-transform: none;
        margin-bottom: 30px;
        color: $color-white;
        font-weight: 500;
    }

    core-radio {
        margin: 30px 0;
        label {
            font-size: $font-size-bigger2;
            span.radio-text {
                margin-left: 40px;
                color: $color-white;

            }
        }

        i.radio-circle.outer {
            z-index: 0;
        }
    }

    @include scale-for-720p {
        .setting-sub-header {
            font-size: 26px;
        }

        core-radio {
            label {
                font-size: 23px;
            }
        }
    }
}
