@import "variables.10ft";
@import '../../../../../styles/mixins.scss';
@import '../../../../../styles/variables.scss';

.search-container {
    .recent-searches-container {
        width: 100%;
        height: auto;
        float: left;
        padding-top: 3vh;

        .recent-searches-title {
            color: $color-white;
            font-size: 2.6vh;
            font-weight: 400;
            padding: 1.5vh 0 1.5vh 0.8vh;
        }

        .recent-searches-list {
            height: auto;
            overflow-y: scroll;

            .row {
                font-size: 2.6vh;
                font-weight: 300;
                padding-left: 0.5vw;
                height: 5vh;
                line-height: 5.5vh;
                width: 19.8vw;
                border-radius: 0.3vw;
                outline: none;
                display: flex;
                align-items: center;
                cursor: pointer;

                &.recent-searches-item {
                    @include ellipsis-able();
                    display: inline-block;
                    color: $color-warm-gray;
                    text-transform: uppercase;

                    &.core-focused-item {
                        @include focused-box();
                    }
                }

                &.clear-all-button {
                    color: $color-yellow;
                    span {
                        padding-left: 0.5vw;
                    }

                    svg {
                        width: 3vh;
                        height: 3vh;
                    }

                    &.core-focused-item {
                        @include focused-box();
                    }
                }
            }
        }
    }
}
