@import "variables.10ft";
@import '../../../../../styles/mixins.scss';
@import '../../../../../styles/variables.scss';

.search-container {
    .search-term-container {
        width: 100%;
        position: relative;

        &.core-focused-item {
            .search-term-input {
                @include focused-box();
            }
        }

        .search-term-input {
            background-color: $color-medium-light-gray;
            width: 100%;
            outline: none;
            border-radius: 4px;
            border-color: transparent;
            color: $color-white;
            box-shadow: 0 6px 18px 0 rgba(0,0,0,0.2);
            font-weight: 300;
            font-size: 2.6vh;
            padding: 1.4vh 3vw 1.4vh 1vw;
            letter-spacing: 1.5px;
            text-transform: uppercase;
            position: relative;

            &::placeholder {
                text-transform: capitalize;
                color: $color-white;
            }
        }

        .keyboard-icon {
            width: 2.08vw;
            right: .83vw;
            bottom: 2vh;
            position: absolute;
        }
    }
}
