@import "variables.10ft";
@import './Ribbon';
@import '../../../styles/mixins.scss';
@import '../../../styles/variables.10ft';

.ribbon-container {
    margin-left: 7.2vw;

    &.ribbon-ui4 {
        height: 30.65vh;
        margin-bottom: 7vh;
        margin-left: 5.2084vw;

        h2.ribbon-title {
            line-height: 4.074vh;
            font-size: 2.963vh;
            padding: 1.4vh 0 2.5vh 0;
        }

        &.carousel-ribbon {
            margin-left: -17.0867vw;

            h2.ribbon-title {
                margin-left: 22.2867vw;
            }
        }
    }

    h2.ribbon-title {
        padding: 1.4vh 0 1.4vh 0;
        line-height: 4.65vh;
        font-size: 3.33vh;
        font-weight: 500;
        letter-spacing: 0.02vw;

        .ribbon-description {
            margin-left: 20px;
            font-weight: 400;
        }
    }

    .ribbon {
        .empty-ribbon-gradient {
            background: linear-gradient(90deg, $color-cyan-blue 0%, $color-cyan-blue 100%);
            margin-left: -1vw;
            width: 78.33vw;

            .empty-ribbon-message {
                font-weight: 500;
                font-size: $font-size-bigger;
                line-height: 1.2;
                margin: 7.22vh 0 0 5.417vw;

                .message-title {
                    color: $color-lightning-yellow;
                    font-style: italic;
                }

                .message-subtitle {
                    color: $color-sheen-gray;
                }
            }

            @include scale-for-720p {
                .empty-ribbon-message {
                    font-size: 22px;
                }
            }
        }

        &.ribbon-empty {
            &::after {
                background: repeating-linear-gradient(to right, rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0.25) 512px, transparent 512px, transparent 532px);
                content: "";
                flex: 1;
            }
        }
    }
}
