@import '../../../styles/variables';
@import '../../../styles/mixins.scss';

core-ribbon-item {

    &.selected {
        position: relative;
        // When an asset item is selected, make the thumbnail and
        // metadata containers semi-opaque. Set on the containers
        // so the check mark is bright.
        .thumbnail-wrapper,
        .sub-content {
            opacity: .3;
        }
    }

    // Styling for the majority of asset items in ribbons. See below for people ribbon overrides.
    li.ribbon-item {
        outline: none;
        list-style-type: none;
        height: $asset-item-height;
        max-width: $asset-item-width;
        cursor: pointer;
        position: relative;
        overflow: hidden;

        .thumbnail-wrapper {
            height: $asset-image-height;
            background-color: $color-gray;
            position: relative;
        }

        div.delete-icon {
            @include position($left: auto);
            color: $color-white;
            background-color: $color-overlay-dark-gray;
            width: 25px;
            height: 25px;
            line-height: 25px;
            visibility: hidden;
            opacity: 0.8;
            font-size: 36px;
            font-weight: 100;
            text-align: center;
            z-index: 1; // This will show the icon over the lock icon.
            &:hover { opacity: 1; }
        }

        &:hover {
            div.delete-icon {
                visibility: visible;
            }
        }

        .sub-content {
            @include black-box($asset-border-size);
            // font-size is set in platform specific sheets
            height: $asset-metadata-height;
            padding: 0 15px;
            position: absolute;
            width: 100%;
            border: none;

            .icon-record-single {
                padding-right: 5px;
            }

            .protected {
                color: $color-yellow;
            }

            &--details,
            dd,
            dt {
                @include ellipsis-able();
                line-height: normal;
                width: 100%;
            }
        }

        &:hover .sub-content {
            @include hovered-box();
        }

        &:focus .sub-content {
            @include focused-box();
        }

        // These elements won't inherit their parent's color on hover because they're set with more specificity below
        &:hover,
        &.focus-grid-focused {
            outline: none;

            dt,
            dd::after,
            dd.important {
                color: $color-white;
            }

            i {
                color: $color-white;
            }
        }

        dt {
            color: $color-near-white;
            display: block;
            @include ellipsis-able();
            // font-size is set in platform specific style sheets
            font-weight: 500;
            // margin is set in platform specific style sheets
            padding: 0;
            text-align: left;
            width: 100%;
            max-width: $asset-item-width;
        }

        .meta-data {
            @include ellipsis-able();
            width: 100%;
        }

        dd {
            display: inline;

            &::after {
                content: "|";
                color: $color-off-white;
                padding-left: 6px;
            }

            &:last-child::after {
                content: "";
                padding-left: 0;
            }
        }

        .important {
            color: $color-yellow;
        }

        i {
            // font-size and top are set in platform specific sheets
            position: relative;
        }

        .asset-locked-overlay {
            background: $color-overlay-black;
            @include full-absolute();
            @include flex-center();
        }
    }

    li.ribbon-item.person {
        max-width: $asset-item-people-width;
        min-width: $asset-item-people-width;
    }
}
