@import "variables.10ft";
@import "../../styles/mixins";

core-modal-pin-entry {
    .pin-entry-modal .pin-entry-container {
        $margin-top: 15px;
        width: 250px;

        .row {
            margin: $margin-top 0;
        }

        .pin-entry-title {
            color: $color-white;
            @include font-size(bigger);
            font-weight: 300;
            margin: $margin-top 0 $margin-top * 2 0;
        }

        .modal-body {
            padding-bottom: $margin-top;

            .form-control {
                @include font-size(medium);
                border: 1px solid $color-white;
                background-color: $color-black;
                height: 44px;
                width: 100%;
                padding: 6px 12px;
                color: #555;
                outline-width: 0;

                &:active, &:focus {
                    color: $color-white;
                    border-color: $color-light-blue;
                }
            }

            .modal-spinner-container {
                position: relative;
                display: inline-block;
                float: right;
                margin: -36px 5px;

                .core-spinner i {
                    height: 30px;
                    width: 30px;
                }
            }
        }

        .modal-footer {
            display: block;
            text-align: left;

            a {
                text-decoration: none;
                cursor: pointer;

                &:hover {
                    color: $color-light-blue;
                }
            }

            .message {
                margin-top: 10px;
                text-align: left;
            }

            .error {
                color: $color-orange;
            }

            .success {
                color: $color-green;
            }
        }
    }

    @include scale-for-responsive {
        .core-modal {
            max-width: 350px;
            .pin-entry-container {

                .pin-entry-title {
                    margin-bottom: 15px;
                }
            }
        }
    }
}