@import '../../../../styles/mixins';
@import '../../guide-container.variables.3ft';
// Shared styles for the channel ribbon
core-channel-ribbon {
    display: block;
    padding: 10px 0;
    // Arrow buttons only appear when you hover over the ribbon
    &:hover core-ribbon-paging .arrow-button {
        opacity: 1;
    }

    .core-channel-ribbon__items {
        display: inline-flex;
    }

    .core-channel-ribbon__item {
        @include box-align($display: inline-flex);
        cursor: pointer;
        flex-shrink: 0;
        opacity: 0.5;

        &.focus-grid-focused {
            @include focused-box();
        }

        &:focus,
        &:hover,
        &.focus-grid-focused,
        &--selected {
            opacity: 1;
            outline: none;
            text-decoration: none;
        }

        img {
            @include ellipsis-able();

            &::before {
                color: $color-near-white;
                content: attr(alt);
                font-size: 16px;
                height: 100%;
                width: 100px;
                line-height: 50px;
                text-align: center;
            }
        }
    }
}
