@import '../../../styles/variables';

core-iap-restart-confirmation-screen {
    .iap-restart-modal {
        height: 100%;
        width: 100%;
        padding: 20vh 10vw;

        .modal-header {
            margin-bottom: 2.22vh;

            .modal-title {
                color: $color-silver-gray;
                font-size: 6.48vh;
                font-weight: 400;
                line-height: 9.074vh;
                text-transform: initial;
            }
        }

        .modal-body p {
            color: $color-silver-gray;
            font-size: 2.77vh;
            font-weight: 500;
            line-height: 3.88vh;
            padding: 0 4.5vw;
        }

        .modal-fine-print {
            color: $color-off-white;
            font-size: 2.77vh;
            font-weight: 500;
            line-height: 3.88vh;
            text-align: center;
            padding-top: 2.685vh;

            .fine-print-button {
                text-decoration: underline;
                color: $color-silver-gray;
                margin: 0;
                padding: 0;
                border: 0;
                background: transparent;
                font-size: inherit;
                cursor: pointer;

                &.focus-grid-focused {
                    color: $color-sky-blue;
                }
            }
        }

        .modal-footer {
            margin-top: 7vh;

            .modal-footer-buttons {
                margin: auto;

                .btn.iap-restart-modal-btn {
                    height: 11.85vh;
                    width: 28.54vw;
                    border-radius: 6px;
                    font-size: 4.81vh;
                    font-weight: bold;
                    line-height: 6.66vh;
                    color: $color-off-white;
                    background-color: $color-light-medium-black;
                    margin-right: 2.81vw;
                    margin-left: 0;

                    &:focus, &:hover, &.focus-grid-focused {
                        background-color: $color-sky-blue;
                        color: $color-white;
                    }
                }

                .btn + .btn {
                    margin-left: 0;
                }
            }
        }

        .core-spinner {
            width: 25px;
            height: 25px;
            display: inline-block;
            vertical-align: top;
            margin-right: 40px;
        }
    }
}
