@import "variables.10ft";
@import "../../../styles/base";

.search-page {

    padding-top: 20px;

    .search-page-content {
        height: 100px;
    }

    .search-results-vertical-scroll-section {
        height: 780px;
    }

    .last-ribbon {
        .shifting-ribbon-content {
            padding-bottom: 70px;
        }
    }

    .empty-search-string {
        display: block;
        margin: 10px 0;
        text-align: center;

        .empty-page-image {
            width: 100px;
            height: 100px;
            margin: 20px 47%;
            background: url("../../../assets/images/ghost.svg");
            background-size: contain;
            background-repeat: no-repeat;
            font-size: 0;
        }

        .no-titles-text {
            @include font-size(biggest);
            line-height: 36px;
            letter-spacing: 0.5px;
            color: $color-yellow;
            font-weight: bold;
            text-transform: uppercase;
        }

        .adjust-search-text {
            @include font-size(bigger);
            line-height: 36px;
            color: $color-light-gray;
            margin: 10px 0;
        }
    }

    .form-control {
        font-size: 36px;
        font-weight: 500;
        height: 70px;
        width: 100%;
        padding: 0 12px;
        padding-left: 70px;
        background-color: $color-gray;
        border-color: $color-gray;
        color: $color-white;
        margin-top: 40px; // spec has this at 60px, there is a 20px padding in the dashboard container

        &.core-focused-item {
            border-color: $color-dark-blue;
        }

        &.has-hover{
            border-color: $color-white;
        }

    }

    .input-row {
        width: $search-input-width;
        margin: auto;

        .dish-icon-search {
            position: absolute;
            font-size: 36px;
            margin-top: 16px;
            margin-left: 26px;
        }
    }

    .input-row.browser {
        margin: inherit;
    }

    .browser .form-control:focus {
        background-color: $color-white;
        color: $color-off-white;
    }

    .result-title {
        font-weight: bold;
        color: $color-yellow;
        margin: 20px 0;
    }

    .channel-btn {
        position: relative;
        display: inline-block;
        margin: 0 20px 0 0;
        height: 100%;
        overflow: visible;
        box-sizing: border-box;
        cursor: pointer;
        padding: 0;

        img {
            height: 100px;
            padding: 10px 20px;
            width: auto;
            opacity: 0.5;
        }

        &:hover img {
            opacity: 1.0;
        }

        &.watching {

            img,
            i,
            .subtext {
                opacity: 1;
            }
        }

        &.core-focused-item {

            outline: 0;
            @include focused-box();

            img {
                opacity: 1;
            }
        }

        &:focus {
            outline: none;
        }
    }
}

@include scale-for-hd-tv {
    .search-page {
        padding-top: 14px;

        .search-page-content {
            height: 67px;
        }

        .search-results-vertical-scroll-section {
            height: 520px;
        }

        .last-ribbon {
            .shifting-ribbon-content {
                padding-bottom: 47px;
            }
        }

        .empty-search-string {
            margin: 7px 0;

            .empty-page-image {
                width: 67px;
            }

            .no-titles-text {
                line-height: 24px;
            }

            .adjust-search-text {
                line-height: 24px;
                margin: 7px 0;
            }
        }

        .form-control {
            height: 45px;
            font-size: 24px;
            padding: 0 8px;
            padding-left: 47px;
            margin-top: 27px;

            &.core-focused-item {
                border-width: 4px;
            }
        }

        .input-row {
            width: $ribbon-search-input-width-hd-tv;

            .dish-icon-search {
                font-size: 24px;
                margin-top: 11px;
                margin-left: 17px;
            }
        }

        .result-title {
            margin: 13px 0;
        }

        .channel-btn {
            margin: 0 13px 0 0;

            img {
                height: 67px;
                padding: 7px 14px;
            }
        }
    }
}
