@import '../../../styles/mixins';

core-details-container {
    .details-wrapper {
        position: fixed;
        z-index: $index-front + 1;
    }

    .details-container {
        position: fixed;
        height: 100%;
        width: 100%;
        bottom: 0;
        top: 0;
        right: 0;
        margin: 0;
        padding: 0;
    }

    .details {
        background-color: $color-dark-gray;
        box-shadow: 0 8px 30px 0 rgba(black, 0.5);
        color: $color-off-white;
        height: 100%;
        overflow-y: auto;
        position: absolute;
        right: 0;
        overflow-x: hidden;
        // width is set in platform specific sheets
        z-index: $index-middle;
    }

    .error-message {
        padding: 30px;
        font-size: 17px;
        color: $color-yellow;
    }

    i.icon-arrow-left2 {
        display: none;
    }

    .details-content {
        // $details-left-gutter is imported in platform specific sheets
        padding-left: $details-side-gutter;
        padding-right: $details-side-gutter;
        // padding-top is set in platform specific sheets

        section {
            margin-bottom: 20px;
        }

        .core-ui-ribbon-content {
            padding-left: 0;
        }
    }

    .details-mask {
        @include position($position: fixed);
        z-index: 0;
        cursor: pointer;
        background-color: $color-overlay-dark-gray;
    }

    .banner-image {
        //background-color: $color-black;
        // height is set in platform specific style sheets
        text-align: center;
        position: relative;
        width: 100%;
        display: flex;
        justify-content: center;

        img {
            height: 100%;
            z-index: 1;
            position: relative;
        }

        .background-banner {
            @include position();
            filter: blur(3px);
            background-size: cover;
            background-position: center center;
            height: 100%;
            opacity: 0.3;
            width: 100%;
        }
    }

    hr {
        background-color: $color-gray;
        border: 0;
        // height and margin-top are set in platform specific sheets
        margin-bottom: 20px;
    }

    core-scrolling-ribbon {

        .ribbon-title {
            margin-left: 0px;
        }

        core-ribbon-tile {
            margin-left: 0px !important;
        }
    }
}

