@import "variables.10ft";
.manage-viewing-history-filters-container {
    width: 100vw;
    position: fixed;
    top: 12.96vh;
    z-index: $index-front + 1;
    background-color: $color-dark-gray;

    .user-text {
        font-size: 2.8vh;
        font-weight: 400;
        padding: 2.2vh 0 4.43vh 3.2vw;
        text-align: left;
    }

    .tabs-row {
        margin-top: -0.1vh;
        position: relative;
        justify-content: center;
        letter-spacing: 1.5px;
        font-size: 2.4vh;

        .tabs {
            font-size: 2.4vh;
        }
    }
}
