@import '../../styles/mixins';
@import '../../styles/variables';

core-modal-sign-in {
    .core-modal form .modal-header {
        margin-bottom: 20px;
        .sign-in-title {
            color: #fff;
            &.error-message {
                color: $color-orange;
                text-align: center;
            }
        }
    }

    .modal-body {
        .sign-in-input {
            font-weight: 500;
            color: #fff;
            background-color: #323232;
            margin: 0;
            border-radius: 0;
            outline: none;
            box-shadow: none!important;
        }
    }

    .core-modal form .modal-footer {
        text-align: center;
        margin-top: 0px;

        .forgot-password-text {
            text-align: center;
        }
    }

    .core-spinner {
        z-index: 9999;
    }
}
