@import "../../../../styles/variables";

@mixin cart-item() {
    line-height: 38px;
    font-weight: 500;
    color: $color-silver-gray;
    border-bottom: 2px solid $color-light-gray;
    padding-bottom: 20px;
    margin-bottom: 20px;
    margin-left: 50px;
    position: relative;
    font-size: 26px;
}

core-cart-summary {
    width: 40%;
    height: 100%;
    .iap-cart-summary {
        height: 100%;
        background-color: $color-medium-light-gray;
        overflow: hidden;
        padding: 60px 40px 0;

        .cart-title {
            border-bottom: 2px solid $color-light-gray;
            line-height: 40px;
            color: $color-yellow;
            margin-bottom: 20px;
            padding-bottom: 20px;
            font-size: 30px;
        }

        .cart-items {
            .cart-item {
                @include cart-item();

                &:before{
                    font-family: FontAwesome;
                    content: "\f00c";
                    color: $color-medium-green;
                    font-weight: 100;
                    position: absolute;
                    left: -50px;
                    top: 50%;
                    margin-top: -25px;
                    font-size: 25px;
                }
            }

            .update-summary {
                @include cart-item();
                margin-left: 0;
            }
        }

        .legal-copy {
            display: none;
        }
    }

    &.full-cart-summary {
        width: 100%;
        background-color: $color-medium-light-gray;
        display: flex;
        justify-content: center;
        align-items: center;
        
        .iap-cart-summary {
            height: auto;
            width: 50%;
            padding: 0;

            .cart-title {
                font-size: 3vh;
                line-height: 3.5vh;
                border: none;
                width: 65%;
                text-align: center;
                margin: 8px auto;
            }
            
            .cart-items {
                .cart-item {
                    border: none;
                    font-weight: 300;
                    font-size: 2.5vh;
                    line-height: 3vh;
                    margin-bottom: 10px;
                    color: $color-silver-gray;
                }
            }
        }

        .legal-copy {
            display: block;
            font-weight: 400;
            color: $color-off-white;
            font-size: 1.8vh;
        }
    }
}
