@import "variables.10ft";
@import "../../../styles/base";

.keypad-container{
    background: transparent;
    padding: 0;
    margin-left: 4px;
    height: auto;
    width: $keypad-width;

    .form-control {
        height: 65px;
        border: none;
        padding-left: 25px;
        width: 100%;
    }

    .form-control-lg {
    	margin-bottom: 20px;
    	height: 80px;
        line-height: 80px;
        border: none;
        padding-left: 25px;
        @include font-size(biggest);
    }

    .non-input-text {
        margin-bottom: 10px;
    }

	.keypad-title {
		color: $color-white;
	}


	// each type of key, based on a single key-size
	.keypad-btn {
		text-align: center;
		@include font-size(biggest3);
		width: $keypad-btn-width;
		height: $keypad-btn-height;
        line-height: $keypad-btn-height;
		margin: 0;
		float: left;
        @include border-radius(0);

		&.wide {
			width: $keypad-btn-width * 2.085;
		}

		&.ex-wide {
			width: $keypad-btn-width * 3.085;
		}

		+.keypad-btn {
			margin-left: $keypad-btn-margin;
		}

		// custom icon keys
		.dish-icon-backspace {
			font-size: $tv-font-size-biggest * 1.75;
		}

		.spacebar {
            font-size: $tv-font-size-biggest * 1.75;
		}

		&[disabled] {
			color: $color-gray;
		}
	}

	.key-row {
		float: left;
        width: $keypad-width;
		margin-bottom: $keypad-btn-margin;
	}

	// the top most keys (Enter, Cancel, etc)
	.action-row {
		position: relative;
		margin: 0;

		.keypad-btn {
			width: $keypad-btn-width * 5.33;

			+ .keypad-btn {
				margin-left: $keypad-btn-margin;
			}
		}
	}
}

@include scale-for-tv {

	.keypad-container {

	    .form-control {
	        margin-bottom: 10px;
            color: $color-white;
	    }

	    .non-input-text {
			@include font-size(biggest3);
	    }
	}
}

@include scale-for-hd-tv {

    .keypad-container {
        margin-left: 3px;
        width: $keypad-width-hd-tv;

        .form-control {
            margin-bottom: 7px;
            height: 43px;
            color: $color-white;
        }

        .form-control-lg {
            margin-bottom: 14px;
            height: 54px;
            line-height: 54px;
        }

        .non-input-text {
            @include font-size(biggest3);
            margin-bottom: 7px;
        }

        // each type of key, based on a single key-size
        .keypad-btn {
            width: $keypad-btn-width-hd-tv;
            height: $keypad-btn-height-hd-tv;
            line-height: $keypad-btn-height-hd-tv;
            margin: 0;
            float: left;
            @include border-radius(0);

            &.wide {
                width: $keypad-btn-width-hd-tv * 2.085;
            }

            &.ex-wide {
                width: $keypad-btn-width-hd-tv * 3.085;
            }

            + .keypad-btn {
                margin-left: $keypad-btn-margin-hd-tv;
            }

            // custom icon keys
            .dish-icon-backspace {
                font-size: $tv-font-size-biggest-hd-tv * 1.75;
            }

            .spacebar {
                font-size: $tv-font-size-biggest-hd-tv * 1.75;
            }
        }

        .key-row {
            width: $keypad-width-hd-tv;
            margin-bottom: $keypad-btn-margin-hd-tv;
        }

        // the top most keys (Enter, Cancel, etc)
        .action-row {

            .keypad-btn {
                width: $keypad-btn-width-hd-tv * 5.33;

                + .keypad-btn {
                    margin-left: $keypad-btn-margin-hd-tv;
                }
            }
        }
    }
}
