@import "variables.10ft";
@import '../../ribbon-system/ribbon-system.variables.10ft';
$dvr-status-bar-height: 18px;

@import './style';

core-dvr-status-bar {
    height: 75px;
    margin-left: $ribbon-left-gutter;
    h4 {
        font-size: $font-size-bigger2;
        font-weight: normal;
    }
}


// 720p styles
$dvr-status-bar-height-720p: 12px;
$dvr-status-bar-radius-720p: $dvr-status-bar-height-720p / 2;
$dvr-status-bar-border-radius-left-720p: $dvr-status-bar-radius-720p 0 0 $dvr-status-bar-radius-720p; // left edge rounded, right edge sharp
$dvr-status-bar-border-radius-all-720p: $dvr-status-bar-radius-720p; // left and right edges rounded

@include scale-for-720p {
    core-dvr-status-bar {
        height: 50px;
        margin-left: $ribbon-left-gutter-720p;
        h4 {
            font-size: $font-size-small;
        }

        // Shared by all the status bar layers
        .dvr-status-bar,
        .dvr-status-bar-total,
        .dvr-status-bar-used,
        .dvr-status-bar-selected {
            height: $dvr-status-bar-height-720p;
        }

        .dvr-status-bar {
            .dvr-status-bar-total {
                border-radius: $dvr-status-bar-border-radius-all-720p; // left and right edges rounded
            }

            .dvr-status-bar-used {
                border-radius: $dvr-status-bar-border-radius-left-720p; // left edge rounded, right sharp
            }

            .used-one-hundred-percent {
                border-radius: $dvr-status-bar-border-radius-all-720p;
            }

            .selected-greater-than-or-equals-used {
                border-top-left-radius: $dvr-status-bar-radius;
                border-bottom-left-radius: $dvr-status-bar-radius;
            }

            .selected-used-one-hundred-percent {
                border-top-right-radius: $dvr-status-bar-radius-720p;
                border-bottom-right-radius: $dvr-status-bar-radius-720p;
            }
        }
    }
}
