@import "variables.10ft";
@import '../../screen.variables.10ft';
@import './style';

core-screen-tile-micro-guide {
    li {
        padding: 19px 28px;
        height: $my-channels-asset-tile-height;
        background-color: $color-light-cool-gray;
        border-bottom: 3px solid $color-light-cool-gray;

        // Large title, spans entire width
        h3 {
            font-size: 28px;
            line-height: 35px;
            padding-bottom: 8px;
        }

        .tile-attributes {
            font-size: 22px;
            bottom: 55px;
            width: $my-channels-asset-tile-width * 0.6;
            margin-top: 10px;

            // spaces between items in 10ft sketches
            span::after {
                content: "";
                padding-left: 0.3em;
            }

            span:last-child::after {
                content: "";
                padding-left: 0;
            }
        }

        // Channel logo
        span.channel-logo {
            width: $my-channels-asset-tile-width * 0.3;
            right: 16px;
            bottom: 19px;
            img {
                max-height: 80px;
                max-width: $my-channels-asset-tile-width * 0.3;
            }
            .alt-text {
                font-size: 26px;
            }
        }

        // Channel logo centered for channel assets without linear schedules
        span.channel-logo.logo-only {
            width: $my-channels-asset-tile-width * 0.8;
            right: $my-channels-asset-tile-width * 0.1;
            text-align: center;
            vertical-align: center;
            bottom: unset;
            top: 30px;
            img {
                max-height: 90px;
                max-width: unset;
            }
        }

        .asset-locked-icon {
            i {
                font-size: 1.6em;
                margin-top: 4px;
            }
        }

        &.focus-grid-focused {
            @include focused-box();

            .tile-title, .tile-attributes {
                color: $color-white;
            }
        }
    }
}

@include scale-for-720p {

    core-screen-tile-micro-guide {
        li {
            padding: 10px 18px;
            height: $my-channels-asset-tile-height-720p;
            font-size: $asset-metadata-font-size-720p;
            &.micro-guide-tile .tile-attributes {
                min-height: 20px;
                font-size: $asset-metadata-font-size-720p;
                width: calc(#{$my-channels-asset-tile-width-720p} - #{$my-channels-asset-tile-width-720p * 0.3});
                margin-top: 6px;
            }

            h3.tile-title {
                font-size: 19px;
                line-height: 23px;
                padding-bottom: 5px;
            }

            // Channel logo
            span.channel-logo {
                width: $my-channels-asset-tile-width-720p * 0.3;
                // right: 16px;
                // bottom: 19px;
                img {
                    max-width: $my-channels-asset-tile-width-720p * 0.3;
                }
                .alt-text {
                    font-size: $asset-metadata-font-size-720p;
                }
            }

            // Channel logo centered for channel assets without linear schedules
            span.channel-logo.logo-only {
                width: $my-channels-asset-tile-width-720p * 0.8;
                right: $my-channels-asset-tile-width-720p * 0.1;
            }

        }
    }
}
