@import "variables.10ft";
@import "../../../styles/base";
@import "../../../styles/mixins";

// this is the element that ngAnimate applying enter and leave classes to
.drawer-wrapper {
    top: 0; //top: 0 is required to fix XBOX-1752
    @include fixed-position();
    z-index: $index-front;

    &.ng-enter,
    &.ng-leave {
        transition: opacity 300ms ease;

        .drawer {
            transition: transform 300ms ease;
        }
    }

    &.ng-enter {
        opacity: 0;

        .drawer {
            transform: translate($drawer-width, 0);
        }
    }

    &.ng-enter-active {
        opacity: 1;

        .drawer {
            transform: translate(0, 0);
        }
    }

    &.ng-leave-active {
        opacity: 0;

        .drawer {
            transform: translate($drawer-width, 0);
        }
    }
}

core-details-container {

    .details-wrapper .details-container .details {
        // Use width specified by CoreUI, to take care of full screen iViews in In App Extras ribbon
        //width: $drawer-width;
        box-shadow: -10px 0px 50px 0px rgba(0, 0, 0, 0.5);
    }

    .details-content {
        .drawer-details .title {
            font-size: 42px;
            font-weight: normal;
            line-height: normal;
        }
        .description {
            margin: 40px 0 25px 0;
            line-height: normal;
            padding-right: 30px;
        }
    }

    .folder-content {
        .season-menu {
            @include font-size(bigger);
        }

        .episode {
            @include font-size(bigger);
            padding: 15px 30px 35px 50px;

            .episode-content {
                margin: 0 50px 0 30px;
            }
        }
    }

}

@include scale-for-hd-tv {

    core-details-container {

        .details-wrapper .details-container .details {
            // Use width specified by CoreUI, to take care of full screen iViews in In App Extras ribbon
            //width: $drawer-width-hd-tv;
        }

        .details-content {
            .drawer-details .title {
                font-size: 27px;
            }
        }

        .folder-content {

            .season-menu {
                padding: 7px 0 14px 33px;
                margin: 10px;
                font-size: 17px;
            }

            .episode {
                font-size: 17px;
                padding: 10px 20px 23px 40px;

                .episode-content {
                    font-size: 17px;

                    .episode-button-row {
                        margin-top: 14px;
                    }
                }
            }
        }
    }

    core-details-meta-data {
        .drawer-details {
            .recording-icon {
                @include font-size(biggest2);
                line-height: 43px;
                i {
                    @include font-size(biggest2);
                }
            }
        }
    }
}
