@import "variables.10ft";
@import './style';
@import '../guide-container.variables.10ft.scss';
@import '../../../styles/mixins';

button.btn.black {

    &:hover {
        background-color: $color-black;
        color: $color-cool-white;
        white-space: nowrap;
        i:before {
            color: $color-cool-white;
        }
    }

    &.has-hover {
        background-color: $color-near-gray;
        color: $color-white;
        text-decoration: none;

        // icon hover state color
        i:before {
            color: $color-white;
        }
    }

    &.core-focused-item {
        color: $color-white;
        background-color: $color-dark-blue;
        outline: none;

        // icon hover state color
        i:before {
            color: $color-white;
        }
    }

}

core-date-filter {
    button.btn {
       font-size: $guide-header-component-font-size;

        &:focus {
            font-size: $guide-header-component-font-size;
        }
    }
}

@include scale-for-720p {
    core-date-filter {
        button.btn {
            height: 50px;
            font-size: $guide-header-component-font-size-720p;

            &:focus {
                font-size: $guide-header-component-font-size-720p;
            }
        }
    }
}
