@import "../../styles/mixins";

.core-modal button.modal-close-btn {
    position: absolute;
    right: 10px;
    top: 10px;
    height: 22px;
    width: 22px;
    font-size: 22px;
    line-height: 0;
    padding: 0;
    font-weight: 600;
    min-width: auto;
    min-height: auto;
    color: $color-light-gray;
    background-color: $color-black;
    border: 2px solid $color-light-gray;
    border-radius: 100%;
    outline: none;
    cursor: pointer;

    &:focus {
        background-color: $color-medium-gray;
        color: $color-cool-white;
    }

    &:hover {
        background-color: $color-near-gray;
        color: $color-white;
    }
}
