@import "variables.10ft";
@import './style';

// 10ft (LG) style overrides
core-channel-filter-modal {
    .core-modal {
        padding: 50px 90px;
        width: 730px;

        .modal-header {
            margin-bottom: 40px;
        }
    }

    .modal-footer {
        text-align: center;
    }

    .filters-array {
        display: inline-flex;
        flex-direction: column;
        position: relative;
        width: 50%;
    }

    core-radio {
        margin-bottom: 20px;
    }
}

@include scale-for-720p {
    core-channel-filter-modal {
        .core-modal {
            padding: 33px 60px;
            width: 487px;

            .modal-header {
                margin-bottom: 27px;
            }

            .modal-body {
                max-height: 334px;

                .vertical-scroll-container {
                    max-height: 334px;
                }
            }
        }

        .radio-buttons {
            padding-left: 1px;
        }

        core-radio {
            margin-bottom: 13px;
        }

        button.btn {
            height: 50px;
        }
    }
}
