@import "variables.10ft";
@import '../../screen.variables.10ft';
@import './style';

core-screen-tile-button {
    .utility-tile {
        font-size: 24px;
        .utility-icon {
            font-size: 80px;
        }

        // shorter little utility tiles in the new My Channels ribbon
        &.favorite-channels__utility-tile {
            height: $my-channels-asset-tile-height;
        }
    }
}

@include scale-for-720p {
    core-screen-tile-button {
        .utility-tile {
            height: $asset-item-height-720p;
            font-size: $asset-metadata-font-size-720p;
            .utility-icon {
                font-size: 60px;
            }

            // shorter little utility tiles in the new My Channels ribbon
            &.favorite-channels__utility-tile {
                height: $my-channels-asset-tile-height-720p;
            }
        }
    }
}
