@import './modals';
@import './mixins';

.core-modal {
    border-width: 2px;
    line-height: 36px;
    padding: 35px 50px 50px 50px;

    .modal-title {
        color: $color-yellow;
        font-size: 30px;
        font-weight: 600;
        letter-spacing: 0.5px;
        text-transform: uppercase;
    }

    .modal-sub-title {
        color: $color-white;
        font-size: 30px;
        font-weight: 600;
    }

    .modal-body p {
        font-size: 30px;
    }

    .modal-footer .btn + .btn {
        // Overriding bootstrap's style currently present in 10ft
        margin-left: 20px;
    }

    .modal-content {
        font-size: 30px;
    }
}

@include scale-for-720p {
    .core-modal {
        border-width: 1px;
        line-height: 24px;
        padding: 23px 33px 33px 33px;

        .modal-title {
            font-size: 20px;
            font-weight: 600;
            letter-spacing: 0.3px;
        }

        .modal-sub-title {
            font-size: 17px;
        }

        .modal-body p {
            font-size: 17px;
        }

        .modal-footer .btn + .btn {
            // Overriding bootstrap's style currently present in 10ft
            margin-left: 13px;
        }

        .modal-content {
            font-size: 20px;
        }

        .modal-footer {
            margin-top: 20px;
        }
    }
}
