@import "variables.10ft";
@import '../../../../components/ribbon-system/ribbon-system.variables.10ft';
@import './style';

// 10ft/LG outer container for the edit channels screen
core-edit-my-channels {
    $edit-my-channel-tile-margin-right: 32px;
    $edit-my-channel-tile-margin-bottom: 32px;
    z-index: 10;

    section.edit-my-channels-grid {
        height: 87vh;
        margin: 0 100-$edit-my-channel-tile-margin-right 0 100px;

        p.edit-my-channels-instruction {
            margin: 30px 0 18px 0;
        }
    }

    #edit-my-channels-tiles-container {
        margin-top: 10px;
        .channel-tile {
            margin-bottom: $edit-my-channel-tile-margin-bottom;
            margin-right: $edit-my-channel-tile-margin-right;
            width: $my-channels-edit-tile-width;
        }
    }
}
