@import "variables.10ft";
@import '../../screen.variables.10ft';
@import '../../../../styles/mixins.scss';
@import './style';

core-screen-ribbon-row {
    .horizontal-scroll-container {
        overflow-x: scroll;
    }

    .ribbon-content {
        display: inline-flex;
    }

    .ribbon-title {
        line-height: 50px;
        font-size: 32px;
        padding: 15px 0 15px $ribbon-left-gutter;
    }

    .scrolling-ribbon__empty .ribbon-content {
        width: 100%;

        &::after {
            background: repeating-linear-gradient(to right, rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0.25) 512px, transparent 512px, transparent 532px);
            content: '';
            flex: 1;
        }
    }

    core-ribbon-paging .arrow-button {
        display: none;
    }
}

@include scale-for-720p {
    core-screen-ribbon-row {

        .ribbon-title {
            padding: 15px 0 15px $ribbon-left-gutter-720p;
        }

    }
}
