@import "variables.10ft";
@import './style.scss';

core-rental-no-payment {
    .nopayment-container {
        width: 1000px;
    }

    .core-modal {
        .modal-body p {
            font-size: 35px;
        }

        .modal-footer .btn {
            text-transform: uppercase;
        }
    }
}

@include scale-for-720p {
    core-rental-no-payment {

        .nopayment-container {
            width: 467px;
        }

        .modal-body {

            .header {
                padding-bottom: 23px;
            }

            .message {
                font-size: 11px;
                font-weight: 200;

                .phone {
                    font-weight: 300;
                }
            }
        }
    }

}
