@import './variables';

.modal {
    display: inline-block;
}

// Main style for a modal in the application
.core-modal {
    background-color: $color-black;
    border: solid $color-medium-gray;
    // border-width is set in UI_TYPE sheets
    box-shadow: 0 10px 40px 0 $color-overlay-black60;
    display: inline-block;
    // padding is set in UI_TYPE sheets
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1050;

    .modal-header {
        margin-bottom: 10px;
    }

    .modal-sub-header {
        margin-bottom: 5px;
    }

    .modal-title {
        // all .modal-title styles are set in UI_TYPE sheets
    }

    .modal-sub-title {
        // all .modal-sub-title styles are set in UI_TYPE sheets
    }

    .modal-body {
        padding: 0;
    }

    .modal-footer {
        margin-top: 30px;
        padding: 0;
    }
}

// The modal wrapper. Should fill the entire viewport
// OnClick event handler is attached here. It closes the modal when clicked
.modal-open .modal {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: none; //will be overriden by angular-ui when modal is opened
    overflow: hidden;
    outline: 0;
}

// The modal background
.modal-backdrop, .core-modal-backdrop {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: $color-overlay-black60;

    &.modal-backdrop-override {
        z-index: 1042 !important;
    }}
