@import "variables.10ft";
@import "../../styles/base";
@import "../../styles/base.win10";

@include scale-for-hd-tv {
    /*
        this is to override the css style present in buttons.scss,
        .btn,.btn:focus {
            min-width: $button-short-width-hd-tv;
            min-height: $button-height-hd-tv;
            padding: 12px 20px;
            height: 50px;
    */

    core-iap-restart {
        .core-iap-restart {
            .core-iap-restart-container {
                .btn.restart-now-btn, .btn.restart-now-btn.focus-grid-focused {
                    height: 11vh;
                    padding: 0;
                }

                .btn.bottom-btn, .btn.bottom-btn.focus-grid-focused {
                    height: 9.33vh;
                    padding: 0;
                }
            }
        }
    }
}
