@import '../../styles/variables';

core-iap-restart {
    .core-iap-restart {
        background-color: $color-dark-gray;
        position: fixed;
        width: 100%;
        height: 100%;
        text-align: center;
        padding: 11.77vh 10.62vw;
        overflow-y: auto;
        z-index: 1;

        .iap-restart-title {
            color: $color-silver-gray;
            font-size: 4.629vh;
            font-weight: 500;
            line-height: 6.38vh;
            text-align: center;
            padding-bottom: 2vh;
            width: 55vw;
            margin: auto;
        }

        .iap-restart-sub-title {
            color: $color-silver-gray;
            font-size: 3.24vh;
            font-weight: 300;
            line-height: 5vh;
            padding: 1vh 20v
        }

        .iap-restart-sub-text {
            padding-bottom: 2vh;
        }

        .restart-now-btn {
            border-radius: 6px;
            font-size: 5.5vh;
            font-weight: 500;
        }

        .another-option-text {
            color: $color-silver-gray;
            font-size: 2.77vh;
            font-weight: 300;
            line-height: 3.88vh;
            padding-top: 5.5vh;
        }

        .bottom-btn {
            display: block;
            margin: auto;
            border-radius: 6px;
            font-size: 3.7vh;
            font-weight: bold;
            line-height: 5.185vh;
            margin-top: 4vh;
        }

        .core-iap-restart-container {
            .btn.restart-now-btn, .btn.bottom-btn{
                background-color: $color-light-medium-black;
                color: $color-off-white;

                &:focus, &:hover, &.focus-grid-focused {
                    background-color: $color-sky-blue;
                    color: $color-white;
                }
            }
        }
    }
}
