@import "variables.10ft";
@import '../ribbon-system.variables.10ft';
@import '../../../styles/mixins.scss';
@import './style';

core-ribbon-item {
    &.selected {
        &:after {
            @include green-asset-selection-check('big'); //See mixins.scss
        }
    }

    li.ribbon-item:not(.person) {
        &:hover {
            // This should be moved into the style.scss once 10ft supports quick play.
            .icon-quick-play {
                display: none;
            }
        }

        .sub-content {
            font-size: 22px;
            @include ellipsis-able();

            dt {
                font-size: 26px;
                margin: 10px 0 5px 0;
            }
        }

        i {
            font-size: 20px;
        }

        .delete-icon {
            width: 45px;
            height: 45px;
            line-height: 45px;
            font-size: 75px;
        }

        .asset-locked-overlay {
            background: $color-overlay-dark-gray;
            .fa-lock {
                font-size: 130px;
                color: $color-light-gray;
            }
        }
    }
    li.ribbon-item.person {
        .sub-content {
            font-size: 26px;
            text-align: center;

            .first_name {
                margin: 10px 0 0 0;
            }

            .last_name {
                margin: 0 0 10px 0;
            }
        }
    }

    li.ribbon-item:focus {
        &.core-focused-item .sub-content {
            @include focused-box();
        }
    }
}

@include scale-for-720p {
    core-ribbon-item {
        &.selected {
            &:after {
                @include green-asset-selection-check(); //See mixins.scss
            }
        }

        // Styling for the majority of asset items in ribbons. See below for people ribbon overrides.
        li.ribbon-item {
            height: $asset-item-height-720p;
            max-width: $asset-item-width-720p;

            .delete-icon {
                width: 30px;
                height: 30px;
                line-height: 30px;
                font-size: 50px;
            }

            .thumbnail-wrapper {
                height: $asset-image-height-720p;
            }

            .sub-content {
                // font-size is set in platform specific sheets
                height: $asset-metadata-height-720p;
                padding: 0 10px;

                .icon-record-single {
                    padding-right: 3px;
                }
            }

            dt {
                max-width: $asset-item-width-720p;
            }

            dd {
                display: inline;

                &::after {
                    content: "|";
                    color: $color-off-white;
                    padding-left: 6px;
                }

                &:last-child::after {
                    content: "";
                    padding-left: 0;
                }
            }
        }

        li.ribbon-item:not(.person) {
            .delete-icon {
                width: 30px;
                height: 30px;
                line-height: 30px;
                font-size: 50px;
            }
        }

        li.ribbon-item.person {
            max-width: $asset-item-people-width-720p;
            min-width: $asset-item-people-width-720p;

            .sub-content {
                font-size: 17px;

                .first_name {
                    margin-top: 7px;
                }

                .last_name {
                    margin-bottom: 7px;
                }
            }
        }

        li.ribbon-item:not(.person) {
            .sub-content {
                font-size: 15px;

                dt {
                    font-size: 17px;
                    margin: 7px 0 3px 0;
                }
            }

            i {
                font-size: 14px;
            }

            .asset-locked-overlay {
                .fa-lock {
                    font-size: 87px;
                }
            }
        }
    }
}
