@import '../../../styles/mixins.scss';

// My channels asset item
core-ribbon-item-my-channels {
    .ribbon-item.channel-tile {
        @include black-box($asset-border-size);
        @include flex-center();
        height: $asset-image-height;
        line-height: $asset-item-line-height;
        border: 1px solid $color-gray;

        &:focus {
            @include focused-box();
        }

        .alt-text {
            color: $color-near-white;
            display: none;
            width: 100%;
            @include font-size(big2);
            @include ellipsis-able();
            text-align: center;
            padding: 15px;
        }

        img,
        .alt-text {
            backface-visibility: hidden;
            opacity: 0.5;
            transition: opacity 250ms;
        }

        .thumbnail-error {
            display: none;

            & ~ .alt-text {
                display: inline-block;
            }
        }

        &:hover,
        &:focus {
            img ,
            .alt-text {
                opacity: 1;
            }
        }

        .check-mark {
            $asset-item-channel-check-offset: 10px;
            $asset-item-channel-check-width: 34px;
            $asset-item-channel-check-height: $asset-item-channel-check-width;

            position: absolute;
            color: $color-green;
            right: $asset-item-channel-check-offset;
            top: $asset-item-channel-check-offset;
            bottom: 0;
            left: auto;

            // font awesome check icon in the circle, hidden by default
            i {
                color: $color-green;
                display: none;
                margin: 0;
                font-weight: normal;
                font-variant: normal;
                speak: none;
                text-transform: none;
                line-height: 1;
                font-size: 1.5em;
            }
        }
    }
}
