// Styling for an individual channel in the micro-guide
core-micro-channel {
    .micro-channel {
        position: relative;
        flex: none;
        // width, height and padding set in platform stylesheets

        // vertical separator at left and following last channel created by this added border
        &::before,
        &:last-child::after {
            content: "";
            width: 0;
            position: absolute;
            border: 1px solid $color-shadow-gray;
            left: 0;
            // height, top and last-child left position set in platform stylesheets
        }

        // no vertical separator at left edge of the first channel
        &:first-child::before {
            border: none;
        }

        // hover state change background to gray and temporarily remove vertical border separators
        &:hover {
            background-color: $color-shadow-gray;
            cursor: pointer;
            &::before,
            &:last-child::after {
                border: none;
            }

        }

        // hover state also temporarily removes the vertical border separator of the _next_ channel
        &:hover + &::before {
            border: none;
        }

        .program-title {
            display: flex;

            i {
                display: inherit;
                align-items: center;
            }

            h3 {
                overflow: hidden;
                white-space: nowrap;
                color: $color-white;
            }
        }

        // Text items
        h4, p {
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            color: $color-off-white;
            // width, font-size and padding set in platform stylesheets
        }

        // metadata items
        h4 span {

            // vertical bars
            &::after {
                content: " |";
                color: $color-off-white;
            }

            // no bars on the last one
            &:last-child::after {
                content: "";
            }

            // 'New' in yellow
            &.is-new {
                color: $color-yellow;
            }
        }

        // Channel logo
        span.channel-logo {
            position: absolute;
            display: inline-block;
            text-align: center;
            // width and position set in platform stylesheets

            img {
                width: auto;
                // height set in platform stylesheets
            }
        }

        // Dim all the colors for the channel when parental lock in effect
        &.is-locked {
            .program-title {
                h3, i {
                    color: $color-light-gray;
                }
            }

            h4, h4 span::after, h4 span.is-new, p {
                color: $color-near-gray;
            }
            span.channel-logo {
                opacity: 0.5;
            }
        }

        // Parental controls lock icon and overlay
        .lock-overlay {
            position: absolute;
            top: 0;
            left: 0;
            z-index: $index-front;
            text-align: center;
            background-color: transparent;
            height: 100%;
            width: 100%;

            .icon-locked {
                position: relative;
                margin: auto;
                color: $color-cool-white;
                // font-size and top position set in platform stylesheets
            }
        }

        // Special behavior when the channel does not have enough data to render all details
        &.no-data {
            // We don't show any of the text
            h3, h4, p {
                display: none;
            }

            // Logos are centered and a bit larger, we only constrain the height
            // Width, height and top position are set in platform stylesheets
            .channel-logo {
                bottom: auto;
                margin: auto;
                img {
                    max-width: none;

                    // For images without a src attribute, or where src is empty, style alt text
                    &[src=''],
                    &:not([src]) {
                        display: inline-block;
                        position: relative;
                        padding: 0 1em;
                        // see additional styles in platform stylesheets
                    }
                }
            }
        }
    }
}
