@import "variables.10ft";
@import './style';

$margin-top-720p: 10px;
$pinpad-input-font-size-720p: 27px;
$pinpad-input-gutter-720p: 2px;
$pinpad-input-width-720p: 109px;
$pinpad-width-720p: ($pinpad-input-width-720p + $pinpad-input-gutter-720p) * 3;
$pinpad-row-width-720p: $pinpad-width-720p - $pinpad-input-gutter-720p;
$pinpad-button-font-size-720p: 19px;
$pinpad-button-height-720p: 50px;
$pinpad-splitter-height-720p: 13px;

@include scale-for-720p {
    core-modal-pinpad {
        .pinpad-modal {
            width: $pinpad-width-720p;

            .modal-header .pinpad-title {
                font-size: 17px;
                margin: $margin-top-720p 0 0 0;
            }

            .row {
                width: $pinpad-row-width-720p;
            }

            .pin-input {
                margin: 15px 0 $pinpad-splitter-height-720p 0;
                font-size: $pinpad-input-font-size-720p;
                height: 39px;
                width: 100%;
                padding: 0 13px 4px;
            }

            .pin-button + .pin-button {
                margin-right: $pinpad-input-gutter-720p;
            }

            .pin-button {
                width: $pinpad-input-width-720p;
                font-size: $pinpad-button-font-size-720p;
                height: $pinpad-button-height-720p;
                line-height: $pinpad-button-height-720p;
                margin: 0 $pinpad-input-gutter-720p $pinpad-input-gutter-720p 0;
                padding: 0 8px;

                .icon-backspace {
                    font-size: 15px;
                }
            }

            .pin-button-full {
                margin-top: $pinpad-splitter-height-720p;
                height: $pinpad-button-height-720p;
                width: $pinpad-row-width-720p;
                font-size: $pinpad-button-font-size-720p;
            }
        }
    }
}
