.modal {
  display: inline-block; }

.core-modal {
  background-color: #000000;
  border: solid #323232;
  box-shadow: 0 10px 40px 0 rgba(0, 0, 0, 0.6);
  display: inline-block;
  position: fixed;
  top: 50%;
  left: 50%;
