  z-index: 1050; }
  .core-modal .modal-header {
    margin-bottom: 10px; }
  .core-modal .modal-sub-header {
    margin-bottom: 5px; }
  .core-modal .modal-body {
    padding: 0; }
  .core-modal .modal-footer {
    margin-top: 30px;
    padding: 0; }

.modal-open .modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: none;
  overflow: hidden;
  outline: 0; }

.modal-backdrop, .core-modal-backdrop {
