@import "variables.10ft";
@import '../../../styles/mixins';
@import '../../../styles/variables.3ft';
core-search-bar {
    .search-bar {
        align-items: center;
        display: flex;
        height: 100%;
        min-width: 44px;
        position: relative;

        input {
            background-color: transparent;
            border: 0;
            color: $color-white;
            font-size: 0;
            height: 44px;
            outline: 0;
            position: relative;
            transition: width $hover-transition-speed;
            width: 0;
        }
        input::-ms-clear {
            display: none;
        }
        .show-button,
        .hide-button {
            background-color: transparent;
            border: 0;
            outline: 0;
            position: absolute;
            width: 44px;
        }
        .show-button {
            height: 100%;
            left: 0;
            opacity: 0.5;
            transition: opacity $hover-transition-speed;
            overflow: hidden;
            svg {
                width: 26px;
                height: 100%;
            }
            &:hover, &:focus, &.has-hover {
                opacity: 1;
                cursor: pointer;
                outline: none;
                border: none;
            }
            &:focus {
                transition: opacity $hover-transition-speed;
                svg g {
                    fill: $color-blue;
                }
            }
        }
        .hide-button {
            display: none;
            font-size: 42px;
            font-weight: 100;
            right: 0;
        }
        &.active {
            input {
                background-color: $color-gray;
                font-size: 1em;
                font-weight: 300;
                letter-spacing: 0.4px;
                padding: 0 44px;
                transition-duration: 300ms;
                width: 20vw;
            }
            .show-button {
                pointer-events: none;
                opacity: 0.5;
            }
            .hide-button {
                color: $color-white ;
                display: block;
                cursor: pointer;
            }
        }
        &.active-tab{
            .show-button {
                opacity: 1;
            }
        }
    }

    @media (max-width: $max-screen-width-tablet) {
        .search-bar{
            .show-button{
                padding: 0 7px;
                width: 4vw;
            }
            .hide-button {
                padding: 0 30px;
                width: 4vw;
            }
            input {
                height: 100%;
            }
            &.active {
                input{
                    width: 40vw;
                }
            }
        }
    }

    @include scale-for-tablet {
        .search-bar {
            .show-button,
            .hide-button {
                padding: 0 30px 0 7px;
                width: 4vw;
            }
            &.active {
                z-index: 3;
                input {
                    width: 100vw;
                }
            }
        }
    }
}
