@import "../../styles/mixins";

core-modal-watch-options {
    .modal-body {
        .header {
            text-align: center;
            color: $color-yellow;
            line-height: 36px;
            margin-bottom: 40px;
            font-size: 30px;
            text-transform: uppercase;
            letter-spacing: 0.5px;
            font-weight: 400;
        }

        .btn {
            display: block;
            width: 370px;
            margin: 12px 28px 0;
        }

        .btn:last-of-type {
            margin-top: 30px;
        }
    }

}
