@import '../../../styles/mixins.scss';

core-parental-controls {
    color: $color-light-gray;
    display: block;

    button.btn {
        margin: 20px 0;
    }

    hr {
        background-color: $color-gray;
        border: 0;
        //platform specific styles set height
        margin-bottom: 20px;
        margin-top: 35px;
    }

    .active {
        color: $color-white;
    }

    .parental-controls {

        #parental-controls {
            margin-bottom:20px;

            .clickable-row, .clickable-row-disabled {
                cursor: pointer;
                height: 37px;

                &:last-child {
                    margin-bottom: 0;
                }

                core-toggle, label, span {
                    float: left;
                }

                label {
                    cursor: pointer;
                    display: block;
                    width: 66.6%;
                }

                .toggle-label {
                    padding-left: 0;
                    padding-right: 1em;
                }
            }
            .clickable-row-disabled {
                color: $color-light-gray;
                cursor: initial;

                core-toggle, label, span {
                    pointer-events: none;
                }
            }
        }
    }

    .hidden-accessible-element {
        @include offscreen-accessible-element();
    }
}
