@import '../../../../styles/mixins';

// See style.win10.scss, style.10ft.scss, style.3ft.scss for platform specific styling.

// NOTE:
// Some changes were made to reflect the structure of the grid-channel.html to make it
// easier to style it per platform. Also removed few selectors that wasn't contributing
// to the styling of this page after the change.

core-grid-channel {
    display: block;
    position: absolute;
    width: $grid-guide-channel-width;
    // This needs to be on top of the schedule row because
    // the schedule row can scroll beneath it.
    z-index: $index-front;
    background-color: $color-light-black;
    box-shadow: inset -2px 0px 0px 0px $color-medium-gray;
    padding-right: 2px;

    &:hover {
        cursor: pointer;
    }

    .channel-container {
        // value for $grid-guide-row-height imported in platform specific sheets
        height: $grid-guide-row-height;
        align-items: center;
        border: none;
        display: flex;
        justify-content: center;
        overflow: hidden;

        i,
        .image-container {
            background-color: $color-light-black;
            height: 100%;
            @include flex-center();
            // value for $grid-guide-item-padding imported in platform specific sheets
            padding: $grid-guide-item-padding;

            &:hover {
                background-color: $color-light-medium-gray;

                img {
                    flex: 0 0 auto;
                    opacity: 1;
                }

                &.icon-mytv_off:before {
                    color: $color-white;
                }
            }
        }

        .image-container {
            width: 100%;

            img {
                opacity: 0.6;
                max-width: 100%;
                @include alt-text();
            }
        }
    }
}
