@import "../../../../styles/variables";

core-billing-form {
    .billing-form {
        width: 25.52vw;
        float: left;

        .form-group {
            width: 25.52vw;
        }

        .short-input:last-child {
            margin-right: 0px;
        }
    }

    .address-form {
        margin-left: 2.6vw;
        width: 25.52vw;
        float: left;

        .form-group {
            width: 25.52vw;

            .one-half-fieldset {
                width: 12.23vw;
                float: left;
                margin-right: 1.04vw;

                &:last-child {
                    margin-right: 0px;
                }
            }
        }
    }

    form {
        display: block;
        width: 33.33vw;

        fieldset {
            outline: none;
            border: none;
            padding: 0;
            margin: 0;

            .form-group {
                position: relative;
                margin-bottom: 1.85vh;
                width: 33.33vw;

                .alt-text {
                    display: none;
                    position: absolute;
                    white-space: nowrap;
                    top: 1.3vh;
                    right: 1.3vh;
                    font-size: 2.6vh;
                    width: 50%;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    text-align: right;
                }

                .cc-img {
                    position: absolute;
                    top: 13.5%;
                    right: 0.8vh;
                    height: 73%;
                }

                .thumbnail-error {
                    display: none;

                    & ~ .alt-text {
                        display: inline-block;
                    }
                }

                input {
                    display: block;
                    font-size: 26px;
                    color: $color-white;
                    font-weight: 600;
                    padding-left: 4px;
                    width: 100%;
                    height: 5.5vh;
                    box-shadow: none;
                    padding-right: 2px;
                    background-color: $color-medium-gray;
                    margin: 0;
                    border: 6px solid $color-medium-gray;
                    border-radius: 0;
                    outline: none;

                    &.core-focused-item {
                        border-color: $color-dark-blue;
                    }

                    &.ng-invalid.ng-dirty {
                        border-color: $color-red;
                    }
                }

                &.short-input {
                    width: 7.81vw;
                    float: left;
                    margin-right: 1.04vw;
                }

                &.cc-cvv {
                    float: right;
                    margin-right: 0;
                }

                &.zip-code {
                    width: 16.66vw;
                }
            }
        }

        .coreValidationToolTip {
            margin-top: 0.92vh;
            z-index: 1;
        }
    }

    .legal-copy {
        font-size: 21px;
        font-weight: 400;
        color: $color-off-white;
        position: absolute;
        bottom: 10.27vh;
        width: 52.34vw;
    }
}
