@import "variables.10ft";
@import "../../../styles/base";

.last-channel-wrapper{
    height: 192px;
    width: 660px;
    padding: 60px 37px 60px 75px !important;

    .modal-body{
        color: $color-white;
        text-align: center;
        float: left;
        width: 100%;
        height: 100%;
        display: flex;
        flex-flow: row wrap;
        align-items: center;

        .channel-recall-icon {
            fill: white;
            padding-right: 6px;

            svg {
                height: 35px;
                width: 39px;
            }
        }

        .channel-recall-text {
            font-size: 45px;
            height: 35px;
            width: 501px;
            padding-left: 6px;
            color: $color-white;
        }
    }
}

// TODO this is a bit of a hack to make sure webpack includes these files, these styles are not actually used.
.channel-recall-icon {
    background: url('../../../assets/images/channel-recall.svg');
}

@include scale-for-hd-tv {

    .last-channel-wrapper {
        height: 129px;
        width: 442px;
        padding: 40px 25px 40px !important;

        .modal-body {
            .channel-recall-icon {
                padding-right: 4px;

                svg {
                    height: 23px;
                    width: 26px;
                }
            }

            .channel-recall-text {
                font-size: 30px;
                height: 23px;
                width: 335px;
                padding-left: 4px;
            }
        }
    }
}
