@import "variables.10ft";
@import './style';
@import '../../../styles/mixins';

@include scale-for-720p {
    core-record-button-player-control {
        div.player-record-action {
            width: 133px;
        }

        button.record-button {
            span.button-icon {
                width: 66px;
                height: 69px;
                padding-top: 13px;
                margin: 0 auto 5px;

                .icon-rec-single {
                    font-size: 40px;
                }
