@import "variables.10ft";
@import "../../../styles/variables";
@import '../../../styles/mixins';

core-support {
    color: $color-white;

    .support-item {
        margin-bottom: 40px;

        .support-item-header {
            font-size: $font-size-bigger2;
            font-weight: 700;
        }

        .support-item-detail {
            font-size: $font-size-big2;
            margin-left: 10px;
        }
    }

    .support-debug-logging {
        display: flex;
        margin-top: 20px;
        .support-debug-logging-header {
            font-size: $font-size-bigger2;
            padding-top: 5px;
        }

        core-toggle {
            margin-right: 10px;
        }
    }

    .hidden-accessible-element {
        @include offscreen-accessible-element();
    }

        @include scale-for-720p {
            .support-item {
                margin-bottom: 25px;
                .support-item-header {
                    font-size: $font-size-medium;
                }

                .support-item-detail {
                    font-size: $font-size-small;
                    margin-left: 7px;
                }
            }

        .support-debug-logging {
            margin-top: 13px;

            core-toggle {
                margin-right: 10px;
            }

            .support-debug-logging-header {
                font-size: $font-size-medium;
                padding-top: 0;
            }
        }
    }
}
