@import "variables.10ft";
@import './style';
@import "../../styles/mixins";
$sling-logo-width: 190px;
$sling-logo-width-720p: 127px;

// These styles pertain to the top-nav items, and the top-nav container itself
core-main-nav {
    background-color: $color-black;
    display: block;
    height: $nav-dimensions-desktop;
    position: relative;
    top: 0;
    width: 100vw;
    .top-nav-bar {
        @include disable-select;
        background-color: $color-black;
        color: $color-warm-gray;
        font-size: $font-size-smallest;
        font-weight: bold;
        height: $nav-dimensions-desktop;
        padding-right: 30px;
        .top-nav-scrolling-area {
            display: flex;
            height: 100%;
        }

        .sling-logo {
            border: 15px solid $color-black;
            float: left;
            font-size: 0;
            height: 100%;
            margin-left: 85px;
            margin-right: 75px;
            width: $sling-logo-width;
        }

        .dashboard-menu-item {
            padding: 0 5px;
            display: inline-block;

            &.focus-grid-focused {
                @include focused-nav;
            }
            &.has-hover:not(.icon-wrapper) {
                @include hovered-nav;
                img {
                    opacity: 1;
                }
            }
            span {
                font-size: $font-size-biggest;
                line-height: $nav-dimensions-desktop;
                overflow: hidden;
            }
            svg {
                width: 40px;
                height: 40px;
                g {
                    transition: fill 35ms linear;
                }
            }
            .filter-icons {
                display: none;
                font-size: 57px;
                right: 5px;
                margin-top: -60px;
                text-align: center;
                &.hidden {
                    display: none;
                }

                .icon-filter1 {
                    color: $color-white;
                }
            }
            .active-filter {
                display: block;
                .icon-filter1 {
                    color: $color-yellow;
                }
            }
            &.active, &:hover {
                .filter-icons {
                    display: block;
                }
            }
            &:focus {
                .filter-icons {
                    margin-bottom: -2px;
                }
            }
            &.icon-wrapper {
                width: 100px;
                &.has-hover, &.focus-grid-focused {
                    background-color: transparent;
                    opacity: 1;
                    outline: none;
                    border: none;
                    transition: opacity $hover-transition-speed;
                }

                &.focus-grid-focused {
                    span {
                        margin: 0;
                    }

                    svg g {
                        fill: $color-blue;
                    }
                }
            }
        }

        #search {
            margin-left: auto;
        }
    }
}
@include scale-for-720p {
    core-main-nav {
        height: $nav-dimensions-desktop-720p;
        .top-nav-bar {
            font-size: $font-size-big;
            height: $nav-dimensions-desktop-720p;
            padding-right: 52px;

            .sling-logo {
                border: 10px solid $color-black;
                height: 100%;
                margin-left: 57px;
                margin-right: 38px;
                width: $sling-logo-width-720p;
            }

            .dashboard-menu-item {
                font-size: $font-size-big;
                height: $nav-dimensions-desktop-720p;
                .text {
                    line-height: $nav-dimensions-desktop-720p;
                    padding: 0 20px;
                }
                img {
                    width: 32px;
                    height: 32px;
                }
                .filter-icons,
                &.active-filter {
                    margin-top: -34px;
                    font-size: 38px;
                }
            }

            .icon-wrapper {
                padding: 38px 0px;
            }

        }
    }
}
