@import "variables.10ft";
$transition-duration: 0.3s;

@import '../../../styles/variables';
@import '../../../styles/mixins';

.toggle {
    .toggle-container {
        display: block;

        .toggle-content {
            border: 2px solid transparent;
            border-radius: 100px;
            height: 40px;
            width: 74px;
            &.core-focused-item {
                border-color: $color-light-blue;
            }
        }

        .toggle-button {
            background-color: $color-black;
            height: 25px;
            width: 60px;
            border: 2px solid $color-off-white;
            border-radius: 50px;
            border-color: $color-off-white;
            left: 5px;
            display: inline-block;
            position: relative;
            top: 5px;
            transition: background-color $transition-duration;
            cursor: pointer;

            .toggle-switch {
                background-color: $color-light-gray;
                height: 15px;
                position: absolute;
                top: 3px;
                left: 2px;
                transition: left $transition-duration;
                display: inline-block;
                border-radius: 50px;
                width: 15px;
            }

            &.on {
                background-color: $color-yellow;
                border: none;

                .toggle-switch {
                    background-color: $color-white;
                    left: 42px;
                    top: 5px;
                }
            }
        }
    }
}
