@import "variables.10ft";
@import './style';

core-upgrade-details {
    .upgrade-content {
        padding: 5vw;

        .upgrade-title {
            font-size: 4.5vmin;
            padding-bottom: 2vmin;
            line-height: 1.2;
        }

        .upgrade-description {
            height: 26vh;
            margin-bottom: 1vmin;
            overflow: hidden;
            line-height: 5.2vh;
        }

        .upgrade-text-medium {
            color: $color-white;
            font-size: 3.75vmin;
            padding-bottom: 2vmin;
        }

        .upgrade-text-small {
            font-size: 2.25vmin;
            padding-bottom: 2vmin;
        }

        .upgrade-details-left {
            display: inline-block;
            width: 66%;
            padding-right: 8vmin;

            core-details-button-row {
                padding-bottom: 2vmin;
            }
        }

        .upgrade-details-right {
            width: 33%;

            .upgrade-thumbnail {
                width: 100%;
            }
        }
    }

    .upgrade-included-channel-icon {
        width: 9vw;
        height: 6vw;
        margin: .6vw 1vw;
    }

    .featured-channels {
        padding-top: 1vh;
    }

    .featured-channel-dots {
        color: $color-light-gray;
        margin: 0;
        float: right;
        padding-top: .5vmin;

        i.fa + i.fa {
            padding-left: .5vmin;
        }

        .fa-circle {
            font-size: 2vmin;
        }
        .current {
            color: $color-yellow;
        }
    }
}
