@import "variables.10ft";
@import "../../../styles/base";

@include scale-for-hd-tv {
    core-edit-my-channels {
        .edit-my-channels-container {
            core-edit-my-channels-container-header {
                padding: 26px 88px;
                span {
                    font-size: 21px;
                }
            }
        }

        #edit-my-channels-tiles-container {
            .channel-tile {
                margin-bottom: 18px;
                margin-right: 18px;
                width: 203px;
                height: 140px;
            }
        }
    }
    core-ribbon-item-my-channels {
        .ribbon-item.channel-tile img {
            max-width: 121px;
        }
    }
}
