@mixin flex-box {
  display: flex;
}

@mixin align-items($type) {
  -webkit-align-items: $type;
  align-items: $type;
}

@mixin make-transform($x, $y) {
  -webkit-transform: translate($x, $y);
  transform: translate($x, $y);
}

@mixin vertical-horizontal-center {
  @include make-transform(-50%, -50%);
  position: fixed;
  left: 50%;
  top: 50%;
}

@mixin include-icon($x, $y) {
  width: $x;
  height: $y;
  display: inline-block;
  margin: 0 auto;
}

@mixin hidden-button() {
    visibility: hidden;
    position: absolute;
    width: 1px;
    height: 1px;
    left: 50%;
    top: 0;
}

@mixin fixed-position() {
    position: fixed;
}
