@import '../../styles/mixins';

.empty-page-container{

    position: relative;
    margin: 100px auto 0 auto;
    text-align: center;
    width: 460px;

    .icon-ghost {
        display: inline-block;
        font-size: 100px;
        color: $color-off-white;
    }

    .empty-page-title{
        color: $color-yellow;
        margin-top: 10px;
        font-size: $font-size-big;
        font-weight: bold;
        text-transform: uppercase;
    }

    .empty-page-description{
        color: $color-light-gray;
        font-size: $font-size-medium;
        margin-top: 0.5em;
    }


    &.prospect {
        width: 700px;

        .btn.subscribe {
            margin-top: 50px;
            min-width: 200px;
            font-size: 20px;
            font-weight: 500;
        }

    }

    @include scale-for-mobile {
        width: 94%;
        max-width: 460px;
        margin-top: 20px;

        &.prospect {
            width: 94%;
        }

        .icon-ghost {
            font-size: 70px;
        }

        .empty-page-title {
            font-size: 22px;
        }

        .empty-page-description{
            font-size: 18px;
        }

        .btn.subscribe {
            margin-top: 20px;
        }
    }
}
