// ==== APP-WIDE VARIABLES ======================================
// Pan-platform global variables should be defined here.
//
// This sheet should be imported at the beginning of every
// ./variables.UI_TYPE.scss sheet which are ingested during our webpack build

// Font Size Restrictions (min/max)
$font-size-map-min: (
    smallest:   12px,
    smaller:    13px,
    small:      14px,
    medium:     15px,
    big:        16px,
    big2:       18px,
    bigger:     20px,
    biggest:    24px,
    biggest2:   26px
);

$font-size-map: (
    smallest:   14px,
    smaller:    15px,
    small:      16px,
    medium:     17px,
    big:        18px,
    big2:       20px,
    bigger:     23px,
    biggest:    27px,
    biggest2:   29px
);

$font-size-map-max: (
    smallest:   15px,
    smaller:    16px,
    small:      17px,
    medium:     18px,
    big:        20px,
    big2:       22px,
    bigger:     26px,
    biggest:    30px,
    biggest2:   32px
);


// Core-UI variables
$font-size-smallest2: 14px;
$font-size-smallest: 16px;
$font-size-smaller: 18px;
$font-size-small: 20px;
$font-size-medium: 24px;
$font-size-big: 26px;
$font-size-big2: 30px;
$font-size-bigger: 32px;
$font-size-bigger2: 35px;
$font-size-biggest: 36px;
$font-size-biggest2: 40px;

// z-indexing
$index-overlay: 2;
$index-front:   1;
$index-middle:  0;
$index-back:   -1;

