// ==== COMPONENT-BASED VARIABLES ==================================

// Dashboard
$dashboard-left-padding: 30px;

// Device Screen Widths
$screen-width-desktop: 1200px;
$max-screen-width-tablet: $screen-width-desktop - 1px;
$max-screen-width-mobile: 480px;

// Menu and top navigation dimensions
$nav-dimensions-desktop: 140px;
$nav-dimensions-tablet: 140px;

// Side Menu
$side-menu-width-expanded: 256px;

// Focused Tile Border
$focused-tile-border-width: 0.7408vh;

$focused-tile-border-width-in-px: 8px;

$focused-tile-border-720p-width-in-px: 6px;
