@import "variables.10ft";
@import '../ribbon-system.variables.10ft';
@import './style';

core-scrolling-ribbon {
    display: block;

    .horizontal-scroll-container {
        overflow-x: scroll;
    }

    .ribbon-content {
        display: inline-flex;
    }

    .ribbon-title {
        font-weight: 500;
        line-height: 50px;
        padding: 15px 0 15px $ribbon-left-gutter;
    }

    &.scrolling-ribbon__empty .ribbon-content {
        &::after {
            background: repeating-linear-gradient(to right, rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0.25) 512px, transparent 512px, transparent 532px);
            content: '';
            flex: 1;

        }

        width: 100%;
    }

    // Temporary Hack for the 10ft branches decided to override h3 element font size.
    .core-ribbon__error {
        font-size: 34px;
    }
}

@include scale-for-720p {
    core-scrolling-ribbon {
        .ribbon-title {
            line-height: 33px;
            padding: 10px 0 10px $ribbon-left-gutter-720p;
        }

        h2.ribbon-title {
            span.message {
                margin-left: 13px;
            }
        }

        .core-ribbon-section {
            min-height: $asset-item-height-720p;
            padding-bottom: 17px;
        }

        &.scrolling-ribbon__empty .ribbon-content::after {
            background: repeating-linear-gradient(to right, rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0.25) 342px, transparent 342px, transparent 355px);
        }
        .core-spinner {
            min-height: $asset-item-height-720p;
        }

        .core-ribbon__error {
            padding: $asset-item-height-720p / 2;
        }
    }

    // The My Channels ribbon isn't as tall
    &.favorite-channels-ribbon .core-ribbon-section {
        min-height: $my-channels-asset-tile-height;
    }
}
