@import '../../../../styles/mixins.scss';

core-screen-tile-micro-guide {
    line-height: normal;

    li.micro-guide-tile {
        list-style-type: none;
        flex: none;
        position: relative;
        cursor: pointer;

        &:hover {
            @include hovered-box();
            .tile-attributes {
                color: $color-white;
            }
        }

        &:focus {
            @include focused-box();

            .tile-title, .tile-attributes {
                color: $color-white;
            }

            &:hover {
                @include focused-box();
            }
        }

        .tile-title {
            @include ellipsis-able();
            color: $color-white;
            font-weight: 500;
        }

        .tile-attributes {
            @include ellipsis-able();
            color: $color-off-white;
            font-weight: 400;
            min-height: 30px;
        }

        dd {
            display: inline;
        }

        .channel-logo {
            position: absolute;

            img {
                bottom: 0;
                float: right;
                right: 0;
                width: auto;
            }

            .thumbnail-error {
                display: none;

                & ~ .alt-text {
                    display: inline-block;
                }
            }

            .alt-text {
                @include ellipsis-able();
                display: none;
                float: right;
                max-width: 100%;
            }
        }

        .asset-locked-icon {
            float: left;
            margin-right: 8px;
        }
    }
}