@import "variables.10ft";
@import './style';
@import '../../../styles/mixins';
@import '../guide-container.variables.10ft.scss';

// LG (10ft) Styles for the core channel filter button used to display the Guide's channel filter modal.
core-channel-filter {
    button.btn {
        background-color: $color-medium-gray;
        white-space: nowrap;
        font-size: $guide-header-component-font-size;

        &:hover {
            background-color: $color-dark-blue;
            color: $color-white;
            white-space: nowrap;
        }

        &:focus {
            font-size: $guide-header-component-font-size;
        }
    }

    button.btn.black {

        &:hover {
            background-color: $color-dark-blue;
            color: $color-white;
            white-space: nowrap;
            i:before {
                color: $color-cool-white;
            }
        }

        &.has-hover {
            background-color: $color-near-gray;
            color: $color-white;
            text-decoration: none;

            // icon hover state color
            i:before {
                color: $color-white;
            }
        }

        &.focus-grid-focused {
            color: $color-white;
            background-color: $color-dark-blue;
            outline: none;

            // icon hover state color
            i:before {
                color: $color-white;
            }
        }

    }
}

@include scale-for-720p {
    core-channel-filter {
        button.btn {
            font-size: $guide-header-component-font-size-720p;
            height: 50px;

            &:focus {
                font-size: $guide-header-component-font-size-720p;
            }
        }
    }
}
