// Shared styling for the channel filter. Note some overrides in platform-specific stylesheets.
@import '../../../../styles/mixins.scss';
core-channel-filter-modal {
    .core-modal {
        // padding and width set in UI_TYPE sheets

        .modal-header {
            // margin-bottom is set in UI_TYPE sheets
        }

        .modal-body {
            max-height: 500px;
            position: relative;

            &:before, &:after {
                display: none;
                content: '';
                position: absolute;
                left: 0;
                width: 100%;
                background: black;
                z-index: $index-front;
                height: 60px;
                pointer-events: none;  // make sure that mouse interactions are ignored on gradients elements
            }

            &:before {
                top: -1px;
                background: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
            }

            &:after {
                bottom: -1px;
                background: linear-gradient(to top, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
            }

            &.show-top-gradient {
                &:before {
                    display: block;
                }
            }

            &.show-bottom-gradient {
                &:after {
                    display: block;
                }
            }

            .vertical-scroll-container {
                max-height: 500px;
                overflow-y: auto;
            }

            .hidden-accessible-element {
                @include offscreen-accessible-element();
            }
        }
    }

    .radio-buttons {
        // column-count is set in UI_TYPE (applicable for win10) sheets

        core-radio {
            // margin-bottom is set in UI_TYPE sheets
        }
    }

    .modal-footer {
        // text-align is set in UI_TYPE sheets
    }

    core-radio {
        // margin-bottom is set in UI_TYPE sheets
    }
}
