@import "../../../styles/variables";
@import '../../../styles/mixins';

core-account {

    .accounts-heading {
        width: 9.3vw;
        height: auto;
        font-size: 35px;
        font-weight: 500;
        color: $color-white;
    }

    .user-details {
        width: auto;
        height: auto;
        font-size: 35px;
        color: $color-white;
        line-height: normal;
    }

    .services-heading {
        width: 7vw;
        height: auto;
        font-size: 35px;
        font-weight: 500;
        color: $color-white;
    }

    .accounts-button {
        padding-top: 15px;
        padding-bottom: 15px;
        margin-bottom: 3vh;


        .button-text-normal {
            width: 11.5vw;
            height: 7.8vh;
            font-size: 35px;
            text-align: center;
        }
    }

    .hidden-accessible-element {
        @include offscreen-accessible-element();
    }

}
