@import "variables.10ft";
@import '../../../../ribbon-system/ribbon-system.variables.10ft';
@import './style';
@import '../../../../../styles/mixins';

core-edit-my-channels-container-header {
    padding: 40px $ribbon-left-gutter;

    // The individual headings
    span {
        font-size: $font-size-biggest;
    }

    .btn {
        margin-right: 10px;
        height: 60px;
        padding: 0 20px 0 20px;

        &.focus-grid-focused {
            padding: 0 20px 0 20px;
            height: 60px;
        }

        &:focus {
            padding: 0 20px 0 20px;
            height: 60px;
        }
    }
}

@include scale-for-720p {
    core-edit-my-channels-container-header {
        padding: 27px $ribbon-left-gutter-720p;

        // The individual headings
        span {
            font-size: $font-size-medium;
        }

        .btn {
            margin-right: 7px;
            height: 40px;
            padding: 0 13px 0 13px;

            &.focus-grid-focused {
                padding: 0 13px 0 13px;
                height: 40px;
            }

            &:focus {
                padding: 0 13px 0 13px;
                height: 40px;
            }
        }
    }
}
