// ==== UTILITY MIXINS ==================================
$hover-transition-speed: 200ms;
$animation-speed: 500ms;

@mixin box-align($display: flex, $align-items: center, $flex-direction: column, $justify-content: center) {
    display: $display;
    align-items: $align-items;
    flex-direction: $flex-direction;
    justify-content: $justify-content;
}

// We need both a function and mixin form, for cases where multiple transform
// properties need to be applied (like for spinners)
@function translate-center($size) {
    $halfSize: ($size / 2);

    @return translate(calc(50vw - #{$halfSize}), calc(50vh - #{$halfSize}));
}

@mixin translate-center($size) {
    $halfSize: ($size / 2);

    // Hash bracket syntax because sass can't evaluate calc with variables
    transform: translate(calc(50vw - #{$halfSize}), calc(50vh - #{$halfSize}));
}

@mixin ellipsis-able() {
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    // Height and width should be set on recipient.
}

@mixin fade-in($speed: $animation-speed) {
    @include keyframes(fade-in) {
        0%   { opacity: 0; }
        100% { opacity: 1; }
    }
    @include animation('fade-in' $speed);
}

@mixin fluid-bg-img() {
    height: 100%;
    width: 100%;
    position: relative;
    display: -webkit-box;
    -webkit-box-pack: center;
    box-pack: center;
    -webkit-box-align: center;
    box-align: center;
}

@mixin full-absolute() {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
}

@mixin full-center() {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

@mixin position($position: absolute, $top: 0, $right: 0, $bottom: 0, $left: 0) {
    position: $position;
    top: $top;
    right: $right;
    bottom: $bottom;
    left: $left;
}

@mixin flex-center() {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

// easy animation styles
@mixin animate($name) {
    -webkit-transition: $name $animation-speed ease;
    -moz-transition: $name $animation-speed ease;
    -ms-transition: $name $animation-speed ease;
}

// Actual keyframe-based animation for cross-browser usage
@mixin animation($animate...) {
    $animations: '';
    $max: length($animate);

    @for $i from 1 through $max {
        $animations: #{$animations + nth($animate, $i)};

        @if $i < $max {
            $animations: #{$animations + ", "};
        }
    }
    -webkit-animation: $animations;
    -moz-animation: $animations;
    -o-animation: $animations;
    animation: $animations;
}

// For use with animation mixin above, creates keyframes that work across browsers
// with an animation name
@mixin keyframes($animationName) {
    @-webkit-keyframes #{$animationName} {
        @content;
    }
    @-moz-keyframes #{$animationName} {
        @content;
    }
    @-o-keyframes #{$animationName} {
        @content;
    }
    @keyframes #{$animationName} {
        @content;
    }
}

// An example implementation of the above animation mixins for a spinner
@mixin spinner {
    @include keyframes(spin) {
        0% {
            transform: rotate(0deg);
        }
        100% {
            transform: rotate(360deg);
        }
    }

    @include animation("spin 1s infinite linear");
}

// consistent border styling (non-vmins)
@mixin border-radius($size) {
    border-radius: $size;
    -webkit-border-radius: $size;
}

@mixin box-shadow($top, $left, $blur, $color, $inset: false) {
    @if $inset {
        -webkit-box-shadow: inset $top $left $blur $color;
        -moz-box-shadow: inset $top $left $blur $color;
        box-shadow: inset $top $left $blur $color;
    } @else {
        -webkit-box-shadow: $top $left $blur $color;
        -moz-box-shadow: $top $left $blur $color;
        box-shadow: $top $left $blur $color;
    }
}

// gradient fills - invisible to black is the default
@mixin vertical-gradient($startColor: $color-overlay-invisible, $endColor: $color-black) {
    background-color: $startColor;
    background: -webkit-gradient(linear, left top, left bottom, from($startColor), to($endColor));
    background: -webkit-linear-gradient(top, $startColor, $endColor);
    background: -moz-linear-gradient(top, $startColor, $endColor);
    background: -ms-linear-gradient(top, $startColor, $endColor);
    background: -o-linear-gradient(top, $startColor, $endColor);
}

@mixin horizontal-gradient($startColor: $color-overlay-invisible, $endColor: $color-black) {
    background-color: $startColor;
    background: -webkit-gradient(linear, left top, right top, from($startColor), to($endColor));
    background: -webkit-linear-gradient(left, $startColor, $endColor);
    background: -moz-linear-gradient(left, $startColor, $endColor);
    background: -ms-linear-gradient(left, $startColor, $endColor);
    background: -o-linear-gradient(left, $startColor, $endColor);
}

// the collective styling for hovered assets in a ribbon
@mixin hovered-box {
    background-color: $color-gray;
    color: $color-white;
    outline: none;
    text-decoration: none;
    cursor: pointer;
}

@mixin hovered-nav {
    background-color: $color-darker-gray;
    outline: none;
    opacity: 1;
}

// the collective styling for focused assets in a ribbon
@mixin focused-box {
    background-color: $color-dark-blue;
    color: $color-white;
    outline: none;
    text-decoration: none;
    cursor: pointer;
}

@mixin focused-nav {
    background-color: $color-darker-gray;
    outline: none;
    opacity: 1;
    border-bottom: 2px solid $color-blue;
}

@mixin active-nav {
    outline: none;
    opacity: 1;
}

// the collective styling for active assets in a ribbon
@mixin active-box {
    background-color: $color-blue;
    color: $color-white;
    outline: none;
    text-decoration: none;
    cursor: pointer;
    box-shadow: none;
}

// the collective styling for black assets in a ribbon
// this mixin should be used as a default on boxed element
@mixin black-box($border-size) {
    background-color: $color-black;
    color: $color-off-white;
    outline: none;
    text-decoration: none;
    cursor: pointer;
}

// disable user select
@mixin disable-select() {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

// Outputs responsive font size
@mixin font-size($size) {
    @if map_has_key($font-size-map, $size) {
        @media (max-width: 480px) {
            $font-size: map_get($font-size-map-min, $size);
            font-size: $font-size;
        }
        @media (max-width: 767px) {
            $font-size: map_get($font-size-map, $size);
            font-size: $font-size;
        }
        @media (min-width: 769px) {
            $font-size: map_get($font-size-map-max, $size);
            font-size: $font-size;
        }
    }
}

@mixin scale-layout() {
    // included here bare bones to support platforms that wish to extend the layout with media queries
    @content;
}

@mixin formLabels {
    @include font-size(big);
    display: block;
    color: $color-warm-gray;
}

// Draws a green check mark using the border and a rotate transform.
// Used on the DVR management screen. 10-ft style.
@mixin green-asset-selection-check($size: 'small') {
    content: " ";
    display: block;
    border: solid $color-green;
    transform: rotate(45deg);

    @if $size == 'big' {
        @include position($top: 25px, $right: 25px, $bottom: auto, $left: auto);
        width: 13px;
        height: 26px;
        border-width: 0 5px 5px 0;
    } @else {
        @include position($top: 20px, $right: 20px, $bottom: auto, $left: auto);
        width: 9px;
        height: 19px;
        border-width: 0 3px 3px 0;
    }
}

@mixin offscreen-accessible-element {
    clip: rect(1px, 1px, 1px, 1px);
    height: 1px;
    overflow: hidden;
    position: absolute;
    white-space: nowrap;
    width: 1px;
}

@mixin alt-text {
    overflow: hidden;
    background-color: inherit;
    z-index: 0;

    &:after {
        background-color: inherit;
        content: attr(alt);
        color: $color-white;
        font-weight: bold;
        text-align: center;
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
    }
}

// HD TV models specific styling(needs to be called in root/global level of the cascade)
@mixin scale-for-720p() {
    @media (max-width: 1280px) {
        @content;
    }
}

@mixin scale-for-responsive() {
    @media only screen and (max-width: 1024px) { //covers both mobile and tablet
        @content;
    }
}

@mixin scale-for-mobile() {
    @media only screen and (max-width: 767px) {
        @content;
    }
}

@mixin scale-for-tablet() {
    @media only screen and (min-width: 768px) and (max-width: 1024px) {
        @content;
    }
}

@mixin scale-for-larger-tablet-landscape() {
    @media only screen and (max-width: 1366px) and (orientation : landscape) {
        @content;
    }
}

@mixin scale-for-small-mobile() {
    @media only screen and (max-width: 480px) {
        @content;
    }
}

@mixin top-left() {
    position: absolute;
    left: 0;
    top: 0;
    display: flex;
    align-items: center;
}

@mixin scale-for-touchscreen-devices() {
    @media (any-pointer: coarse) {
        @content;
    }
}

@mixin show-scrollbar {
    -ms-overflow-style: scrollbar;
}

@mixin hide-scrollbar {
    -ms-overflow-style: none;
}

@mixin focused-tile-border {
    border: $focused-tile-border-width-in-px solid $color-dark-blue;
    @include scale-for-720p() {
        border: $focused-tile-border-720p-width-in-px solid $color-dark-blue;
    }
}

@keyframes shine-lines {
    0% {
        background-position: -700px 0
    }
    100% {
        background-position: 700px 0
    }
}

@mixin shine-animation {
    animation: shine-lines 1.3s infinite linear;
    animation-fill-mode: forwards;
    background: $color-light-cool-gray;
    background-image: linear-gradient(to right, $color-light-cool-gray 0%, $color-matte-gray 20%, $color-matte-gray 30%, $color-light-cool-gray 100%);
    background-repeat: no-repeat;
    position: relative;
}

@mixin upscaled-tile {
    transform: scale(1.1537, 1.1837); // (((((480 - 416) * 100) / 416) + 100 ) / 100), (((((277 - 234) * 100) / 234) + 100 ) / 100)
    margin-top: 0vh; // To achieve 22px top and bottom  between scaled tile and normal tile.
    z-index: $index-overlay;
    transition: transform 100ms ease-out;
}

@mixin upscaled-tile-3ft {
    transform: scale(1.1);
    transition: transform 200ms ease-in;
    transition-delay: 100ms;
}

@mixin tile-scale-down {
    transform: scale(0.8453); // (1 / scale-up y component)
}

@mixin upscaled-grid-tile {
    transform: scale(1.077, 1.1068); // (448, 259)
    margin-top: 0vh; // To achieve 16px top and bottom  between scaled tile and normal tile.
    z-index: $index-overlay;
    transition: transform 100ms ease-out;
}

@mixin grid-tile-scale-down {
    transform: scale(0.9035); // (1 / scale-up y component)
}
