@import '../../../styles/mixins';

core-rental-confirm {

    .rental-poster {
        height: 280px;
        text-align: center;

        img {
            height: 100%;
            border-style: solid;
            border-width: 2px;
            border-color: $color-light-gray;
        }
    }

    .rental-controls {
        width: 580px;
        margin-top: 20px;
        text-align: right;
    }

    .rental-buttons {
        margin-top: 20px;
    }

    .rental-buttons button {
        text-transform: uppercase;
    }

    .rental-controls p {
        text-align: left;
        margin-bottom: 20px;
    }

    .rental-error {
        color: $color-yellow;
        border: 1px solid $color-light-gray;
        background: $color-light-black;
        border-radius: 5px;
        margin-top: 5px;
        padding: 5px;

        .modal-close-btn {
            position: relative;
            float: right;
            right: 0px;
            top: 0px;
        }
    }

    .offscreen-text {
        @include offscreen-accessible-element();
    }
}
