@import "variables.10ft";
@import '../../../../styles/variables';
@import '../../../../styles/mixins';

// Html Keypad
$keypad-font-size: 24px;
$keypad-width: 472px;
$keypad-input-height: $keypad-width;
$keypad-btn-width: 52px;
$keypad-btn-modal-width: 255px;
$keypad-btn-height: $keypad-btn-width;
$keypad-btn-padding: $keypad-width;
$keypad-btn-margin: 4px;

core-modal-container {
    .core-modal {
        padding: 36px;
    }

    .core-modal.key-pad {

        .modal-header {
            margin-bottom: 10px;

            .keypad-title {
                color: $color-yellow;
                @include font-size(bigger);
                font-weight: bold;
                text-align: center;
                text-transform: uppercase;
                margin-bottom: 15px;
            }
        }

        .modal-keypad-container {
            background: transparent;
            padding: 0;
            margin-left: 4px;
            height: auto;
            width: $keypad-width;

            .form-control {
                margin-bottom: 20px;
                height: 52px;
                padding: 6px 20px;
                font-size: 30px;
                background-color: $color-cool-gray;
                border: 6px solid $color-cool-gray;
                width: 100%;
                color: $color-white;
            }

            // each type of key, based on a single key-size
            .keypad-btn {
                text-align: center;
                font-size: $keypad-font-size;
                width: $keypad-btn-width;
                height: $keypad-btn-height;
                line-height: $keypad-btn-height;
                margin: 0;
                background-color: $color-cool-gray;
                color: $color-white;
                border: none;
                justify-content: center;
                align-items: center;
                @include border-radius(0);

                &.wide {
                    width: 78px;
                    font-size: $font-size-small;
                }

                &.ex-wide {
                    width: 108px;
                }

                +.keypad-btn {
                    margin-left: $keypad-btn-margin;
                }

                &.spacebar {
                    width: 95px;
                }

                &[disabled] {
                    color: $color-gray;
                }

                &:hover {
                    background-color: $color-shadow-gray;
                    color: $color-white;
                }

                &:active,
                &.core-focused-item {
                    color: $color-white;
                    background-color: $color-dark-blue;
                    outline: none;
                    text-decoration: none;
                }

                .svg {
                    display:inline;
                    svg {
                        height: 20px;
                        margin: -1px 2px;
                    }
                }
            }

            .key-row {
                display: flex;
                justify-content: space-evenly;
                width: $keypad-width;
                margin-bottom: $keypad-btn-margin;
            }

            .modal-footer {
                margin-top: 0px;
                display: flex;
                justify-content: space-evenly;

                .keypad-btn {
                    width: 100%;
                }
            }
        }
    }
}
