@import "variables.10ft";
@import "../../../styles/base.win10";

.volume-controls {

    .fa {
        color: $color-near-white;

        &:hover {
            color: $color-white;
        }

        &:active {
            color: $color-white;
        }
    }

    .fa.background {
        color: $color-gray;
    }

    .volume-slider-group {
        position: absolute;
        bottom: 80px;
        margin-left: 7px;
        height: 140px;
        background-color: $color-dark-gray;

        &:after {
            content: '\f0d7';
            font-family: fontAwesome;
            position: absolute;
            display: block;
            font-size: 50px;
            bottom: 0;
            margin: 0;
            color: $color-dark-gray;
            margin-bottom: -23px;
        }

        .rzslider {
            margin: 0;
            padding: 0;
            width: 32px;
            height: 120px;

            .rz-bar-wrapper {
                padding: 0;
                margin: 0;
            }
            .rz-bar {
                margin: 13px 15px;
                bottom: 0;
                left: 0;
                top: 0;
                width: 2px;
                border-width: 1px;
                background-color: #727272;

                &.rz-selection {
                    background-color: $color-white;
                }
            }

            .rz-pointer {
                color: $color-white;
                background-color: $color-white;
                border-color: $color-white;
                outline: none;
                height: 16px;
                width: 16px;
                margin-bottom: -14px;
                margin-left: 18px;
                left: -10px !important;
                right: auto;
                top: auto;
                bottom: 0;
                position: absolute;
                @include border-radius(100px);
            }

            .rz-pointer:after {
                display: none;
            }
            .rz-bubble {
                display: none;
            }
        }
    }
}
