// Ribbon system component variables shared by lower-level components on the 10ft platform
$asset-item-width: 384px;
$asset-item-people-width: 160px;
$asset-image-height: 216px;
$asset-metadata-height: 105px;
$asset-border-size: 3px;

$asset-item-height: $asset-image-height + $asset-metadata-height;

$empty-ribbon-height: $asset-image-height;

// IMPORTANT: Update vs-size value in components/ribbon-system/scrolling-ribbon/ribbon.UI_TYPE.html if you change this
$asset-item-spacer: 30px;

$ribbon-left-gutter: 100px;

// My Channels tiles
$my-channels-asset-tile-width: 512px;
$my-channels-asset-tile-height: 160px;

$asset-item-width-720p: 254px;
$asset-image-height-720p: 143px;
$asset-metadata-height-720p: 70px;
$asset-item-height-720p: $asset-image-height-720p + $asset-metadata-height-720p;
$asset-metadata-font-size-720p: 17px;

// My Channels tiles
$my-channels-asset-tile-width-720p: 342px;
$my-channels-asset-tile-height-720p: 107px;
$ribbon-left-gutter-720p: 67px;

// Leave at the bottom to allow overrides to work.
@import './screen.variables';
