@import "variables.10ft";
@import "../../../styles/base";

$asset-item-max-width-720p: 534px;

$asset-image-height-720p: 140px;
$asset-metadata-height-720p: 70px;
$asset-border-size-720p: 2px;
$asset-item-height-720p: $asset-image-height-720p + $asset-metadata-height-720p;

core-my-tv {

    height: 87vh;

    core-scrolling-ribbon core-ribbon-item{
        //prevent resizing of ribbon so that margin exist between tiles of ribbon.
        flex: 0 0 auto;
    }

    core-scrolling-ribbon-my-channels core-ribbon-item-my-channels{
        //prevent resizing of ribbon so that margin exist between tiles of ribbon.
        flex: 0 0 auto;
    }

    core-ribbon-utility-tile {
        //prevent resizing of ribbon so that margin exist between tiles of ribbon.
        flex: 0 0 auto;
    }
}

core-screen {
    height: 87vh;
}

@include scale-for-hd-tv {
    core-item-thumbnail .core-item-thumbnail .image-wrapper img {
        // added width 100% to fit the image into the tiles.
        width: 100%;
    }
    core-screen-ribbon .ribbon-section {
        padding-bottom: 17px;
    }
    core-screen-ribbon-row .ribbon-title {
        padding: 10px 0 10px 67px;
    }
    core-screen-tile-standard {
        li.standard-tile {
            height: $asset-item-height-720p;
            max-width: $asset-item-max-width-720p;

            .thumbnail-wrapper {
                height: $asset-image-height-720p;
            }

            .sub-content {
                height: $asset-metadata-height-720p;
                padding: 0 10px;
                dt {
                    font-size: 17px;
                    margin: 7px 0 3px;
                }

                .icon-record-single {
                    padding-right: 3px;
                }
            }
            .meta-data, span.sub-content--details {
                font-size: 15px;
            }
            i {
                font-size: 14px;
            }
            .asset-locked-overlay .fa-lock {
                font-size: 87px;
            }
            dt {
                max-width: $asset-item-max-width-720p;
            }

            dd {
                display: inline;

                &::after {
                    content: "|";
                    color: $color-off-white;
                    padding-left: 6px;
                }

                &:last-child::after {
                    content: "";
                    padding-left: 0;
                }
            }
        }
    }
}
