@import "variables.10ft";
@import "../../../styles/base";
@import "../../../styles/base.win10.scss";

.menudrawer-container{
    position: absolute;
    @include font-size(medium);
    color: $color-off-white;
    overflow: hidden;
    top: 60px;
    left: 0;
    bottom: 0;
    z-index: 2;
    width: 60px;
    transition: width 300ms ease-in-out;

    .menudrawer-overlay{
        opacity: 0;
    }

    &.closed {
        width: 0px;
    }
    &.open {
        width: 320px;
    }

    .menudrawer {
        height: 100%;
        background-color: $color-win10-off-black;
        transition: all 300ms ease-in-out;

        .menu-item {
            width: 320px;
            height: 60px;
            line-height: 60px;
            background-color: $color-win10-off-black;
            padding-left: 15px;
            display: flex;

            .menu-icon-container {
                height: 60px;
                width: 60px;

                .icon {
                    width: 30px;
                    -webkit-filter: brightness(60%) //match $color-off-white
                }
            }
            &:active, &.selected-item {
                color: $color-white;
                background-color: $color-dark-blue;
                border-bottom: $color-light-blue 1px solid;

                .menu-icon-container {
                    .icon {
                        -webkit-filter: brightness(100%);
                    }
                }
            }
            &:hover {
                background-color: $color-gray;
                border-bottom: $color-white 1px solid;
            }
        }
    }

    &.is-collection {
        z-index: $index-side-nav-win10;
    }
}

@media screen and (orientation:landscape) {
    .modal.win10-menu-modal {
        .modal-dialog {
            width: 400px;
            max-width: 60%;
        }
    }
}
