@import "variables.10ft";
@import "../../../styles/base";
@import "../../../styles/mixins";

@include scale-for-hd-tv {
    core-activation-code {
        .activation__return-buttons {
            button.btn {
                height: 65px;
                border-radius: 3px;
            }
            button.btn.activation__return-button-arrow {
                min-width: 57px;
                width: 59px;
                margin-left: -5px;
                padding-left: 20px;
            }
            button.btn.activation__return-button-arrow > .fa-angle-right {
                line-height: 0.6;
                font-size: 65px;
            }
        }
    }
}


