@import '../../../../../styles/mixins.scss';

core-screen-tile-standard-thumbnail {
    .thumbnail {
        display: flex;
        justify-content: center;
        position: relative;
        height: 100%;

        .image-wrapper {
            height: 100%;
            width: 100%;
            display: flex;
            overflow: hidden;
            background-color: $color-gray;
            justify-content: center;
            position: relative;

            .portrait-thumbnail-cover {
                left: 0;
                width: 100%;
                height: 100%;
                position: absolute;
                background-size: cover;
                background-repeat: no-repeat;
                filter: blur(3px);
                -webkit-filter: blur(3px);
                opacity: 0.5;
            }

            img {
                width: 100%;
                height: 100%;
                position: relative;
                font-family: 'Sling-Actions';
                -webkit-font-smoothing: antialiased;
                -moz-osx-font-smoothing: grayscale;
            }

            img:before {
                /* Content for broken img - displayed only when
                    image url returns 404 */
                content: "\e91d";
                display: flex;
                align-items: center;
                justify-content: center;
                height: 100%;
                width: 100%;
                color: $color-off-white;
                background-color: $color-gray;
                border: none 0;
                outline: 0;
                font-size: 92px;
            }
        }

        .no-image-wrapper {
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
        }

        .no-image-wrapper i:before {
            font-size: 92px;
            color: $color-off-white;
            background: $color-gray;
        }

        &:hover .no-image-wrapper i:before {
            color: $color-dark-gray;
        }

        .background-thumbnail {
            background-size: cover;
            background-position: center center;
            height: 100%;
            opacity: 0.3;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
        }
    }
}