@import "variables.10ft";
@import '../../../../styles/mixins';

.standard-modal {
    width: 33vw;

    .modal-close-btn {
        display: none;
    }

    .modal-header {
        .modal-header-text {
            color: $color-yellow;
            font-size: 2.8vh;
            font-weight: 500;
            text-transform: uppercase;
            text-align: center;
        }
    }

    .modal-body {
        a {
            color: $color-white;
            outline: none;
        }
    }

    .modal-footer {
        display: flex;
        justify-content: center;
        .focused-button{
                background-color: $color-dark-blue;
                color: $color-white;
                outline: none;
                text-decoration: none;
                cursor: pointer;
        }
    }

    @include scale-for-mobile {
        width: 85%;
    }
}
