@import '../../../../styles/mixins.scss';
@import '../../../../styles/variables';

// $asset-item-spacer is imported in platform specific sheets
core-manage-continue-watching {
    @include position($position: fixed);
    background: $color-dark-gray;
    z-index: 10;

    section.manage-continue-watching-content-container {
        flex: 1;
        overflow: auto; // allow scrolling if needed
        height: 88vh; // constrain height, note more header on this screen

        .item-grid {
            display: flex;
            flex-flow: row wrap;
            justify-content: flex-start;
            align-content: space-between;
        }

    }

    .core-tool-bar {
        .core-tool-bar-button-container {
            white-space: nowrap;
        }
    }

    .offscreen-text {
        @include offscreen-accessible-element();
    }
}
