@import "variables.10ft";
@import './style';
@import '../../../styles/mixins';

core-recording-options {
    .core-modal {
        width: 506px;

        .modal-header {
            margin-bottom: 40px;

            .modal-title {
                text-align: center;
            }

            .modal-content {
                text-align: center;
            }
        }
    }
}

@include scale-for-720p {
    core-recording-options {
        .core-modal {
            width: 338px;

            .modal-header {
                margin-bottom: 27px;
            }
        }
    }
}
