@import "variables.10ft";
@import "style";

core-modal-forgot-password {
    .core-modal {
        width: 640px;

        .modal-header {
            font-weight: 500;
            padding: 0;
            margin-bottom: 30px;
            font-size: 26px;
        }

        .modal-body {
            .forgot-password-input {
                width: 540px;
                height: 58px;
                display: block;
                background-color: #323232;
                border: 6px solid #323232;
                margin-bottom: 20px;
                padding-left: 6px;

                &.core-focused-item {
                    border-color: #069;
                }
            }
        }

        .core-spinner {
            @include position();
        }
    }
}

@include scale-for-720p {
    core-modal-forgot-password {
        .core-modal {
            width: 427px;

            .modal-header {
                margin-bottom: 20px;
                font-size: 17px;
            }

            .modal-body {
                .forgot-password-input {
                    width: 360px;
                    height: 39px;
                    border-width: 4px;
                    margin-bottom: 13px;
                    padding-left: 4px;
                }
            }

            .core-spinner {
                margin-left: -33px;
            }
        }
    }
}
