@import "variables.10ft";
$grid-tile-top-bottom-margin: 20px;
$ribbon-left-gutter: 100px;
@import '../../../ribbon-system/ribbon-system.variables.10ft';
@import "./style";

// $asset-item-spacer is imported in platform specific sheets
$grid-tile-left-right-margin: $asset-item-spacer / 2;
core-manage-continue-watching {

    .core-tool-bar {
        display: flex;
        position: relative;
        background-color: $color-medium-cool-gray;
        padding: 15px $ribbon-left-gutter;
        min-width: 300px;
        border-bottom: none;

        &::after {
            $height: 20px;
            @include vertical-gradient(rgba(0, 0, 0, .25), transparent);
            @include position($top: auto, $bottom: -$height);
            content: ' ';
            display: block;
            width: 100%;
            height: $height;
            z-index: 1;
        }

        button {
            flex-grow: 1;
            flex-shrink: 0;
            white-space: nowrap;
        }
    }

    .manage-continue-watching-content-container {

        .item-grid {
            // $grid-tile-top-bottom-margin $ribbon-left-gutter are defined or imported in platforms specific sheets
            margin: -$grid-tile-top-bottom-margin #{$ribbon-left-gutter - $grid-tile-left-right-margin};
        }

        // Individual asset item in the Manage Recordings screen
        core-ribbon-item {
            .ribbon-item {
                // $grid-tile-top-bottom-margin and $asset-item-width are defined or imported in platforms specific sheets
                margin: $grid-tile-top-bottom-margin $grid-tile-left-right-margin;
                width: $asset-item-width;
            }
        }

    }
}

@include scale-for-720p {
    $grid-tile-left-right-margin-720p: $asset-item-spacer-720p / 2;
    $grid-tile-top-bottom-margin-720p: 13px;

    core-manage-continue-watching {

        .core-tool-bar {
            padding: 10px $ribbon-left-gutter-720p;
            min-width: 200px;

            &::after {
                $height: 13px;
                height: $height;
                bottom: -$height;
            }
        }

        .manage-continue-watching-content-container {

            .item-grid {
                // $grid-tile-top-bottom-margin $ribbon-left-gutter are defined or imported in platforms specific sheets
                margin: -$grid-tile-top-bottom-margin-720p #{$ribbon-left-gutter-720p - $grid-tile-left-right-margin-720p};
            }

            // Individual asset item in the Manage Recordings screen
            core-ribbon-item {
                .ribbon-item {
                    // $grid-tile-top-bottom-margin and $asset-item-width are defined or imported in platforms specific sheets
                    margin: $grid-tile-top-bottom-margin-720p $grid-tile-left-right-margin-720p;
                    width: $asset-item-width-720p;
                }
            }
        }
    }
}
