@import "variables.10ft";
@import '../../../styles/mixins.scss';

.tabs-container {
    .tabs-row {
        padding: .5vh 0.37vw;
        margin-top: -0.2vh;
        margin-left: -.3vw;
        display: flex;
        position: absolute;
        z-index: $index-front;
        background-color: $color-dark-gray;
        width: 100%;

        .tabs {
            height: 4.6vh;
            padding: 1vh 1vw;
            color: $color-warm-gray;
            outline: none;
            cursor: pointer;
            border-radius: 0vh;
            font-size: 2.2vh;
            background-color: inherit;
            border-bottom: 3px solid transparent;
            transition: color 200ms, background-color 200ms;
            &:hover{
                color: $color-white;
            }
        }
        .active-tab {
            color: $color-white;
            border-bottom: 3px solid $color-blue;
        }
        .core-focused-item {
            @include focused-box();
            border-radius: 0.2vh;
            border-bottom: 3px solid transparent;
        }
    }
}
