@import '../../../styles/mixins.scss';

core-details-button-row {
    display: block;
    // Prevent additional sizing from the layout.scss overrides
    font-size: 0;
    .offscreen-text {
        @include offscreen-accessible-element();
    }
    .purchase {
        .icon {
            font-weight: bold;
        }
    }

    core-spinner {
        @include position(absolute);
    }
}
