// On Now, Sports, Shows etc screens
core-dynamic-ribbon-page {
    flex: 1;
    // Enable scrolling within a restricted viewport height
    .vertical-scroll-container {
        padding-bottom: 5vh; // add room at the bottom
        // see platform stylesheets for bottom margin on last ribbon

        @media screen and (max-height: 700px) {
            padding-bottom: 8vh; // more room on shorter screens
        }
    }
}
