@import "variables.10ft";
@import "style";

core-modal-sign-in {
    .core-modal {
        .modal-header {
            padding: 0;

            .sign-in-title {
                font-weight: 500;
                margin-bottom: 30px;
                font-size: 26px;
            }
        }

        .modal-body {
            .sign-in-input {
                width: 540px;
                height: 58px;
                display: block;
                border: 6px solid #323232;
                margin-bottom: 20px;
                padding-left: 6px;

                &.core-focused-item {
                    border-color: #069;
                }
            }
        }

        .modal-footer {
            .footer-buttons {
                display: block;
            }

            .forgot-password-text {
                color: #ffab15;
                font-weight: 500;
                display: inline;
                font-size: 26px;
                border-bottom: 3px solid transparent;
                position: relative;
                top: 26px;

                &.core-focused-item {
                    color: #069;
                    outline: transparent;
                    text-decoration: none;
                    border-bottom: 3px solid #069;
                }
            }
        }

        .core-spinner {
            @include position();
        }
    }
}

@include scale-for-720p {
    core-modal-sign-in {
        .core-modal {
            .modal-header {
                .sign-in-title {
                    margin-bottom: 20px;
                    font-size: 17px;
                }
            }

            .modal-body {
                .sign-in-input {
                    width: 360px;
                    height: 39px;
                    border-width: 4px;
                    margin-bottom: 13px;
                    padding-left: 4px;
                }
            }

            .modal-footer {
                .forgot-password-text {
                    font-size: 17px;
                    border-bottom-width: 2px;
                    top: 17px;

                    &.core-focused-item {
                        border-bottom-width: 2px;
                    }
                }
            }

            .core-spinner {
                margin-left: -33px;
            }
        }
    }
}
