@import "variables.10ft";
@import './style';
@import '../../../styles/mixins.scss';
@import '../../../styles/variables.10ft';

core-side-menu {
    .settings-side-menu {
        border-right: 2px solid $color-warm-gray;
        padding: 30px 0;
        width: $settings-left-menu-width;
        background-color: $color-cool-gray;

        .menu-item {
            padding: 20px 30px;
            font-size: $font-size-bigger2;

            &.active {
                outline: 0;
                background-color: $color-shadow-gray;
                color: $color-white;
                text-decoration: none;

                .menu-subtitle {
                    color: $color-white;
                }
            }

            &:focus,
            &.in-focus,
            &:hover {
                background-color: $color-shadow-gray;
                color: $color-white;

                .menu-subtitle {
                    color: $color-white;
                }
            }

            &.core-focused-item {
                @include focused-box();
            }

            .menu-subtitle {
                font-size: $font-size-big;
            }
        }
    }
}

@include scale-for-720p {
    core-side-menu {
        .settings-side-menu {
            padding: 18px 0;
            width: $settings-left-menu-width-720p;

            .menu-item {
                padding: 13px 20px;
                font-size: $font-size-medium;

                .menu-subtitle {
                    font-size: $font-size-smaller;
                }
            }
        }
    }
}
