@import "variables.10ft";
@import './style';

core-modal-change-device-name {
    .core-modal {
        .modal-header {
            h4.device-name-change-title {
                font-size: $font-size-biggest2;
            }
        }
        .modal-body {
            text-align: left;
            .device-name-input {
                height: 58px;
                display: block;
                border: 2px solid $color-medium-gray;
                margin-bottom: 20px;
                padding-left: 6px;
                background-color: $color-black;
                &.core-focused-item {
                    border-color: $color-dark-blue;
                }
            }
        }
    }
}
@include scale-for-720p {
    .modal {
        core-modal-change-device-name {
            .core-modal {
                .modal-header {
                    h4.device-name-change-title {
                        font-size: $font-size-big;
                    }
                }
                .modal-body {
                    .device-name-input {
                        height: 39px;
                        margin-bottom: 13px;
                        padding-left: 4px;
                    }
                }
            }
        }
    }
}

