@import '../../../styles/mixins.scss';

core-side-menu {
    background-color: $color-cooler-gray;
    height: 100%;

    & > section {
        height: 100%;
    }

    .settings-side-menu {
        overflow: auto;
        height: 100%;

        .menu-item {
            display: block;
            color: $color-off-white;
            text-decoration: none;
            font-weight: normal;
            line-height: normal;

            .menu-subtitle {
                display: block;
                color: $color-light-gray;
                font-weight: normal;
                @include ellipsis-able();
            }
        }
    }
}
