@import "variables.10ft";
@import './Tile';
@import '../../../styles/mixins.scss';

.tile-container {
    .standard-tile {
        &.core-focused-item {
            .metadata {
                @include focused-box();

                .attributes {
                    color: $color-white;
                }
            }
        }

        .icon-locked {
            font-size: 9.3vh;
            color: $color-light-gray;
            background-color: $color-overlay-dark-gray;
        }

        .icon-quick-play {
            right: 1.39vw;
            box-shadow: 0 0 6px 2px rgba(0, 0, 0, 0.6);
            font-size: $play-icon-size;
        }

        .metadata {
            .icon {
                margin-right: 0.3vw;
                font-size: 2vh;
            }
        }

        .progress-bar {
            bottom: 9vh;
            height: .56vh;
        }

        .unentitled-icon-wrapper {
            padding: 0.37vh 1.15vw 0.37vh 0.52vw;

            .icon-add {
                padding-right: 0.78vw;
            }
        }
    }

    .metadata {
        padding: 0 .78vw 0;

        .icon {
            margin-right: .26vw;
            font-size: 1.3vh;
            top: .09vh;
        }

        .attributes {
            span {
                &::before {
                    margin: 0 .55vw;
                }

                &:first-child {
                    &::before {
                        margin: 0;
                    }
                }
            }
        }

        &.MICRO_GUIDE {
            .upcoming-content {
                font-size: 2.04vh;
                width: 16vw;
            }
        }
    }

    .utility-tile {
        height: 27.41vh;
        width: 7.81vw;

        &.core-focused-item {
            @include focused-box();
        }

        &.channel-row {
            height: 14.81vh;
        }

        .icon {
            font-size: 7.4vh;
            margin-bottom: 0.93vh;
        }

        .utility-tile-text {
            font-size: 2.32vh;
            line-height: normal;
        }
    }

    .channel-tile {
        width: 26.67vw;
        height: 14.81vh;
        padding: 1.76vh 1vw;

        &.core-focused-item {
            @include focused-box();

            .metadata {
                &.MICRO_GUIDE {
                    .attributes, .upcoming-content {
                        color: $color-white;
                    }
                }
            }
        }

        .icon-locked {
            position: absolute;
            top: 2.59vh;
            margin-left: 0.3vw;
            font-size: 3.2vh;
        }

        .metadata {
            height: 10vh !important;
            width: 18vw;
            padding: .5vh;
        }


        .channel-logo {
            position: absolute;
            width: 8vw;
            right: 0;
            bottom: 1.1vw;

            img {
                max-height: 5.56vh;
                max-width: 8vw;
                right: 1.5vw;
                bottom: 0;
            }

            .thumbnail-error {
                display: none;

                & ~ .alt-text {
                    display: inline-block;
                    margin-right: 0.4vw;
                }
            }

            .alt-text {
                @include ellipsis-able();
                display: none;
                float: right;
                max-width: 100%;
            }
        }
    }
}


// UI4.0
.metadata-ui4 {
    padding-top: .37vh;

    &.STANDARD, &.STANDARD_V4 {

        .meta-title-badge-container {

            &.live-badge::before {
                content: "";
                position:absolute;
                left: 0;
                height: 1.48vh;
                width: 1.97vw;
                background-size: contain;
                background-repeat: no-repeat;
                background-image: url("../../../../assets/images/live-badge.svg");
            }

            .meta-title {
                font-size: $font-size-big;
                margin-left: 3.612vw;
                line-height: normal;
                margin-top: -0.8vh;
                margin-bottom: -0.5vh;
            }

            @include scale-for-720p {
                .meta-title {
                    font-size: $font-size-smallest2;
                }
            }

            .badges {

                .recording-badge {
                    height: 2.592vh;
                    width: 1.458vw;
                    background-size: contain;
                    background-repeat: no-repeat;

                    &.DVR_GRAY_DOT {
                        background-image: url("../../../../assets/images/gray-record-badge.svg");
                    }

                    &.DVR_RED_DOT {
                        background-image: url("../../../../assets/images/red-record-badge.svg");
                    }
                }

                .favorite-badge {
                    height: 3.1vh;
                    width: 1.45vw;
                    margin-left: 0.5vw;
                    background-size: contain;
                    background-repeat: no-repeat;

                    &.FAVORITE_HEART_FILLED {
                        background-image: url("../../../../assets/images/favorite-badge.svg");
                    }
                }
            }
        }
    }
}

.tile-ui4 {
    height: 21.667vh;
    width: 21.67vw;
    margin-right: 1vw;

    // WHY ?
    // Showing/Hiding chevron logic is implemented inside UtilityTile.jsx
    // All tiles are memoized by react and renders under certain conditions only.
    // If there are "6" tiles in ribbon, including utility tile, then if utility tile exits from
    // the left edge of the carousel and enters into the right edge of the carousel, we see utility
    // tile left chevron instead of tile text and icon.
    // If there are "> 6" tiles, we do not see this issue.
    // This happens because Utility Tile is not rerendered as it moves from left edge to right edge.
    // Hence this CSS

    // If its NOT the first "tile-ui4", and happens to have ".partial-visible" class on it,
    // then hide left chevron and show utility tile text along with icon
    &:not(:first-of-type) {
        &.partial-visible {
            .icon, .fa {
                display: inline-block;
            }
            .utility-tile-text {
                display: block;
            }
            .fa-chevron-left {
                display: none;
            }
        }
    }
}

.standard-tile-ui4 {
    overflow: visible;

    .thumbnail {
        height: 21.667vh;

        &.icon-locked::before {
            font-size: 3.6vh;
        }
    }

    &.new-badge::after {
        content: "";
        position: absolute;
        height: 2.129vh;
        width: 3vw;
        top: 0;
        right: 0;
        background-size: cover;
        background-repeat: no-repeat;
        background-image: url("../../../../assets/images/new-badge.svg");
    }

    .progress-bar {
        bottom: 0vh;
        height: 0.42vh;
        z-index: $index-front;
    }

    .unentitled-icon-wrapper-v4 {
        @include top-left();
        width: 8vw;
        height: 4vh;
        color: $color-black;
        background-color: $color-yellow;
        font-weight: bold;
        font-size: 2.5vh;
        background: linear-gradient(315deg, transparent 10px, $color-yellow 10px);
        padding: 0.37vh 1.15vw 0.37vh 0.52vw;

        .icon-add {
            padding-right: 0.78vw;
        }
    }

    .channel-logo-placeholder {
        position: absolute;
        height: 7.77vh;
        width: 100%;
        bottom: 0;
        background: linear-gradient(180deg, $color-gradient-black-trasparent 0%, $color-gradient-black10 15.16%, #000000 100%);

        .channel-logo {
            position: absolute;
            height: 5.555vh;
            bottom: 1.11vh;
            right: 0;
        }

        .channel-logo-error {
            display: none;

            & ~ .alt-text {
                display: inline-block;
                position: absolute;
                bottom: 1.11vh;
                right: 0.4166vw;
            }
        }

        .alt-text {
            @include ellipsis-able();
            display: none;
            float: right;
            max-width: 100%;
        }

    }

    &.core-focused-item {

        .thumbnail, .icon-locked::before {
            @include focused-tile-border();
            box-shadow: 4px 4px 20px 0 $color-gradient-black50;
        }

        .progress-bar {
            width: calc(100% - #{$focused-tile-border-width-in-px * 2}); // can't use vh here thanks to Samsung 2016 TV limitation
            bottom: 0.7407vh;
            left: $focused-tile-border-width-in-px;
        }

        .channel-logo-placeholder {
            width: calc(100% - #{$focused-tile-border-width-in-px * 2}); // can't use vh here thanks to Samsung 2016 TV limitation
            bottom: 0.7407vh;
            left: $focused-tile-border-width-in-px;
        }

        .metadata-ui4 {
            .meta-title-badge-container {
                color: $color-white;

                &.live-badge::before {
                    left: $focused-tile-border-width;
                }
            }
        }

        &:not(.grid-tile) {
            @include upscaled-tile();
            .metadata-ui4 {
                .meta-title-badge-container {
                    &.live-badge::before, .meta-title, .badges {
                        @include tile-scale-down(); // scale down to preserve original metadata content dimensions
                    }
                }

                .meta-attributes {
                    @include tile-scale-down();
                }
            }
        }

        &.grid-tile {
            @include upscaled-grid-tile();
            .metadata-ui4 {
                .meta-title-badge-container {

                    &.live-badge::before, .meta-title, .badges {
                        @include grid-tile-scale-down(); // scale down to preserve original metadata content dimensions
                    }
                }

                .meta-attributes {
                    @include grid-tile-scale-down();
                }
            }
        }

        @include scale-for-720p {
            .thumbnail {
                box-shadow: 2.67px 2.67px 20px 0 $color-gradient-black50;
            }

            .progress-bar {
                width: calc(100% - #{$focused-tile-border-720p-width-in-px * 2});
                left: $focused-tile-border-720p-width-in-px;
            }
        }
    }
}

.utility-tile-ui4 {

    &.core-focused-item {
        @include upscaled-tile();
        @include focused-tile-border();
        margin-top: 0vh;
        box-shadow: 4px 4px 20px 0 $color-gradient-black50;
        @include scale-for-720p {
            box-shadow: 2.67px 2.67px 20px 0 rgba(0, 0, 0, 0.5);
        }
    }

    &.partial-visible{
        .icon, .fa {
            display: none;
        }
        .utility-tile-text {
            display: none;
        }
        .fa-chevron-left {
            display: block;
        }
    }

    .icon, .fa {
        font-size: 7.4vh;
        margin-bottom: 0.93vh;
        color: $color-light-white;
    }

    .utility-tile-text {
        font-size: 2.32vh;
        line-height: normal;
    }

    .fa-chevron-left {
        display: none;
        position: absolute;
        right: 1.8vw;
        color: $color-light-white;
        transform: scale(1, 1.5);
        top: 9.352vh;
        font-size: 3vh;
    }
}
