@import '../../../../../styles/mixins.scss';

core-continue-watching-filter {

    .continue-watching-filter-container {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 20px $ribbon-left-gutter 30px $ribbon-left-gutter;
    }

    .offscreen-text {
        border: 0;
        clip: rect(0, 0, 0, 0);
        height: 1px;
        margin: -1px;
        overflow: hidden;
        padding: 0;
        width: 1px;
        position: absolute;
    }
}
