@import "variables.10ft";
// Import local variables shared by guide/grid-guide components
@import '../grid-guide.variables.10ft';
@import './style';
@import '../../../../styles/mixins.scss';

core-grid-channel {
    i:before {
        font-size: 18px;
    }

    .channel-container {
        .image-container {

            &:hover {
                img {
                    opacity: 0.6;
                    max-height: 100%;
                    max-width: 100%;
                    @include alt-text();
                }
                background-color: inherit;
            }

            &.has-hover {
                background-color: $color-light-medium-gray;

                img {
                    flex: 0 0 auto;
                    opacity: 1;
                }

                &.icon-mytv_off:before {
                    color: $color-white;
                }
            }

            &.core-focused-item {
                @include focused-box();

                img {
                    opacity: 1;
                }
            }
        }
    }
}

@include scale-for-720p {
    core-grid-channel {
        width: $grid-guide-channel-width-720p;
        padding-right: 2px;

        .channel-container {
            height: $grid-guide-row-height-720p;

            i,
            .image-container {
                padding: $grid-guide-item-padding-720p;
            }
        }

        i:before {
            font-size: 12px;
        }
    }
}
