// Ribbon system component variables shared by lower-level components on the win10 platform
$asset-item-width: 272px !default;
$asset-item-people-width: 130px !default;

$asset-image-height: 153px !default;
$asset-metadata-height: 56px !default;
$asset-border-size: 2px !default;

$asset-item-height: $asset-image-height + $asset-metadata-height !default;

// Used to restore line height in ribbon items including utility tiles etc.
$asset-item-line-height: 1.4 !default; // units intentionally omitted

// IMPORTANT: Update vs-size value in components/ribbon-system/scrolling-ribbon/ribbon.UI_TYPE.html if you change this
$asset-item-spacer: 15px !default;

$ribbon-left-gutter: 40px !default;
$ribbon-left-gutter-tablet: 30px !default;
$empty-ribbon-height: $asset-image-height !default;

// My TV Channels tiles are styled based on 3ft sketches
$my-channels-max-screen-width-mobile: 768px !default; // breakpoint for 3ft and win10
$my-channels-asset-tile-width-mobile: 260px !default; // < 767px
$my-channels-asset-tile-width: 315px !default;
$my-channels-asset-tile-height: 94px !default;